button,
input[type='submit'],
input[type='reset'],
input[type='button'] {
  cursor: pointer;
  backface-visibility: hidden;
  background: none;
  color: inherit;
  font: inherit;
  border: 0;
  line-height: 1;
  padding: 0;
  outline: 0;
  transition: $ts $te;

  &:disabled,
  &[disabled] {
    opacity: .5;
    pointer-events: none;
  }
}

// Buttons
//
// Weight: -98
//
// Styleguide Components.Buttons

// Default Button
//
// :hover    - Hover state
// :focus    - Focus state
// :active   - Active state
// :disabled - Disabled state
//
// Markup:
// <a class="btn {{modifier_class}}" href="#">Link Button</a>
// <button class="btn {{modifier_class}}">Standard Button</button>
// <input type="submit" class="btn {{modifier_class}}" value="Submit Button" />
// <input type="reset" class="btn {{modifier_class}}" value="Reset Button" />
//
// Weight: -99
//
// Styleguide Components.Buttons.Default Button
.btn {
  position: relative;
  z-index: 1;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  padding: 14px 20px;
  cursor: pointer;
  background-color: primary-color(yellow);
  color: primary-color(black);
  font-family: $inter-b;
  @include rem(font-size, 14px);
  @include line-height(17, 14);
  @include rem(letter-spacing, 1px);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  // white-space: nowrap;
  border: 2px solid primary-color(yellow);
  border-radius: 100px;
  transition: background-color .2s $te 0s, color .2s $te, border-color .2s $te;

  @include breakpoint(550px) {
    white-space: nowrap;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    display: block;
    height: 100%;
    width: 0;
    background: primary-color(white);
    border-radius: 50px;
    transition: .2s $te;
  }

  @include hover {
    background-color: transparent;
    color: primary-color(black);

    &::before {
      left: 0%;
      right: auto;
      width: 100%;
    }
  }

  &.-purple {
    background-color: primary-color(purple);
    border-color: primary-color(purple);
    color: primary-color(white);

    @include hover {
      background-color: primary-color(white);
      color: primary-color(black);
    }
  }

  &.-white {
    background-color: primary-color(white);
    border-color: primary-color(white);
    color: primary-color(black);

    @include hover {
      background-color: primary-color(white);
      color: primary-color(black);
    }
  }

  &.-gray-outline {
    background-color: primary-color(white);
    border-color: primary-color(gray);
    color: primary-color(black);

    @include hover {
      border-color: primary-color(yellow);
    }
  }

  &.-yellow-outline {
    background-color: primary-color(white);
    border-color: primary-color(yellow);
    color: primary-color(black);

    &::before {
      background: primary-color(yellow);
    }

    // @include hover {
    //   border-color: primary-color(yellow);
    // }
  }

  &.-small {
    @include rem(font-size, 12px);
  }

  /////////////////////////////////////////////////////////////
  // NOTE: Adding .wysiwyg-content below is to handle
  //       buttons in a WYSIWYG.
  //
  //       For Drupal projects, also add .mce-content-body to
  //       add styles in the backend WYSIWYG.
  /////////////////////////////////////////////////////////////
  .wysiwyg-content & {
    margin-top: 5px;

    & + .btn {
      margin-left: 5px;
    }
  }
}

.btn-text {
  position: relative;
  display: inline-block;
  padding-left: 32px;
  font-family: $inter-b;
  @include rem(font-size, 16px);
  @include line-height(19, 16);
  text-transform: uppercase;
  @include rem(letter-spacing, 1px);
  border: 0;

  @include icon(arrow) {
    position: absolute;
    top: 2px;
    left: 0;
    margin-right: 15px;
    @include rem(font-size, 17px);
    transition: .2s $te;
  }

  span {
    display: inline;
    border-bottom: 1px solid transparent;
    @include line-height(24, 17);
    transition: $ts $te;
  }

  @include hover {

    span {
      border-bottom: 1px solid primary-color(purple);
    }

    &:not(.-external):not([target='_blank']) {

      &::before {
        left: 3px;
      }
    }
  }

  &.-external,
  &[target='_blank'] {
    @include icon(external-link);
  }
}

