//sass-lint:disable force-element-nesting
//sass-lint:disable single-line-per-selector
//sass-lint:disable no-important
//sass-lint:disable quotes
//sass-lint:disable force-pseudo-nesting
//sass-lint:disable nesting-depth

.CoveoSearchInterface {

  .coveo-pager-list-item.coveo-active,
  .coveo-pager-list-item:hover {
    background-color: primary-color(purple);
    color: white;
    text-decoration: underline;
  }

  .coveo-pager-list-item {
    border: 0;
    border-radius: 2px;
    margin: 5px 6px;
    padding: 4px 8px;
    list-style: none;
    display: inline-block;
    vertical-align: baseline;
    text-decoration: none;
    cursor: pointer;
    color: primary-color(purple);
    @include rem(font-size, 18px);
  }

  .CoveoLogo {
    display: none;
  }

  .CoveoPager {
    display: block;
    text-align: center;
  }

  .coveo-pager-list {
    margin: 50px 0;

    a {
      color: primary-color(purple);
    }

    li {
      position: relative;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin: 0 3px;
      width: 38px;
      height: 38px;
      border-radius: 50%;
      color: primary-color(purple);
      font-weight: normal;
      $border: 2px;
      border: $border solid transparent;
      text-align: center;
      z-index: 1;

      &.sr-text {
        position: absolute;
      }

      &::before,
      &::after {
        content: '';
        @include absolute(0, 0, 0, 0);
        border-radius: inherit;
        z-index: -1;
        opacity: 0;
        transition: $te $ts;
      }

      &::before {
        background: primary-color(purple);
        margin: -$border;
      }

      &::after {
        background-color: primary-color(purple);
      }

      &.coveo-active {
        pointer-events: none;

        a {
          color: primary-color(white);
          font-weight: normal;
        }

        &::before {
          background: primary-color(purple);
          margin: -$border;
          opacity: 1;
        }

        &::after {
          opacity: 1;
        }
      }

      @include hover {

        a {
          color: primary-color(white);
          text-decoration: none;
        }

        &::before {
          background: primary-color(purple);
          margin: -$border;
          opacity: 1;
        }

        &::after {
          opacity: 1;
        }

        &.pagination-pages-dots {
          &::before,
          &::after {
            opacity: 0;
          }
        }
      }
    }
  }

  .coveo-pager-next-icon-svg, .coveo-pager-previous-icon-svg {
    position: relative;
    top: 0;
    width: 9px;
    height: 15px;
    color: primary-color(purple);
    vertical-align: middle;
    transition: $te $ts;
    @include font-weight(bold);
  }

  .coveo-pager-list-item.coveo-pager-next:hover,
  .coveo-pager-list-item.coveo-pager-previous:hover {

    svg {
      color: primary-color(white);
    }
  }
}
