.promo-728 {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  padding: 25px;
  background-image: linear-gradient(0deg, rgba(221, 221, 221, 1) 0%, rgba(237, 237, 237, 1) 8%, rgba(237, 237, 237, 1) 92%, rgba(221, 221, 221, 1) 100%);
  background-color: primary-color(lightgray);

  .inner {
    @include grid-placement;
    display: flex;
    justify-content: center;
  }
}
