.menu-dropdown-item {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  background-color: primary-color(white);
  display: none;
  position: absolute;
  z-index: 100;
  left: 0;
  right: 0;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  padding-top: 30px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .07);
  transition: $ts $te;

  &-details {
    display: grid;
    max-width: $inner-max-width;
    width: 100%;
    justify-self: center;
  }

  .nav-sections {
    display: flex;
    flex-wrap: wrap;

    > li {
      flex: none;
      width: calc(25% - 50px);
      margin: 0 25px 35px;

      // @include from(14) {
      //   width: calc(33.33% - 50px);
      // }
    }
  }

  .nav-section {

    &-first {
      padding-left: 40px;
    }

    li {
      @include rem(font-size, 14px);

      & + li {
        margin-top: 13px;
      }

      a {

        &:not(.-caret) {
          display: inline;
          color: primary-color(black);
          @include line-height(20, 14);
          border-bottom: 1px solid transparent;
          transition: $ts $te;

          @include hover {
            border-bottom: 1px solid primary-color(black);
          }

        }
      }
    }

    img {
      margin-bottom: 15px;
    }
  }

  .nav-section-title {
    display: flex;
    margin-bottom: 20px;
    padding: 12px 0;
    @include rem(font-size, 14px);
    @include font-weight(bold);
    text-transform: uppercase;
    @include rem(letter-spacing, 2px);
    color: primary-color(black);
    border-bottom: 1px solid primary-color(gray);

    &::before {
      content: '';
      flex: none;
      display: block;
      width: 25px;
      height: 3px;
      margin: 6px 15px 0 0;
      background-color: primary-color(red);
    }
  }
}
