// Article List
//
// Markup: article-list.twig
//
// Styleguide Components.Article List
.article-list {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  @include component-padding;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    @include grid-placement;
    overflow: hidden;
    max-width: 700px;
    margin: 0 auto;
  }

  header {
    max-width: 700px;
    margin: 0 0 is-clamp(40px, 8vw, 70px);

    @include from(8) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }

    p {
      max-width: 500px;

      @include from(8) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .button-row {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-top: 30px;

      @include from(8) {
        flex-direction: row;
        justify-content: center;
        align-items: center;
      }

      a {

        @include from(8) {
          margin: 0 20px;
        }
      }
    }
  }

  .items {
    max-width: 650px;
    margin: 0 auto;
  }

  .item {
    display: flex;
    margin-bottom: 40px;

    &:not(:last-child) {
      padding-bottom: 40px;
      border-bottom: 1px solid rgba($hr-color, .4);
    }

    .item-image {
      overflow: hidden;
      width: 112px;
      flex: none;

      img {
        transition: .3s $te;
      }
    }

    .item-text {
      flex: 1 1 auto;
      padding-right: 30px;
    }

    .eyebrow {
      margin-bottom: 5px;
    }

    .h5 {
      margin-bottom: 10px;

      a {
        display: inline;
        border-bottom: 1px solid transparent;
        transition: $ts $te;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    .meta {
      margin-bottom: 20px;
      @include rem(font-size, 14px);
    }

    .meta-author {
      margin-right: 30px;
    }

    p {
      @include rem(font-size, 16px);
      @include line-height(24, 16);
    }

    &.js-link-event {

      @include hover {

        .item-image {

          img {
            transform: scale(1.05);
          }
        }

        .h5 {

          a {
            border-bottom: 1px solid primary-color(purple);
          }
        }
      }
    }
  }
}
