// Four Up
//
// .-modifier - Modifier description
//
// Markup: four-up.twig
//
// Styleguide Components.Four Up
.four-up {
  @include grid-placement;
  @include component-margin;
  padding: 50px 0 0;

  .inner {
    max-width: 1280px;
    margin: 0 auto;
  }

  .h2 {
    margin: 0 0 30px;

    @include from (10) {
      position: sticky;
      top: 50px;
      margin: 0 40px 0 0;
    }
  }

  &-grid {

    @include from (10) {
      display: grid;
      grid-template-columns: minmax(370px, 1fr) 3fr;
    }

  }

  &-card {

    @include from (5) {
      margin: 0 0 30px;
      padding: 0 0 30px;
      display: flex;
      flex-direction: row-reverse;
      justify-content: flex-end;
      border-bottom: 1px solid primary-color(gray);

      &:last-of-type {
        border-bottom: 0;
        margin: 0;
        padding: 0;
      }
    }

    &-image {

      @include from (5) {
        width: 35%;
      }

      picture {

        img {
          width: 100%;
          height: 100%;
        }
      }
    }

    &-container {
      margin: 20px 0;

      @include from (5) {
        margin: 0 30px 0 0;
      }

      &[data-image="true"] {
        @include from (5) {
          width: 65%;
        }
      }

      .wysiwyg-content {

        ul {
          list-style: disc;
          padding: 0 0 0 30px;
        }

        li {
          margin: 0 0 25px;
          padding: 0;
          @include line-height(24, 16);

          &:last-child {
            margin: 0;
          }

          &::before {
            content: none;
          }

          @include from (5) {
            margin: 0 0 10px;

            &:last-child {
              margin: 0;
            }
          }
        }
      }
    }
  }
}
