.hero-circle {
  @extend %grid-columns;
  position: relative;
  z-index: 1;
  @include grid-placement(full-bleed);
  overflow: hidden;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .08);

  &[data-margin='true'] {
    @include component-margin;
  }

  .inner {
    position: relative;
    @include grid-placement(full-bleed);
    display: flex;
    flex-direction: column;
    background-image: url('../images/hero/bg-hero-circle.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    &::before,
    &::after {
      content: '';
      position: absolute;
      z-index: 0;
      top: 0;
      display: block;
      width: 50%;
      height: 100%;
      mix-blend-mode: multiply;
      pointer-events: none;
    }

    &::before {
      display: none;

      @include from(10) {
        display: block;
        left: 0;
        background: primary-color(green);
      }
    }

    &::after {
      right: 0;
      width: 100%;
      background: primary-color(yellow);

      @include from(10) {
        width: 50%;
      }
    }
  }

  .text {
    position: relative;
    z-index: 1;
    max-width: 640px;
    margin: 0 auto;
    text-align: center;
    transform-style: preserve-3d;

    @include from(6) {
      max-width: 720px;
    }

    // the big white circle
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      top: 40px;
      left: 50%;
      transform: translateX(-50%);
      width: 200%;
      padding-bottom: 200%;
      background-color: primary-color(white);
      border-radius: 100%;
      transition: $ts $te;

      @include from(10) {
        top: -40px;
        width: 185%;
        padding-bottom: 185%;
      }

      @include from(12) {
        top: -70px;
      }

      @include from(14) {
        top: -140px;
      }
    }

    .text-inner {
      padding: 100px 30px 70px;

      @include from(6) {
        padding: 100px 70px 70px;
      }

      @include from(10) {
        padding: 75px 70px 90px;
      }
    }
  }

  .eyebrow {
    margin-bottom: 14px;
    @include font-clamp(rem(12px), 2.4vw, rem(20px));
  }

  // event hero only subtitle:
  .subtitle {
    margin: -10px 0 25px;
    font-family: $inter-b;
    @include font-clamp(rem(16px), 3.2vw, rem(22px));
    @include line-height(30, 22);
    color: primary-color(black);
  }

  p {
    color: #4d4d4f;
  }

  .button-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 35px;

    @include from(10) {
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }

    .btn {

      @include from(10) {
        flex: none;
      }

      + .button-row-text-link {
        margin: 35px auto 0;

        @include from(10) {
          display: flex;
          justify-content: flex-start;
          margin: 0 0 0 35px;
          text-align: left;
        }
      }
    }

    .button-row-text-link {

      a {
        display: inline;
      }
    }
  }
}
