.page-container {
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: 'header' 'main' 'footer';
  grid-template-columns: minmax(0, 1fr);
  position: relative;
  min-height: 100%;
  background: primary-color(white);

  @include breakpoint(1921px) {
    @include content-constraint(1920px);
    box-shadow: 0 0 100px rgba(0, 0, 0, .1);
  }

  & > .site-header {
    grid-area: header;
  }

  & > main {
    grid-area: main;

    // NOTE: ALL direct descendants of this container MUST have a grid-column declared
    .grid-container {
      @extend %grid-columns;
      opacity: 1;
      transition: opacity .05s $te;
    }
  }

  & > .site-footer {
    grid-area: footer;
  }
}
