.mobile-menu {
  position: fixed;
  top: 0;
  right: -340px;
  width: 320px;
  height: 100vh;
  padding-bottom: 200px;
  background-color: primary-color(lightgray);
  box-shadow: 0 13px 24px 0 rgba(30, 30, 30, .5);
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  opacity: 0;
  visibility: hidden;
  transition: right .3s $te, opacity .3s $te, visibility .3s $te, background-color .1s $te;
  z-index: 100;

  @include breakpoint(360px) {
    right: -380px;
    width: 360px;
  }

  @include from(5) {
    right: -470px;
    width: 450px;
  }

  html[data-mobile-menu='open'] & {
    right: 0;
    opacity: 1;
    visibility: visible;
  }

  &:not([data-level='1']) {
    background: primary-color(white);

    .nav-utility {
      background: primary-color(white);
    }
  }

  // top section
  &-header {
    height: 130px;
    padding-bottom: 7px;
    background-color: white;

    .mobile-menu-top {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 23px 28px 8px;
    }

    .mobile-menu-search-container {
      position: relative;
      flex: 1 1 auto;
    }

    .mobile-menu-search-input {
      height: 41px;
      padding: 0 50px 0 14px;
      border-radius: 100px;
      @include rem(font-size, 12px);
    }

    .mobile-menu-search-submit {
      position: absolute;
      top: 1px;
      right: 1px;
      width: 45px;
      height: calc(100% - 2px);
      font-size: 0;
      background-color: transparent;
      border: 1px solid transparent;
      border-radius: 0 100px 100px 0;
      background-image: url('../images/search.svg');
      background-size: 21px 22px;
      background-repeat: no-repeat;
      background-position: center;

      &:focus {
        background-color: primary-color(lightgray);
      }

      &:hover {
        background-color: primary-color(purple);
        background-image: url('../images/search-white.svg');
      }
    }

    .mobile-menu-top-nav {
      margin: 0 28px;
      padding-bottom: 5px;
      border-bottom: 2px solid #979797;

      ul {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      li {
        font-family: $inter-b;
        @include rem(font-size, 11px);
        @include line-height(13, 11);
        text-transform: uppercase;

        a {
          height: 35px;
          display: flex;
          align-items: center;
          color: primary-color(black);

          &::before {
            margin-right: 10px;
            @include rem(font-size, 18px);
          }

          @include hover {
            color: primary-color(purple);
          }
        }
      }
    }
  }


  &[data-level='1'] {
    background-color: primary-color(lightgray);
  }

  .inner {
    position: relative;
    padding: 0 28px;
    overflow: hidden;
    background-color: primary-color(white);
    transition: $ts $te;
  }

  .mobile-menu-back {
    display: flex;
    align-items: center;
    width: 100%;
    color: primary-color(black);
    font-family: $inter-b;
    @include rem(font-size, 14px);
    letter-spacing: rem(.2px);
    @include line-height(19, 14);
    text-align: left;
    text-transform: uppercase;
    transition: .3s $te;

    span {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 12px;
      flex: none;
      width: 27px;
      height: 27px;
      border: 2px solid primary-color(purple);
      border-radius: 4px;
      transition: $ts $te;
      @include icon(caret) {
        position: relative;
        top: -1px;
        left: -1px;
        @include rem(font-size, 11px);
        color: primary-color(purple);
        transform: scaleX(-1);
        transition: $ts $te;
      }

      @media not all and (min-resolution: .001dpcm) {
        @supports (-webkit-appearance: none) and (stroke-color: transparent) {

          &::before {
            top: 0;
            left: 0;
          }
        }
      }
    }

    @include hover {
      color: primary-color(purple);

      span {
        background-color: primary-color(purple);

        &::before {
          color: primary-color(white);
        }
      }
    }
  }

  .menu-close {
    position: relative;
    left: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: none;
    width: 40px;
    height: 40px;
    color: primary-color(black);
    @include rem(font-size, 18px);
    border-radius: 100%;
    @include font-weight(normal);
    transition: $ts $te;

    @include icon(close) {
      flex: none;
    }

    @include hover {
      background-color: primary-color(purple);
      color: primary-color(white);
    }
  }

  .nav-main {
    margin-bottom: 28px;
    transform: translateX(0);
    transition: .3s $te;

    @include from($mobile-menu-break) {
      display: none;
    }

    // Top-Level Items
    & > li {
      display: flex;
      align-items: center;
      padding: 12px 0;

      &:not(:last-child) {
        border-bottom: 1px solid primary-color(gray);
      }

      &.mobile-user {

        .nav-main-link {
          font-family: $inter;
          text-transform: none;
          color: primary-color(black);
        }
      }
    }

    &-link {
      flex: 1 1 0%;
      padding: 3px 0;
      color: primary-color(black);
      font-family: $inter-b;
      @include rem(font-size, 14px);
      letter-spacing: rem(.2px);
      @include line-height(19, 14);
      text-transform: uppercase;

      &[data-active="true"] {
        color: primary-color(purple);
      }

      @include hover {
        color: primary-color(purple);
      }
    }

    &-next {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: none;
      width: 27px;
      height: 27px;
      border: 2px solid primary-color(purple);
      border-radius: 4px;
      @include rem(font-size, 11px);
      color: primary-color(purple);
      @include icon(caret) {
        position: relative;
        @include rem(font-size, 11px);
        top: -1px;
        left: 1px;
        flex: none;
      }

      @media not all and (min-resolution: .001dpcm) {
        @supports (-webkit-appearance: none) and (stroke-color: transparent) {

          &::before {
            top: 0;
            left: 0;
          }
        }
      }

      @include hover {
        background-color: primary-color(purple);
        color: primary-color(white);
      }
    }

    &-panel {
      position: absolute;
      top: 0;
      left: calc(100% + 28px);
      width: 100%;
      opacity: 0;
      z-index: -1;
      transition: .3s $te;
      background-color: primary-color(white);

      &[aria-hidden='false'] {
        opacity: 1;
        z-index: 2;
      }

      &[data-opened='true'] {
        left: 0;
        z-index: 2;
        opacity: 1;

        @include from(5) {
          left: 0;
        }
      }

      &[data-opened='false'] {
        left: calc(100% + 28px);
        z-index: 0;
        opacity: 0;
        transition: $ts $te;
        visibility: hidden;
      }

      // child levels
      & ul {

        .mobile-nav-section-title {
          display: flex;
          margin-top: 20px;
          padding: 12px 0;
          @include rem(font-size, 14px);
          @include font-weight(bold);
          text-transform: uppercase;
          @include rem(letter-spacing, 1px);
          color: primary-color(black);
          border-bottom: 0;

          &::before {
            content: '';
            flex: none;
            display: block;
            width: 25px;
            height: 3px;
            margin: 0 15px 0 0;
            background-color: primary-color(red);
          }
        }

        & li {
          display: flex;
          align-items: center;
          padding: 12px 0;
          border-bottom: 1px solid primary-color(gray);

          &:first-child {
            border-bottom: 0;
          }

          &:last-child {
            margin: 0;
            border-bottom: 0;
          }

          & a {
            flex: 1 1 0%;
            padding: 3px 0;
            color: primary-color(black);
            font-family: $inter-sb;
            @include rem(font-size, 14px);
            letter-spacing: rem(.2px);
            @include line-height(19, 14);
            text-transform: none;

            &[data-active="true"] {
              color: primary-color(purple);
            }

            @include hover {
              color: primary-color(purple);
            }
          }
        }
      }

      .mobile-menu-practice {
        display: block;
        padding: 0;

        .nav-section-title-container {
          margin-bottom: 25px;

          a {
            color: primary-color(purple);
          }
        }

        .nav-section {
          margin-bottom: 40px;

          .nav-section-link {
            margin-bottom: 8px;

            &.-view-all {
              padding-top: 3px;
            }
          }

          a {
            display: inline;
            padding: 0;
            font-family: $inter;

            &:not(.-caret) {
              border-bottom: 1px solid transparent;
            }

            &.-caret {
              color: primary-color(purple);
              @include rem(font-size, 12px);
              text-transform: uppercase;
            }

            @include hover {
              color: primary-color(purple);

              &:not(.-caret) {
                border-bottom: 1px solid primary-color(purple);
              }
            }
          }

          .nav-section-subtitle {
            margin-bottom: 12px;
            @include rem(font-size, 12px);
            @include font-weight(bold);
            text-transform: uppercase;
            color: primary-color(black);
          }
        }

        .promo {

          .top {
            position: relative;
            padding: 30px;
            color: primary-color(white);
            background-color: primary-color(purple);

            a {
              color: primary-color(white);
              font-family: $inter;
              @include rem(font-size, 16px);
              @include line-height(24, 16);

              @include from(12) {
                display: block;
                padding-bottom: 30px;
              }
            }

            .journal {
              display: none;
            }
          }

          .bottom {
            position: relative;
            z-index: 1;
            padding: 30px;
            background: linear-gradient(180deg, rgba(237, 237, 237, 0) 0%, primary-color(lightgray) 100%);

            p {
              @include rem(font-size, 14px);
              @include line-height(20, 14);
            }
          }
        }
      }
    }
  }


  // utility nav
  .nav-utility {
    background-color: primary-color(lightgray);
    margin: 0 -28px;
    padding: 30px 28px;
    transition: background-color .1s $te;

    &-title {
      margin-bottom: 20px;
      font-family: $inter-b;
      @include rem(font-size, 14px);
      @include line-height(17, 14);
      color: primary-color(black);
      @include rem(letter-spacing, 2px);
      text-transform: uppercase;
    }

    li {
      margin-bottom: 15px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      display: inline;
      color: primary-color(black);
      border-bottom: 1px solid transparent;
      transition: $ts $te;

      @include hover {
        color: primary-color(purple);
        border-bottom: 1px solid primary-color(purple);
      }
    }
  }


  // become member button and dropdown
  .mobile-become-member {
    position: fixed;
    z-index: 1;
    bottom: 0;
    right: 0;
    width: 360px;

    @include from(5) {
      width: 450px;
    }

    .mobile-become-member-btn {
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      justify-content: center;
      width: 100%;
      height: 54px;
      border: 0;
      border-radius: 0;
      text-align: center;
      transition: 0s $te;

      &::before {
        display: none;
      }

      @include hover {
        background-color: primary-color(white);
      }

      [data-mobile-menu='open'] & {
        opacity: 1;
        visibility: visible;
        pointer-events: auto;
        transition: opacity .6s $te, background-color $ts $te;
      }

      &[aria-expanded='true'] {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        display: none;
      }
    }

    .mobile-become-member-dropdown {
      display: none;
      width: 100%;
      background: primary-color(yellow);
      text-align: center;
      transition: 0s $te;

      [data-mobile-menu='closed'] & {
        display: none !important;
      }

      .mobile-become-member-dropdown-inner {
        padding: 35px 28px;
      }

      .label {
        width: 100%;
        margin-bottom: 20px;
        font-family: $inter-b;
        @include rem(font-size, 14px);
        @include rem(letter-spacing, 1.1px);
        @include line-height(17, 14);
        text-transform: uppercase;
      }

      ul {
        margin: 0 22px;

        li {
          margin: 10px 0 18px;
        }

        a {
          display: inline;
          @include rem(font-size, 14px);
          @include line-height(17, 14);
          color: primary-color(black);
          border-bottom: 1px solid transparent;

          @include hover {
            border-color: primary-color(black);
          }
        }
      }

      .join-btn {
        margin: 10px auto 0;

        &.btn {

          &::before {
            background-color: #ffde8f;
          }
        }
      }

      .mobile-become-member-dropdown-close {
        width: 100%;
        margin: 0;
        padding: 10px;
        background-color: primary-color(lightgray);
        font-family: $inter-b;
        @include rem(font-size, 11px);
        @include line-height(13, 11);
        @include rem(letter-spacing, 1px);
        text-transform: uppercase;
        box-shadow: 0 -4px 4px rgba(0, 0, 0, .08);
        @include icon(caret) {
          position: relative;
          top: 1px;
          margin-right: 8px;
          transform: rotate(90deg);
        }

        @include hover {
          background-color: #ffde8f;
        }
      }
    }
  }
}
