html[data-modal='open'] {
  height: 100vh;
  overflow: hidden;

  // chrome/safari nonsense...
  .backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #000;
    opacity: .7;
  }

  ::backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #000;
    opacity: .7;
  }
}

._dialog_overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.video-modal {
  display: block;
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
  width: 95vw;
  max-width: 1235px;
  background: none;
  padding: 0;
  border: 0;
  margin: 0 auto;
  opacity: 1;
  visibility: visible;
  transform: translate(0, -50%);
  transition: .33s $te;

  &:not([open]) {
    opacity: 0;
    visibility: hidden;
    transform: translate(0, calc(-50% - 40px));
    transition-duration: $ts;
  }

  // &::backdrop,
  // & + .backdrop {
  //   position: fixed;
  //   top: 0;
  //   right: 0;
  //   bottom: 0;
  //   left: 0;
  //   background: #000;
  //   opacity: .7;
  // }

  .inner {
    position: relative;
    padding-top: rem(44px);
  }

  .close {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    font-family: $inter-b;
    @include rem(font-size, 15px);
    color: primary-color(white);
    text-transform: uppercase;
    transition: .3s $te;

    @include icon(close, after) {
      flex: none;
      margin-left: 20px;
      @include rem(font-size, 24px);
    }

    @include hover {
      color: primary-color(yellow);
    }
  }

  .content {
    position: relative;
    background-color: #000;
    overflow: hidden;
  }
}
