// Accordion
//
// Markup: accordion.twig
//
// Styleguide Components.Accordion
.accordion {
  @include grid-placement;
  width: 100%;
  max-width: 900px;
  margin: 0 auto 60px;

  .container {
    position: relative;
    overflow: hidden;
  }

  header {
    margin: 0 0 is-clamp(30px, 6vw, 40px);

    @include from(9) {
      text-align: center;
    }

    .btn {
      margin-top: 30px;
    }
  }

  .h3 {
    margin-bottom: is-clamp(30px, 6vw, 50px);
  }

  .h4 {
    margin: 0;
  }

  &-item {

    &:first-child {

      .accordion-trigger {
        border-top: 0;
      }
    }
  }

  &-trigger {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    padding: is-clamp(20px, 4vw, 30px) 0;
    border: 0;
    border-top: 1px solid $hr-color;
    background-color: transparent;
    @include line-height(30, 22);
    text-align: left;

    &-icon {
      position: relative;
      flex: none;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 28px;
      height: 28px;
      margin-left: 25px;
      background-color: primary-color(white);
      border: 2px solid primary-color(purple);
      border-radius: 100%;
      transition: $ts $te;
      @include icon(plus, after) {
        position: relative;
        @include rem(font-size, 13px);
        line-height: 1;
        color: primary-color(purple);
        transition: $ts $te;
      }
    }

    [data-expanded='true'] & {

      .accordion-trigger-icon {
        background-color: primary-color(purple);
        @include icon(minus, after, false) {
          color: primary-color(white);
        }
      }

      @include hover {

        .accordion-trigger-icon {
          background-color: primary-color(white);

          &::after {
            color: primary-color(purple);
          }
        }
      }
    }

    @include hover {
      color: primary-color(purple);

      .accordion-trigger-icon {
        background-color: primary-color(purple);

        &::after {
          color: primary-color(white);
        }
      }
    }
  }

  &-content {
    display: none;
    max-width: 870px;
    padding: 0 0 is-clamp(30px, 6vw, 50px);
  }
}

// quick fix for accordions with headers inside of an article detail page 
.article-detail {

  .accordion {

    header {
      text-align: left;

      .h2 {
        @include font-clamp(rem(22px), 4.4vw, rem(26px));
        @include line-height(36, 26);
      }
    }
  }
}
