// Accordion Categories
//
// Markup: accordion-categories.twig
//
// Styleguide Components.Accordion Categories
.accordion-categories {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  padding: is-clamp(40px, 5vw, 100px) 0;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    @include grid-placement;
    @include wide-padding;
  }

  header {
    max-width: 700px;
    margin: 0 0 is-clamp(40px, 8vw, 70px);

    @include from(8) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }

    p {
      max-width: 500px;

      @include from(8) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .button-row {
      margin-top: 30px;
    }
  }

  $sidetabs_breakpoint: 9;

  // overrides of base accordion-tabs settings
  .accordion-tabs {

    @include from($sidetabs_breakpoint) {
      display: flex;
    }

    .tabs {
      display: none;

      @include from($sidetabs_breakpoint) {
        display: block;
        width: calc(50% - 25px);
        margin-right: 25px;
      }

      @include from(10) {
        flex: none;
        width: 400px;
        margin-right: 0;
      }
    }

    &.-sidetabs {

      .accordion-tabs-item {

        .tab-trigger-mobile {
          display: flex;

          @include from($sidetabs_breakpoint) {
            display: none;
          }
        }
      }

      .h4 {

        @include to(8) {
          @include font-clamp(rem(18px), 3.6vw, rem(18px));
          @include line-height(24, 18);
        }
      }
    }

    .content {

      @include from($sidetabs_breakpoint) {
        width: calc(50% - 25px);
        margin-left: 25px;
      }

      @include from(10) {
        width: auto;
        flex: 1 1 auto;
        margin-left: 100px;
      }

      .accordion-tabs-content-intro {
        margin: 30px 0 0;
        padding: 0 10px;

        @include from(7) {
          padding: 0 30px;
        }

        @include from(9) {
          margin: 0 0 clamp(30px, 3.33vw, 50px);
          padding: 0;
        }
      }
    }

    .accordion {
      margin-bottom: 30px;
      padding: 35px 30px 0;

      @include from($sidetabs_breakpoint) {
        margin-top: -30px;
        margin-bottom: 0;
        padding: 0;
      }
    }
  }

  // capabilities model color overrides
  &[data-capability-model='true'] {

    .accordion-tabs {

      .tabs {

        > ul {

          > li {
            position: relative;

            &.-active {

              &::before {
                content: '';
                position: absolute;
                z-index: 1;
                top: 0;
                left: 0;
                width: 5px;
                height: 100%;
                background-color: primary-color(green);
              }
            }
          }
        }
      }

      &.-sidetabs {

        .accordion-tabs-item {

          .tab-trigger-mobile {

            @include hover {

              &::after {
                border-color: primary-color(green);
                color: primary-color(green);
                background-color: primary-color(white);
              }
            }
          }

          &.-active {

            .tab-trigger-mobile {
              background-color: primary-color(green);
            }
          }
        }

        .tab-trigger {
          background-color: primary-color(white);

          &.-active {
            color: primary-color(black);
          }

          &::after {
            border-color: primary-color(black);
            color: primary-color(black);
          }

          @include hover {

            &::after {
              border-color: primary-color(green);
              color: primary-color(green);
              background-color: primary-color(white);
            }
          }
        }
      }
    }
  }

  [data-active-section='adults'] & {

    .accordion-tabs {

      .tabs {

        > ul {

          > li {

            &.-active {

              &::before {
                background-color: primary-color(red);
              }
            }
          }
        }
      }

      &.-sidetabs {

        .accordion-tabs-item {

          .tab-trigger-mobile {

            @include hover {

              &::after {
                border-color: primary-color(red);
                color: primary-color(red);
                background-color: primary-color(white);
              }
            }
          }

          &.-active {

            .tab-trigger-mobile {
              background-color: primary-color(red);
            }
          }
        }

        .tab-trigger {

          @include hover {

            &::after {
              border-color: primary-color(red);
              color: primary-color(red);
            }
          }
        }
      }
    }
  }

  [data-active-section='pediatrics'] & {

    .accordion-tabs {

      .tabs {

        > ul {

          > li {

            &.-active {

              &::before {
                background-color: primary-color(purple);
              }
            }
          }
        }
      }

      &.-sidetabs {

        .accordion-tabs-item {

          .tab-trigger-mobile {

            @include hover {

              &::after {
                border-color: primary-color(purple);
                color: primary-color(purple);
                background-color: primary-color(white);
              }
            }
          }

          &.-active {

            .tab-trigger-mobile {
              background-color: primary-color(purple);
            }
          }
        }

        .tab-trigger {

          @include hover {

            &::after {
              border-color: primary-color(purple);
              color: primary-color(purple);
            }
          }
        }
      }
    }
  }

  [data-active-section='universal'] & {

    .accordion-tabs {

      .tabs {

        > ul {

          > li {

            &.-active {

              &::before {
                background-color: primary-color(yellow);
              }
            }
          }
        }
      }

      &.-sidetabs {

        .accordion-tabs-item {

          .tab-trigger-mobile {

            @include hover {

              &::after {
                border-color: primary-color(yellow);
                color: primary-color(yellow);
                background-color: primary-color(white);
              }
            }
          }

          &.-active {

            .tab-trigger-mobile {
              background-color: primary-color(yellow);
            }
          }
        }

        .tab-trigger {

          @include hover {

            &::after {
              border-color: primary-color(yellow);
              color: primary-color(yellow);
            }
          }
        }
      }
    }
  }

  [data-active-section='other'] & {

    .accordion-tabs {

      .tabs {

        > ul {

          > li {

            &.-active {

              &::before {
                background-color: primary-color(green);
              }
            }
          }
        }
      }

      &.-sidetabs {

        .accordion-tabs-item {

          .tab-trigger-mobile {

            @include hover {

              &::after {
                border-color: primary-color(green);
                color: primary-color(green);
                background-color: primary-color(white);
              }
            }
          }

          &.-active {

            .tab-trigger-mobile {
              background-color: primary-color(green);
            }
          }
        }

        .tab-trigger {

          @include hover {

            &::after {
              border-color: primary-color(green);
              color: primary-color(green);
            }
          }
        }
      }
    }
  }
}
