//sass-lint:disable single-line-per-selector
//sass-lint:disable no-important
//sass-lint:disable quotes
//sass-lint:disable force-pseudo-nesting
//sass-lint:disable nesting-depth

.coveo-filter-controller {
  display: block;
  position: relative;
  width: 100%;
  margin-bottom: 30px;
  padding: 10px 20px 10px 40px;
  background: primary-color(lightgray);
  color: primary-color(black) !important;
  @include rem(font-size, 16px);
  @include font-weight(bold);

  @include from(10) {
    display: none !important;
  }

  @include icon(menu) {
    position: absolute;
    top: 6px;
    left: 10px;
    @include rem(font-size, 12px);
    padding-top: 7px;
  }

  &.-closed {
    @include icon(plus, after) {
      position: absolute;
      top: 8px;
      right: 10px;
      @include rem(font-size, 12px);
      padding-top: 5px;
    }
  }

  &.-opened {
    @include icon(minus, after) {
      position: absolute;
      top: 8px;
      right: 10px;
      @include rem(font-size, 12px);
      padding-top: 5px;
    }
  }

  @include from(10) {
    display: none;
  }

  @include hover {
    background-color: primary-color(purple);
    color: primary-color(white) !important;

    &::before,
    &::after {
      color: primary-color(white);
    }
  }
}


.CoveoSearchInterface {

  .coveo-facet-header {
    background: primary-color(white);
  }

  .coveo-facet-header-title {
    position: relative;
    padding-left: 40px;
    font-family: $inter-b;
    color: primary-color(black);
    font-size: 18px;
    text-transform: uppercase;
    @include rem(letter-spacing, 1px);

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 8px;
      left: 0;
      width: 30px;
      height: 3px;
      background-color: primary-color(red);
    }
  }

  .coveo-facet-header-eraser {
    margin-right: 5px;
    color: primary-color(darkgray);
  }

  .coveo-facet-settings-more-svg {
    width: 22px;
    height: 22px;
    color: primary-color(darkgray);
  }

  .CoveoFacet, .CoveoFacetRange, .CoveoFacetSlider, .CoveoHierarchicalFacet, .CoveoCategoryFacet, .CoveoTimespanFacet {
    margin: 0 0 40px;
    padding: 0;
    border: 0;
    border-radius: 3px;
    position: relative;
  }

  .CoveoFacet {
    position: relative;
    @include font-weight(medium);
    background-color: transparent;

    .coveo-facet-header {
      background-color: transparent;
    }

    .CoveoFacet[data-title="Search Within"] {
      background-color: transparent;

      &::before {
        display: none;
      }

      .coveo-facet-header {
        background-color: transparent;
      }
    }
  }

  .coveo-facet-value .coveo-facet-value-checkbox {
    width: 16px;
    height: 16px;
  }

  .coveo-facet-value.coveo-facet-search-button {
    color: primary-color(black);
  }

  // checkbox plus icon
  .coveo-facet-value.coveo-facet-search-button .coveo-facet-value-checkbox > .coveo-facet-value-checkbox-svg, .coveo-facet-value.coveo-facet-search-button:hover .coveo-facet-value-checkbox > .coveo-facet-value-checkbox-svg {
    color: primary-color(red);
    width: 12px;
    height: 8px;
    bottom: 8px;
  }

  .coveo-facet-value-checkbox .coveo-facet-value-checkbox-svg {
    bottom: -2px;
    left: 1px;
  }

  // checkbox check icon
  .coveo-facet-value.coveo-with-hover:hover:not(.coveo-facet-search-button):not(.coveo-excluded):not(.coveo-facet-value-will-exclude):not(.coveo-has-childs-selected) .coveo-facet-value-checkbox svg {
    bottom: -2px;
    left: 1px;
  }

  .coveo-facet-value.coveo-excluded:not(.coveo-facet-value-will-exclude) .coveo-facet-value-checkbox svg {
    bottom: -3px;
    left: 3px;
  }

  .coveo-facet-more-icon,
  .coveo-facet-less-icon {
    top: 7px;
    left: calc(50% - 12px);
  }

  .coveo-facet-more .coveo-facet-more-icon-svg,
  .coveo-facet-more .coveo-facet-less-icon-svg,
  .coveo-facet-less .coveo-facet-more-icon-svg,
  .coveo-facet-less .coveo-facet-less-icon-svg {
    background: transparent !important;
  }

  .coveo-facet-more, .coveo-facet-less {
    width: 100%;
    height: 20px;
    background: rgba(primary-color(gray), .7);
    cursor: pointer;
    text-align: center;
    transition: $te $ts;
    margin-top: 10px;

    .coveo-facet-more-icon-svg, .coveo-facet-less-icon-svg {
      color: primary-color(purple);
      background: primary-color(lightgray);
      transition: $te $ts;
    }

    @include hover {
      background: primary-color(purple);
      border: 0;
      outline: 0;

      .coveo-facet-more-icon-svg, .coveo-facet-less-icon-svg {
        width: 10px;
        height: 6px;
        color: primary-color(white);
      }
    }
  }

  .coveo-facet-value-count {
    padding: 0 10px;
    color: rgba(primary-color(darkgray), .5);
    @include rem(font-size, 16px);
  }

  .coveo-facet-value {
    padding: 0 20px;
    margin: 0 0 5px;
    line-height: 25px;
    position: relative;
    transition: $te $ts;
  }

  .coveo-facet-value-exclude {
    top: 11px;
  }

  .coveo-facet-value:not(.coveo-excluded).coveo-facet-value-will-exclude .coveo-facet-value-checkbox svg {
    bottom: -3px;
    left: 3px;
    color: primary-color(red);
  }

  .coveo-facet-value:not(.coveo-excluded).coveo-facet-value-will-exclude:hover {

    .coveo-facet-value-checkbox svg {
      bottom: -3px;
      left: 3px;
      color: primary-color(red);
    }
  }

  .coveo-facet-value.coveo-selected .coveo-facet-value-checkbox svg {
    bottom: -2px;
  }

  // facet hover

  .coveo-facet-value.coveo-with-hover:hover, .coveo-facet-value.coveo-with-hover.coveo-focused {
    background-color: rgba(primary-color(gray), .5);
  }

  .coveo-slider-caption {
    margin-top: 20px;
    color: primary-color(black);
    font-weight: normal;
  }

  .coveo-slider-button {
    width: 20px;
    height: 20px;

    @include hover {
      background-color: primary-color(purple);
    }
  }

  .CoveoFacetSlider.coveo-disabled .coveo-slider-button,
  .coveo-slider-button {
    background-color: primary-color(darkgray);

    &::before,
    &::after {
      height: 11px;
      width: 2px;
      top: 4px;
      border-left: 0;
    }

    &::before {
      left: 6px;
    }

    &::after {
      right: auto;
      left: 10px;
    }
  }

  .CoveoFacetSlider.coveo-disabled .coveo-slider-line,
  .coveo-slider-line.coveo-active {
    background-color: primary-color(gray);
  }

  .CoveoSearchInterface input[type='text'], .CoveoSearchInterface textarea {
    border: 1px solid primary-color(red);
    border-radius: 30px;
    padding: 5px 15px 5px 30px;
    color: primary-color(black);
  }

  .coveo-facet-search-magnifier {
    position: absolute;
    right: 30px;
    top: 14px;
  }

  .coveo-facet-search-magnifier-svg {
    margin: 5px 6px 5px 10px;
    width: 15px;
    height: 15px;
    color: primary-color(gray);
  }

  .coveo-facet-search {
    border: 1px solid rgba(primary-color(gray), .5);
    border-bottom: 0;
    border-radius: 2px;
    position: relative;
    background: white;
    display: none;
    margin: 2px 20px 1px;
    padding: 4px 20px;
  }

  .coveo-facet-search-results {
    border: 1px solid rgba(primary-color(gray), .5);
    border-top: 0;
  }

  .coveo-facet-search-results .coveo-facet-value .coveo-facet-value-checkbox {
    width: 14px;
    height: 14px;
    box-sizing: content-box;
    border: 0;
    border-radius: 2px;
    text-align: center;
    vertical-align: middle;
    margin-right: 15px;
    background: rgba(primary-color(gray), .5);
    position: relative;
    top: 5px;
    flex-shrink: 0;
  }

  .coveo-facet-value.coveo-facet-search-button .coveo-facet-value-checkbox > .coveo-facet-value-checkbox-svg, .CoveoSearchInterface .coveo-facet-value.coveo-facet-search-button:hover .coveo-facet-value-checkbox > .coveo-facet-value-checkbox-svg {
    bottom: -4px !important;
    left: 2px !important;
  }

  .coveo-facet-search-results .coveo-facet-value.coveo-with-hover:hover:not(.coveo-facet-search-button):not(.coveo-excluded):not(.coveo-facet-value-will-exclude):not(.coveo-has-childs-selected) .coveo-facet-value-checkbox svg {
    bottom: -3px !important;
  }
}
