// Member Benefits-Grid
//
// .-modifier - Modifier description
//
// Markup: member-benefits-grid.twig
//
// Styleguide Components.Member Benefits-Grid
.member-benefits-grid {
  @include grid-placement;
  @include component-margin;
  padding: 50px 0 0;


  // all common classes here!
  .inner {
    max-width: 1280px;
    margin: 0 auto;
  }

  .h2 {
    text-align: center;

    @include from (10) {
      text-align: left;
      margin: 0 40px 0 0;
    }
  }


  .h3 {
    text-align: center;
    color: primary-color(purple);
    position: relative;

    @include from (10) {
      margin: 0;
      padding: 0 10px;
    }
  }

  &-heading {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0 20px;

    &-line {
      height: 1px;
      width: 20%;
      background: primary-color(purple);
    }
  }

  .btn {
    display: block;
    margin: 0 auto 30px;
    width: max-content;

    @include from (10) {
      white-space: normal;
      max-width: 170px;
    }

    @include from (12) {
      white-space: normal;
      max-width: 100%;
    }
  }



  &-icon {
    position: relative;
    background: #b0ef8f;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    margin: 0 15px 0 0;
    box-shadow: 3px 3px 10px primary-color(gray);

    @include icon(check, after) {
      @include absolute(4px, 0, 0, 4px);
      font-size: rem(12px);
      color: #009045;
    }

    @include from (10) {
      margin: 0 auto;
      height: 30px;
      width: 30px;

      @include icon(check, after) {
        @include absolute(7px, 0, 0, 7px);
        font-size: rem(16px);
      }
    }
  }



  // grid desktop
  &-panel {
    display: none;

    @include from (10) {
      display: grid;
      grid-template-columns: minmax(370px, 1fr) 1fr 1fr 1fr;
      grid-template-rows: min-content 1fr min-content min-content;
    }

    &-row {
      display: flex;
      flex-direction: column;
      justify-content: center;

      &[data-popular="true"] {
        @include rem(letter-spacing, 2px);
        font-size: rem(12px);
        font-family: $inter-b;
        padding: 10px;
        background: primary-color(purple);
        color: primary-color(white);
        text-align: center;
        text-transform: uppercase;
        box-shadow: 0 0 5px 0 primary-color(black);
        clip-path: inset(0 -5px 0 -5px);
      }

      &[data-background="grey"] {
        position: relative;
        padding: 30px 0;

        &::before {
          content: '';
          @include absolute(0, 0, 0, 0);
          height: 100%;
          width: 100%;
          background: linear-gradient(#c4c4c4, #fff);
          opacity: .3;
        }
      }

      &[data-background="purple"] {
        position: relative;
        padding: 30px 0;
        box-shadow: 0 0 5px 0 primary-color(black);
        clip-path: inset(0 -5px 0 -5px);

        &::before {
          content: '';
          @include absolute(0, 0, 0, 0);
          height: 100%;
          width: 100%;
          background: linear-gradient(#eee4ed, #fff);
        }
      }

      &[data-border="true"] {
        border-top: 1px solid primary-color(gray);
      }

      &[data-shadow="true"] {
        box-shadow: 0 0 5px primary-color(black);
        clip-path: inset(0 -5px 0 -5px);
        position: relative;

        &::before {
          content: '';
          position: absolute;
          bottom: -5px;
          height: 10px;
          width: 100%;
          background: primary-color(white);
        }
      }
    }

    &-info {
      position: relative;
    }
  }




  // tabs mobile 
  &-tab {

    max-width: 600px;
    margin: 0 auto;

    @include from (10) {
      display: none;
    }
  }

  &-tabs {
    display: flex;
    justify-content: space-evenly;
    border-bottom: 1px solid primary-color(gray);
    margin: 40px 0 50px;

    &-button {
      padding: 10px 20px;
      margin: 0;
      transition: $ts $te;

      @include hover {
        color: primary-color(white);
        background: primary-color(purple);
      }

      &[data-active='true'] {
        color: primary-color(white);
        background: primary-color(purple);
      }
    }
  }

  &-info {
    margin: 0 0 40px;
    text-align: center;

    &-dues {
      font-size: rem(14px);
      font-family: $inter-b;
      text-transform: uppercase;
      color: primary-color(purple);
    }

    &-pay {
      margin: 10px 0;

      span {
        font-size: rem(22px);
        font-family: $inter-b;
      }
    }
  }

  &-includes {
    display: flex;
    margin: 0 0 20px;

    p {
      @include rem(font-size, 16px);
      @include line-height(28, 16);
      width: calc(100% - 50px);
    }

    a {
      color: primary-color(black);
      text-decoration: underline;
      text-underline-offset: 3px; // sass-lint:disable-line no-misspelled-properties

      @include hover {
        text-decoration: none;
      }
    }

    span {
      @include rem(font-size, 14px);
      margin: 0 auto;
    }

    @include from (10) {
      margin: 0;
      padding: 20px 20px 20px 0;
    }
  }


  &-description {
    transition: $ts $te;

    &[data-hidden='true'] {
      display: none;
    }
  }
}
