// Membership Benefits
//
// Markup: membership-benefits.twig
//
// Styleguide Components.Membership Benefits
.membership-benefits {
  @include grid-placement;
  @include component-margin;
  padding: 50px 0 is-clamp(50px, 10vw, 80px);

  .inner {
    max-width: 1280px;
    margin: 0 auto;
  }

  header {
    max-width: 700px;
    margin: 0 auto is-clamp(40px, 8vw, 70px);
    text-align: center;
  }

  .benefit-types {

    @include from(9) {
      display: flex;
      margin: 0 -16px;
    }

    @include from(11) {
      margin: 0 -22px;
    }
  }

  .benefit-type {
    max-width: 500px;
    margin: 0 auto 50px;

    @include from(9) {
      width: calc(33.33% - 16px);
      margin: 0 8px;
    }

    @include from(11) {
      width: calc(33.33% - 44px);
      margin: 0 22px;
    }

    .title {
      padding: is-clamp(15px, 3vw, 25px);
      background-color: primary-color(purple);
      text-align: center;

      &.h3 {
        margin: 0;
        color: primary-color(white);
      }
    }

    .description {
      padding: 0 35px 45px;
      background-image: linear-gradient(180deg, primary-color(lightgray) 0%, primary-color(white) 100%);
      text-align: center;
      transition: height $ts $te;

      @include from(9) {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      @include between(9, 10) {
        padding: 0 25px 35px;
      }

      p {
        margin-bottom: 30px;
        @include line-height(28, 18);
      }

      .btn {

        @include from(9) {
          margin-top: auto;
        }
      }
    }

    .price {
      padding: 35px 0;

      @include from(9) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 125px;
        padding: 20px 0;
      }

      .h3 {
        margin-bottom: 0;

        &:not(:first-child) {
          margin-top: 20px;
        }
      }

      .secondary-price {
        margin-top: 5px;
      }
    }

    .benefit {
      padding: 35px;
      background-color: primary-color(lightgray);

      @include between(9, 10) {
        padding: 25px;
      }

      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        margin-top: 25px;
        margin-bottom: 25px;
        color: primary-color(purple);

        &:first-child {
          margin-top: 0;
        }
      }

      p,
      li {
        margin-bottom: 15px;
        @include line-height(24, 16);

        @include from(8) {
          @include rem(font-size, 17px);
          @include line-height(24, 17);
        }
      }

      hr {
        margin: 25px 0;
      }
    }
  }
}
