.coveo-for-sitecore-search-box-container {

  .CoveoSearchbox {
    position: relative;
    margin-right: 0 !important;
    min-width: 280px !important;
    border-radius: 50px;

    @media (min-width: 350px) {
      min-width: 320px !important;
    }

    .OmnixBox {
      border-radius: 50px;
    }

    .magic-box .magic-box-input .magic-box-underlay {
      height: 46px;
      padding: 12px 15px 12px 20px;
    }

    .magic-box .magic-box-input .magic-box-underlay,
    .magic-box .magic-box-input > input {
      color: primary-color(black);
    }

    .magic-box.magic-box-notEmpty .magic-box-clear {
      visibility: hidden !important;
    }

    .magic-box-icon {
      display: none !important;
      width: 0 !important;
    }

    .magic-box {
      border: thin solid transparent;
      border-radius: 50px;

      .magic-box-input {
        height: 46px;
        border-radius: 50px;

        > input {
          height: 46px;
          padding: 12px 70px 12px 20px;
          color: primary-color(darkgray);
          font-family: $inter;
        }
      }
    }

    .CoveoSearchButton {
      position: absolute;
      top: 1px;
      left: auto;
      right: 0;
      width: 60px;
      height: 46px;
      border-radius: 0;
      z-index: 99;
      background: transparent;
      border: 0;
      transition: $te $ts;

      @include hover {
        background: transparent;

        .coveo-search-button-svg {
          width: 20px;
          height: 20px;
          color: primary-color(purple) !important;
          fill: primary-color(purple);
        }

        .coveo-magnifier-circle-svg {
          fill: primary-color(purple);
        }
      }

      .coveo-search-button-svg {
        width: 20px;
        height: 20px;
        color: primary-color(black);
        transition: $ts $te;
      }

      .coveo-magnifier-circle-svg {
        fill: primary-color(black);
      }
    }

    .coveo-magnifier-circle-svg {
      transition: $ts $te;
    }
  }
}

// COMPONENT SPECIFIC OVERRIDES:

// site header:
.site-header {

  .coveo-search-input-container {
    position: relative;
    top: 1px;
  }

  .coveo-for-sitecore-search-box-container {

    .CoveoSearchbox {
      min-width: 0 !important;

      .magic-box .magic-box-input .magic-box-underlay {
        height: 48px;
        padding: 0 50px 0 22px;
      }

      .magic-box .magic-box-input .magic-box-underlay,
      .magic-box .magic-box-input > input {
        color: primary-color(darkgray);
      }

      .magic-box {

        .magic-box-input {
          height: 48px;

          > input {
            height: 48px;
            padding: 0 50px 0 22px;
            color: primary-color(darkgray);
            border: 1px solid primary-color(gray);
            border-radius: 100px;

            &:focus {
              border-color: primary-color(purple);
            }
          }
        }
      }

      .CoveoSearchButton {
        top: 1px;
        right: 1px;
        width: 50px;
        height: calc(100% - 2px);
        background-color: transparent;
        border: 1px solid transparent;
        border-radius: 0 100px 100px 0;

        &:focus {
          background-color: primary-color(lightgray);
        }

        @include hover {
          background-color: primary-color(purple) !important;

          .coveo-search-button-svg {
            width: 20px;
            height: 20px;
            color: primary-color(white) !important;
            fill: primary-color(white);
          }

          .coveo-magnifier-circle-svg {
            fill: primary-color(white);
          }
        }
      }
    }
  }
}


// mobile nav
.mobile-menu {

  .coveo-for-sitecore-search-box-container {

    .CoveoSearchbox {
      min-width: 0 !important;

      .magic-box .magic-box-input .magic-box-underlay {
        height: 41px;
        padding: 0 50px 0 14px;
      }

      .magic-box {

        .magic-box-input {
          height: 41px;

          > input {
            height: 41px;
            padding: 0 50px 0 14px;
            border: 1px solid primary-color(gray);
            border-radius: 100px;
            @include rem(font-size, 12px);

            &:focus {
              border-color: primary-color(purple);
            }
          }
        }
      }

      .CoveoSearchButton {
        position: absolute;
        top: 1px;
        right: 1px;
        width: 45px;
        height: calc(100% - 2px);
        border-radius: 0 100px 100px 0;

        &:focus {
          background-color: primary-color(lightgray);
        }

        @include hover {
          background-color: primary-color(purple) !important;

          .coveo-search-button-svg {
            width: 20px;
            height: 20px;
            color: primary-color(white) !important;
            fill: primary-color(white);
          }

          .coveo-magnifier-circle-svg {
            fill: primary-color(white);
          }
        }
      }
    }
  }
}


// search hero
.hero-search {

  .aota-coveo-search {
    max-width: 720px;
    margin: 0 auto;
  }
}
