// Pull Quote
//
// Markup: pull-quote.twig
//
// Styleguide Components.Pull Quote
.pull-quote {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  padding: is-clamp(40px, 5vw, 80px) 0;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='true'] {
    padding: is-clamp(40px, 5vw, 115px) 0;
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);

    @include from(8) {
      background-image: url('../images/bg-fifty-fifty-square-left.svg');
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: top right;
    }
  }

  .inner {
    position: relative;
    z-index: 1;
    @include grid-placement;
    @include wide-padding;

    @include from(8) {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: flex-start;
    }
  }

  .photo {
    position: relative;
    margin-bottom: 45px;

    @include to(7) {
      max-width: 500px;
      margin-left: auto;
      margin-right: auto;
    }

    @include from(8) {
      width: 47%;
      margin-bottom: 0;
    }

    @include from(11) {
      width: 55%;
    }
  }

  .text {
    position: relative;
    padding-top: 24px;

    @include to(7) {
      max-width: $mobile-max-width;
      margin: 0 auto;
    }

    @include from(8) {
      width: 53%;
      padding-right: is-clamp(60px, 7.5vw, 160px);
      margin-top: 50px;
    }

    @include from(11) {
      width: 45%;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 45px;
      height: 2px;
      background-color: primary-color(red);

      @include from(14) {
        display: none;
      }
    }

    header {
      margin-bottom: 30px;
    }

    .h2 {
      position: relative;

      &::before {
        display: none;

        @include from(14) {
          content: '';
          display: block;
          position: absolute;
          top: 22px;
          left: -70px;
          width: 52px;
          height: 3px;
          background-color: primary-color(red);
        }
      }
    }
  }

  .image-container {
    position: relative;
    margin-bottom: 10px;

    @include from(13) {
      max-width: 620px;
      margin-bottom: is-clamp(60px, 4.6vw, 118px);
    }

    @include from(16) {
      margin-bottom: 118px;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 18%;
      height: 62%;
      background-image: linear-gradient(40deg, rgba(primary-color(green), 1) 0%, rgba(primary-color(green), 0) 150%);
      opacity: .9;
      clip-path: polygon(0 0, 0% 100%, 100% 0);
    }
  }

  .quote-container {
    position: relative;
    overflow: hidden;
    padding: 30px;
    background-color: primary-color(purple);
    text-align: center;
    transition: $ts $te;

    @include between(6, 7) {
      margin: -160px 60px 0;
    }

    @include between(11, 12) {
      margin: -160px 60px 0;
    }

    @include from(13) {
      position: absolute;
      bottom: 0;
      left: 368px;
      max-width: 410px;
      padding: 50px is-clamp(30px, 2.3vw, 50px) is-clamp(30px, 2.3vw, 50px);
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 200%;
      height: 64px;
      background-image: url('../images/bg-pull-quote.svg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center bottom;

      @include from(13) {
        width: 110%;
        height: 84px;
      }
    }

    .quote {
      position: relative;
      max-width: 300px;
      margin: 0 auto;

      @include icon(quote) {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 68px;
        height: 68px;
        margin: 0 auto 25px;
        background-color: #7a5078;
        border-radius: 100%;
        color: primary-color(white);
        @include rem(font-size, 30px);
      }
    }

    p {
      font-family: $inter-b;
      @include font-clamp(rem(16px), 4vw, rem(18px));
      @include line-height(24, 16);
      color: primary-color(white);

      @include from(8) {
        @include font-clamp(rem(16px), 2vw, rem(18px));
      }
    }
  }

  &[data-image-position='left'] {

    &[data-gradient='true'] {
      background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);

      @include from(8) {
        background-image: url('../images/bg-fifty-fifty-square-right.svg');
      }
    }

    .inner {

      @include from(8) {
        flex-direction: row;
      }
    }

    .text {

      @include from(8) {
        width: calc(53% - 60px);
        padding-right: 0;
        margin-left: 60px;
      }

      @include from(11) {
        width: calc(45% - 60px);
      }

      @include from(14) {
        width: calc(45% - 100px);
        margin-left: 100px;
      }
    }
  }
}
