.hero-membership {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  padding-bottom: is-clamp(20px, 4vw, 40px);

  &[data-margin='true'] {
    @include component-margin;
  }

  .kss-body & {
    margin: 0;
  }

  .inner {
    position: relative;
    overflow: hidden;
    @include grid-placement(full-bleed);
  }

  .photo {
    position: relative;
    min-height: is-clamp(255px, 51vw, 505px);

    picture {
      @include absolute(0, 0, null, 0);
      height: 100%;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
  }

  .text {
    position: relative;
    max-width: 640px;
    margin: -20px auto 0;
    padding: 0 $desktop-padding;
    text-align: center;
    transform-style: preserve-3d;

    @include from(7) {
      margin-top: -35px;
    }

    @include from(9) {
      margin-top: -50px;
    }
  }

  .curve-divider {
    position: absolute;
    top: is-clamp(200px, 40vw, 400px);
    left: 50%;
    width: 110%;
    overflow: hidden;
    line-height: 0;
    transform: translateX(-50%);
    pointer-events: none;

    @include from(14) {
      width: 125%;
    }

    @include from(16) {
      width: 140%;
    }

    svg {
      position: relative;
      display: block;
    }
  }

  .eyebrow {
    margin-bottom: 14px;
    @include font-clamp(rem(12px), 2.4vw, rem(20px));
  }
}
