.capability-model {

  &-hero {
    @extend %grid-columns;
    position: relative;
    @include grid-placement(full-bleed);
    padding: is-clamp(40px, 8vw, 80px) 0;

    @include from(10) {
      padding: 35px 0;
    }

    .inner {
      @include grid-placement;
      display: flex;
      align-items: center;
    }

    &::after,
    &::before {
      content: '';
      display: none;
      position: absolute;
      z-index: 0;
      top: 0;
      right: 0;
      clip-path: polygon(0 0, 100% 0%, 100% 100%);

      @include from(6) {
        display: block;
      }
    }

    &::after {
      width: 124px;
      height: 142px;
      background-color: primary-color(green);
    }

    &::before {
      width: 164px;
      height: 182px;
      background: linear-gradient(90deg, primary-color(yellow) 5%, primary-color(white) 95%);
    }

    &-image {
      display: none;

      @include from(10) {
        display: block;
        flex: none;
        width: 32%;
      }
    }

    &-text {
      position: relative;
      z-index: 1;

      @include from(10) {
        width: 68%;
        margin-left: is-clamp(40px, 4vw, 75px);
      }

      &-inner {
        max-width: 825px;
      }
    }

    [data-active-section='adults'] & {

      &::after {
        background-color: primary-color(red);
      }
    }

    [data-active-section='pediatrics'] & {

      &::after {
        background-color: primary-color(purple);
      }
    }

    [data-active-section='other'] & {

      &::after {
        background-color: primary-color(green);
      }
    }

    [data-active-section='universal'] & {

      &::after {
        background-color: primary-color(yellow);
      }
    }
  }

  .capability-model-pinwheel-svg {

    path {
      transition: $ts $te;
      transform-origin: center;
    }

    .pinwheel-slices {

      @include hover {

        a {

          path {
            opacity: .5;

            &.-white {
              opacity: 1;
              fill: rgba(primary-color(white), .5);
            }
          }
        }
      }
    }

    a {

      &.slice-adults {

        @include hover {

          path {
            opacity: 1;

            // needed this because of weird flicker on the "L" letter ?!?!?!
            &.-white {
              fill: rgba(primary-color(white), 1);
            }
          }
        }
      }

      &.slice-pediatrics {

        @include hover {

          path {
            opacity: 1;

            &.-white {
              fill: rgba(primary-color(white), 1);
            }
          }
        }
      }

      &.slice-other {

        @include hover {

          path {
            opacity: 1;

            &.-white {
              fill: rgba(primary-color(white), 1);
            }
          }
        }
      }

      &.slice-universal {

        @include hover {

          path {
            opacity: 1;

            &.-white {
              fill: rgba(primary-color(white), 1);
            }
          }
        }
      }
    }
  }
}
