// No Image-Grid
//
// Markup: no-image-grid.twig
//
// Styleguide Components.No Image-Grid
.no-image-grid {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  @include component-padding;

  @include to(9) {
    padding-bottom: 160px;
  }

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  &::before,
  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 150px;
    height: 240px;
    transition: $ts $te;

    @include between(10, 12) {
      height: 180px;
      width: 90px;
    }
  }

  &::before {
    background-image: linear-gradient(0deg, rgba(primary-color(yellow), 1) 0%, rgba(primary-color(yellow), 0) 100%);
    clip-path: polygon(0 0, calc(0% + 30px) 0%, 100% 100%, 0% 100%);

    @include between(10, 12) {
      clip-path: polygon(0 0, calc(0% + 15px) 0%, 100% 100%, 0% 100%);
    }
  }

  &::after {
    width: 120px;
    background-color: primary-color(green);
    clip-path: polygon(0 0, 0% 100%, 100% 100%);

    @include between(10, 12) {
      width: 75px;
    }
  }

  .inner {
    @include grid-placement;
    overflow: hidden;
    max-width: 1210px;
    margin: 0 auto;
  }

  header {
    max-width: 700px;
    margin: 0 0 is-clamp(40px, 8vw, 70px);

    @include from(8) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }

    p {
      max-width: 500px;

      @include from(8) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .button-row {
      margin-top: 30px;
    }
  }

  .items {
    position: relative;
    z-index: 1;

    @include from(7) {
      display: flex;
      flex-wrap: wrap;
    }

    @include from(14) {
      margin: 0 -50px;
    }

    .item {
      margin-bottom: is-clamp(40px, 8vw, 60px);
      padding: 5px is-clamp(20px, 4vw, 50px);
      border-left: 4px solid primary-color(red);

      @include from(7) {
        width: 50%;
      }

      @include from(14) {
        width: calc(50% - 100px);
        margin-left: 50px;
        margin-right: 50px;
      }

      .eyebrow {
        margin-bottom: 8px;
      }

      .h5 {
        margin-bottom: 10px;

        a {
          display: inline;
          border-bottom: 1px solid transparent;
          transition: $ts $te;
        }
      }

      .meta {
        margin-bottom: 20px;
        @include rem(font-size, 14px);
      }

      .meta-author {
        margin-right: 30px;
      }

      p {
        @include rem(font-size, 16px);
        @include line-height(24, 16);
      }

      &.js-link-event {

        @include hover {

          .h5 {

            a {
              border-bottom: 1px solid primary-color(purple);
            }
          }
        }
      }
    }
  }
}
