// Event Promo
//
// Markup: event-promo.twig
//
// Styleguide Components.Event Promo
.event-promo {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  position: relative;
  background-image: linear-gradient(90deg, rgba(primary-color(white), 1) 25%, rgba(primary-color(lightgray), 1) 100%);

  &[data-borders='true'] {
    padding: is-clamp(15px, 3vw, 30px) 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 35px;
      background-image: linear-gradient(90deg, rgba(primary-color(lightgray), 1) 0%, rgba(primary-color(white), 1) 75%);
    }
  }

  &[data-margin='true'] {
    @include component-margin;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30px;
    background-image: linear-gradient(90deg, rgba(primary-color(lightgray), 1) 0%, rgba(primary-color(white), 1) 75%);
  }

  .inner {
    position: relative;
    z-index: 1;
    overflow: hidden;
    @include grid-placement(full-bleed);
  }

  .background-image {

    picture {
      @include absolute(0, 0, null, 0);
      height: 100%;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
  }

  .inner-content {
    position: relative;
    @include grid-placement;
  }

  .content {
    position: relative;
    max-width: 1300px;
    margin: is-clamp(50px, 10vw, 60px) 0 0;
    // padding: 0 is-clamp(40px, 8vw, 70px);
    transform-style: preserve-3d;

    @include from(9) {
      margin: 0 auto;
      background-color: transparent;
    }

    &::before {
      @include pseudo-abs($width: 150%);
      left: 50%;
      transform: translateX(-50%);
      background-color: primary-color(white);
      border-top-left-radius: 150% * 2;
      border-top-right-radius: 150% * 2;

      @include from(9) {
        display: none;
      }
    }

    .circle {
      display: none;

      @include from(9) {
        display: block;
        position: absolute;
        z-index: -1;
        top: -85px;
        left: 0;
        width: 100%;
        padding-bottom: 100%;
        background-image: linear-gradient(90deg, rgba(#ea4927, .9) 0%, rgba(#ea4927, 0) 20%, rgba(#f7b03b, 0) 80%, rgba(#f7b03b, .9) 100%);
        border-radius: 100%;
        transform: rotate(-15deg);
      }

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 24px;
        left: 24px;
        width: calc(100% - 48px);
        height: calc(100% - 48px);
        background-color: primary-color(white);
        border-radius: 100%;

        @include from(10) {
          top: 48px;
          left: 48px;
          width: calc(100% - 96px);
          height: calc(100% - 96px);
        }
      }
    }
  }

  .text {
    position: relative;
    // max-width: 540px;
    margin: 0 auto;
    padding: is-clamp(70px, 14vw, 100px) 40px is-clamp(60px, 12vw, 70px);
    text-align: center;

    @include from(6) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @include from(7) {
      padding-left: 0;
      padding-right: 0;
    }

    .logo {
      width: 80%;
      max-width: 500px;
      max-height: 120px;
      margin: 0 auto 35px;

      img {
        margin: 0 auto;
      }
    }

    .h2,
    p {
      max-width: 540px;
      margin-left: auto;
      margin-right: auto;
    }

    p {
      margin-bottom: 30px;
    }

    .button-row {
      display: flex;
      flex-direction: column;
      align-items: center;

      @include from(12) {
        flex-direction: row;
        justify-content: center;
        align-items: center;
      }

      .btn {

        + .btn-text {

          @include to(11) {
            margin-top: 20px;
          }
        }
      }

      a {

        @include from(12) {
          margin: 0 20px;
        }
      }
    }
  }
}
