// Featured News
//
// Markup: featured-news.twig
//
// Styleguide Components.Featured News

.featured-news {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  @include component-padding;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    position: relative;
    @include grid-placement;
    @include wide-padding;
  }

  .title {
    display: flex;
    align-items: center;

    &.h3 {
      margin-bottom: 20px;
    }

    &::before {
      content: '';
      display: block;
      width: is-clamp(30px, 6vw, 50px);
      height: 3px;
      margin-right: is-clamp(15px, 3vw, 20px);
      background-color: primary-color(red);
    }
  }

  .articles-container {

    @include from(7) {
      display: flex;
      flex-wrap: wrap;
    }

    @include from(11) {
      flex-wrap: nowrap;
    }
  }

  // main article
  .feature {
    margin-bottom: 50px;

    @include from(7) {
      width: 65%;
      margin: 0;
      padding-right: 20px;
    }

    @include from(9) {
      padding-right: 30px;
    }

    @include from(11) {
      width: 44%;
      order: 1;
      padding-right: is-clamp(40px, 3.636364vw, 75px);
    }

    @include from(13) {
      width: 50%;
    }

    .feature-image {
      position: relative;
      margin-bottom: 30px;
      overflow: hidden;

      img {
        transform: scale(1);
        transition: .3s $te;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: -1px;
        right: -1px;
        display: block;
        width: 16%;
        height: 50%;
        background-image: linear-gradient(0deg, rgba(primary-color(yellow), 1) 10%, rgba(primary-color(yellow), 0) 150%);
        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        pointer-events: none;
      }
    }

    .h3 {
      @include line-height(30, 26);

      a {
        display: inline;
        border: 1px solid transparent;
        transition: $ts $te;
      }
    }

    p {
      margin-top: 20px;
    }

    @include hover {

      .h3 {

        a {
          border-bottom: 1px solid primary-color(purple);
        }
      }

      .feature-image {

        img {
          transform: scale(1.03);
        }
      }
    }
  }

  // right column
  .secondary {

    @include from(7) {
      width: 35%;
      padding-left: 20px;
    }

    @include from(9) {
      padding-left: 30px;
    }

    @include from(11) {
      width: 28%;
      order: 3;
      padding-left: is-clamp(40px, 3.636364vw, 65px);
    }

    @include from(13) {
      width: 25%;
    }

    .meta {

      span {

        @include between(4, 6) {
          display: block;
        }
      }

      .meta-author {

        @include between(4, 6) {
          margin: 0 0 5px;
        }
      }
    }
  }

  // middle column
  .tertiary {

    @include from(7) {
      width: 65%;
      margin-top: 30px;
      padding-right: 20px;
    }

    @include from(11) {
      width: 28%;
      order: 2;
      margin-top: 0;
    }

    @include from(13) {
      width: 25%;
    }
  }

  // shared teaser styles
  .eyebrow {
    margin-bottom: 5px;
  }

  .h3,
  .h5 {
    margin-bottom: 0;

    a {
      display: inline;
      border: 1px solid transparent;
      transition: $ts $te;
    }
  }

  .meta {
    margin-top: 10px;
    margin-bottom: 0;
    @include rem(font-size, 14px);
  }

  .meta-author {
    margin-right: 30px;
  }


  // secondary teasers
  .secondary-article {
    margin-bottom: 55px;
    max-width: 700px;

    @include between(4, 6) {
      display: flex;
    }

    @include from(7) {
      margin-bottom: 40px;
      padding-bottom: 40px;
      border-bottom: 1px solid rgba(#979797, .4);
    }

    &:last-child {
      margin-bottom: 25px;
      padding-bottom: 50px;
      border-bottom: 1px solid rgba(#979797, .4);

      @include from(7) {
        border-bottom: 0;
        padding-bottom: 0;
      }
    }

    .secondary-article-image {
      margin-bottom: 20px;
      overflow: hidden;

      @include from(4) {
        width: 30%;
        flex: none;
      }

      @include from(5) {
        width: 40%;
      }

      @include from(7) {
        width: auto;
      }

      img {
        transform: scale(1);
        transition: .3s $te;
      }
    }

    .secondary-article-text {

      @include from(4) {
        flex: 1 1 auto;
        padding-left: is-clamp(20px, 5vw, 30px);
      }

      @include from(5) {
        padding-top: 10px;
        padding-bottom: 10px;
      }

      @include from(7) {
        flex: none;
        padding: 0;
      }

      p {
        margin-top: 20px;
      }
    }

    .meta {

      span {

        @include between(7, 9) {
          display: block;
        }

        @include from(11) {
          display: block;
        }

        @include from(14) {
          display: inline-block;
        }
      }
    }

    .meta-author {

      @include between(7, 9) {
        margin: 0 0 5px;
      }

      @include from(11) {
        margin: 0 0 5px;
      }

      @include from(14) {
        margin: 0 30px 0 0;
      }
    }

    @include hover {

      .h5 {

        a {
          border-bottom: 1px solid primary-color(purple);
        }
      }

      .secondary-article-image {

        img {
          transform: scale(1.04);
        }
      }
    }
  }


  // basic teasers
  .tertiary-article {
    max-width: 700px;
    margin-bottom: 50px;

    &:not(:last-child) {

      @include from(11) {
        margin-bottom: 40px;
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(#979797, .4);
      }
    }

    &:last-child {

      @include from(11) {
        margin-bottom: 0;
      }
    }

    .meta {

      span {

        @include from(11) {
          display: block;
        }

        @include from(14) {
          display: inline-block;
        }
      }
    }

    .meta-author {

      @include from(11) {
        margin: 0 0 5px;
      }

      @include from(14) {
        margin: 0 30px 0 0;
      }
    }

    @include hover {

      .h5 {

        a {
          border-bottom: 1px solid primary-color(purple);
        }
      }
    }
  }

  .view-all {

    @include from(11) {
      text-align: right;
    }
  }

  &[data-type='condensed'] {

    .feature {

      .feature-image {

        @include from(5) {
          margin-bottom: 0;
        }
      }

      .feature-text {
        position: relative;

        @include from(5) {
          background: primary-color(white);
          width: 80%;
          margin-top: -75px;
          padding: 30px 30px 30px 0;
        }

        @include from(6) {
          margin-top: -120px;
        }

        @include between(7, 8) {
          margin-top: -75px;
        }

        @include between(11, 12) {
          margin-top: -75px;
        }

        @include from(11) {
          padding: 30px;
        }

        @include from(14) {
          margin-top: -155px;
        }

        p {
          @include rem(font-size, 16px);
        }
      }
    }

    .secondary-article {

      .secondary-article-text {

        p {
          @include rem(font-size, 16px);
        }
      }
    }
  }
}
