// Text Block
//
// Markup: text-block.twig
//
// Styleguide Components.Text Block

.text-block {
  @include grid-placement;
  @include component-margin;

  &[data-margin='false'] {
    margin-bottom: 0;
  }

  figure {
    display: block;
    position: relative;
    margin: 40px auto;

    &:first-child {
      margin-top: 0;
    }

    picture {
      position: relative;
    }

    figcaption {
      position: relative;
      padding: 15px 0 0;
      @include rem(font-size, 14px);
      color: #4d4d4f;
      @include line-height(20, 14);
    }

    &.align-center {

      picture {

        &::after {
          content: '';
          position: absolute;
          right: 0;
          bottom: 0;
          display: block;
          width: 15%;
          height: 65%;
          background-image: linear-gradient(24deg, rgba(primary-color(red), 1) 50%, rgba(primary-color(red), 0) 150%);
          clip-path: polygon(0% 100%, 100% 0%, 100% 100%);
          opacity: .9;
        }
      }
    }

    &.align-right,
    &.align-left {
      float: none !important; //sass-lint:disable-line no-important
      max-width: 400px;
      transition: $ts $te;

      @include from(6) {
        width: is-clamp(270px, 45vw, 330px);
      }

      img {
        margin: 0 auto;
      }
    }

    &.align-right {

      @include from(6) {
        float: right !important; //sass-lint:disable-line no-important
        margin: 0 0 30px 35px;
      }
    }

    &.align-left {

      @include from(6) {
        float: left !important; //sass-lint:disable-line no-important
        margin: 0 35px 30px 0;
      }
    }
  }

  p + {

    blockquote {

      @include from(6) {
        margin-top: 30px;
      }
    }
  }

  blockquote {
    position: relative;
    display: block;
    overflow: hidden;
    max-width: 400px;
    margin: 40px auto;
    padding: 50px 28px;
    background: primary-color(purple);
    transform-style: preserve-3d;
    transition: $ts $te;

    @include from(6) {
      float: right;
      // margin: 30px 0 30px 35px;
      margin: 8px 0 30px 35px;
      width: is-clamp(270px, 45vw, 330px);
    }

    &:first-child {
      margin-top: 0;
    }

    &.align-left {

      @include from(6) {
        float: left !important; //sass-lint:disable-line no-important
        margin: 0 35px 30px 0;
      }
    }

    span {
      font-family: $inter-b;
      @include rem(font-size, 18px);
      @include line-height(28, 18);
      color: primary-color(white);

      &:first-child {

        @include icon(quote) {
          display: block;
          margin-bottom: 10px;
          color: primary-color(white);
          @include rem(font-size, 28px);
        }
      }
    }

    &::after {
      content: '';
      position: absolute;
      z-index: -1;
      top: 10px;
      left: 50%;
      transform: translateX(-50%);
      width: 180%;
      padding-bottom: 180%;
      background-color: #6b3c68;
      border-radius: 100%;
    }
  }

  // p {
  //
  //   + blockquote {
  //     margin-top: 0;
  //   }
  // }

  .btn {
    margin-bottom: 30px;
  }
}

.simple-text-block {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  @include component-padding;
  margin: 0;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    @include grid-placement;
    @include content-constraint(700px);
  }

  .wysiwyg-content {

    h2 {
      position: relative;

      &::before {
        content: '';
        display: none;
        position: absolute;
        top: 16px;
        left: -75px;
        width: 50px;
        height: 3px;
        background-color: primary-color(red);

        @include from(10) {
          display: block;
        }
      }
    }
  }
}

// using important because of migrated content with inline styles
.wysiwyg-table {
  position: relative;
  overflow: auto;
  margin: 0 auto 40px;
  border: 1px solid primary-color(gray);

  .swipe-indicator {
    position: absolute;
    top: 100px;
    left: 50%;
    width: 126px;
    height: 126px;
    margin-left: -63px;
    z-index: 3;

    @include from(11) {
      display: none;
    }
  }

  table {
    position: relative;
    width: 100%;
    border-collapse: collapse;
    min-width: 700px;
  }

  th {
    background: primary-color(lightgray);
    text-align: left;
    border: 1px solid primary-color(white) !important;
    padding: 10px !important;
    vertical-align: top;
    font-family: $inter-b !important;
    @include rem(font-size, 14px !important);
  }

  td {
    border: 1px solid primary-color(gray) !important;
    padding: 10px !important;
    vertical-align: top;
    font-family: $inter !important;
    @include rem(font-size, 14px !important);

    p {
      font-family: $inter !important;
      @include rem(font-size, 14px !important);
      line-height: 1.5 !important;

      &:last-child {
        margin: 0 !important;
      }
    }
  }
}
