.events-calendar {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  padding: is-clamp(40px, 8vw, 80px) 0 is-clamp(60px, 12vw, 50px);
  background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);

  &[data-margin='true'] {
    @include component-margin;
  }

  .inner {
    position: relative;
    @include grid-placement;
    @include wide-padding;
  }

  header {
    max-width: 700px;
    margin: 0;

    @include from(6) {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
    }

    // since we can't put the header in the coveo markup, hacky way to align with results...
    @include from(8) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      max-width: none;
    }

    @include from(10) {
      text-align: left;
      margin-left: calc(35% + 40px);
      margin-right: 0;
    }

    @include breakpoint(1220px) {
      margin-left: 440px;
    }

    @include from(13) {
      margin-left: 460px;
    }

    @include from(15) {
      margin-left: 500px;
    }

    .h2 {

      @include from(6) {
        margin-bottom: 0;
      }
    }

    .events-calendar-nav {

      a {
        display: inline;
        @include rem(font-size, 16px);
        @include line-height(24, 16);
        color: primary-color(black);
        border-bottom: 1px solid primary-color(red);

        @include hover {
          border-color: transparent;
        }
      }
    }
  }
}
