// Grayscale Colors
//
// Colors:
// primary-color(black)           : #333 - Black
// primary-color(lightgray)    : #ededed - Light Gray
// primary-color(gray)            : #c4c4c4 - Gray
// primary-color(darkgray)     : #595959 - Dark Gray
// primary-color(white)           : #fff - White
//
// Styleguide Colors.Grayscale Colors

// Primary Colors
//
// Colors:
// primary-color(red) : #e94429 - Red
// primary-color(red, shade1) : #de3217 - Dark Red
// primary-color(purple)         : #5e2b5b - Purple
// primary-color(purple, tint1)         : #72396e - Light Purple
// primary-color(yellow)  : #fac23d - Yellow
// primary-color(green)  : #31b493 - Green
//
// Styleguide Colors.Primary Colors
$primarycolors: (
  white: (
    base: #fff
  ),
  black: (
    base: #333
  ),
  lightgray: (
    base: #ededed
  ),
  gray: (
    base: #c4c4c4
  ),
  darkgray: (
    base: #595959
  ),
  red: (
    base: #e94429,
    shade1: #de3217
  ),
  purple: (
    tint1: #72396e,
    base: #5e2b5b
  ),
  yellow: (
    base: #fac23d
  ),
  green: (
    base: #31b493
  )
);

$body-color: primary-color(black);

// Social Media Colors
//
// Colors:
// sm-color(facebook)    : #3b5998 - Facebook
// sm-color(twitter)     : #1da1f2 - Twitter
// sm-color(linkedin)    : #007bb5 - LinkedIn
// sm-color(youtube)     : #f00 - YouTube
// sm-color(instagram)   : #c32aa3 - Instagram
//
// Styleguide Colors.Social Media Colors
$socialmediacolors: (
  facebook:   #3b5998,
  twitter:    #1da1f2,
  linkedin:   #007bb5,
  youtube:    #f00,
  instagram:  #c32aa3
);
