//sass-lint:disable force-element-nesting
//sass-lint:disable single-line-per-selector
//sass-lint:disable no-important
//sass-lint:disable quotes
//sass-lint:disable force-pseudo-nesting
//sass-lint:disable nesting-depth

.CoveoSearchInterface {

  .coveo-results-header {
    margin-bottom: 30px;
    padding: 0;
    flex-wrap: wrap;

    .coveo-summary-section {
      padding-bottom: 20px;
    }
  }

  // header area
  .coveo-summary-section {
    display: block;
  }

  .CoveoQueryDuration {
    display: none !important;
  }

  .CoveoQuerySummary {
    font-family: $inter-b;
    @include rem(font-size, 26px);
    color: #000;
  }

  .coveo-sort-container {
    display: block;
    padding-bottom: 20px;
  }

  .CoveoSort {
    padding: 5px 12px;
    border: 0;
    @include rem(font-size, 16px);
    text-transform: none;
    font-weight: normal;
  }

  .CoveoSort.coveo-selected,
  .CoveoSort.coveo-selected:hover {
    border: 0;

    span:first-child {
      border-bottom: 1px solid primary-color(red);
    }

    .coveo-sort-icon-ascending,
    .coveo-sort-icon-descending {
      border: 0 !important;
    }
  }

  .CoveoBreadcrumb {
    padding: 0;
    flex-direction: column;
  }

  .coveo-breadcrumb-item {

    @include from(6) {
      display: flex;
    }
  }

  .coveo-breadcrumb-items {
    display: block;
  }

  .coveo-breadcrumb-clear-all {
    display: block;
    margin: 0 0 20px;
    @include rem(font-size, 14px);
    font-family: $inter;
    color: primary-color(purple);

    > div {
      border-bottom: 1px solid transparent;
      transition: $ts $te;
    }

    @include hover {
      text-decoration: none;

      > div {
        border-bottom: 1px solid primary-color(purple);
      }
    }
  }

  .coveo-facet-breadcrumb-values {
    display: flex;
    flex-wrap: wrap;

    @include from(6) {
      flex: 1 1 auto;
    }
  }

  .coveo-facet-breadcrumb-title {
    margin-bottom: 10px;
    display: block;
    @include rem(font-size, 16px);
    font-family: $inter;

    @include from(6) {
      min-width: 150px;
      flex: none;
      margin: 18px 20px 0 0;
      white-space: nowrap;
    }
  }

  .coveo-facet-breadcrumb-value {
    position: relative;
    z-index: 1;
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    margin: 5px 10px 10px 0;
    padding: 14px 20px;
    cursor: pointer;
    background-color: primary-color(white);
    color: primary-color(black);
    font-family: $inter-b;
    @include rem(font-size, 12px);
    @include line-height(17, 14);
    @include rem(letter-spacing, 1px);
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
    border: 2px solid primary-color(gray);
    border-radius: 100px;
    transition: background-color .2s $te 0s, color .2s $te, border-color .2s $te;

    @include hover {
      border-color: primary-color(yellow);
    }
  }

  .coveo-facet-breadcrumb-value.coveo-selected * {
    color: primary-color(darkgray);
  }

  .coveo-facet-breadcrumb-value:hover {
    text-decoration: none;
  }

  // no results
  .coveo-query-summary-no-results-string {
    margin-bottom: 20px;
    font-family: $inter-b;
    @include rem(font-size, 26px);
  }

  .coveo-query-summary-cancel-last {
    margin-bottom: 20px;
    color: primary-color(purple);
  }

  .coveo-query-summary-search-tips-info {
    margin-bottom: 10px;
    color: primary-color(darkgray);
    @include rem(font-size, 14px);
    text-transform: uppercase;
    @include rem(letter-spacing, 1px);

    + ul {
      font-family: $inter;
      color: primary-color(darkgray);
      font-weight: normal;

      li {
        margin-bottom: 10px;
      }
    }
  }

  .coveo-facet-slider-breadcrumb {
    font-family: $inter;

    .coveo-facet-slider-breadcrumb-caption {
      color: primary-color(purple);
    }
  }
}
