.hero-search {
  @extend %grid-columns;
  position: relative;
  z-index: 1;
  @include grid-placement(full-bleed);
  background-image: linear-gradient(0deg, rgba(primary-color(white), 1) 0%, rgba(#f0f0f0, 1) 100%);

  &[data-margin='true'] {
    @include component-margin;
  }

  .kss-body & {
    margin: 0;
  }

  .inner {
    @include grid-placement;
    position: relative;
    display: flex;
    justify-content: center;
    margin: 0 -20px;
    background-color: primary-color(purple);

    @include from(10) {
      margin: 0;
    }
  }

  .text {
    width: 100%;
    padding: is-clamp(40px, 3.9vw, 60px) $mobile-padding is-clamp(40px, 3.9vw, 70px);
    text-align: center;
  }

  .h1 {
    margin-bottom: is-clamp(20px, 4vw, 30px);
    color: primary-color(white);
  }

  .search-form {
    max-width: 720px;
    margin: 0 auto;
    position: relative;

    .search-form-input {
      width: 100%;
      padding-right: 70px;
      border-radius: 100px;
    }

    .search-form-submit {
      position: absolute;
      top: 0;
      right: 0;
      width: 60px;
      height: 100%;
      background-image: url('../images/search.svg');
      background-size: 21px;
      background-repeat: no-repeat;
      background-position: center;
      font-size: 0;
    }
  }

  .aota-color-bars {
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;

    .cb-4 {
      fill: #93498e;
    }
  }
}
