.gated-content-promo {

  .cta {
    padding: is-clamp(30px, 6vw, 35px) is-clamp(30px, 6vw, 45px);
    background-color: primary-color(yellow);

    @include between(8, 10) {
      display: flex;
      align-items: center;
    }

    @include from(12) {
      display: flex;
      align-items: center;
      padding: is-clamp(20px, 2.5vw, 35px) is-clamp(20px, 2.5vw, 45px);
    }
  }

  .cta-intro {
    margin-bottom: 40px;
    text-align: center;

    @include between(8, 10) {
      flex: 1 1 auto;
      padding-right: is-clamp(40px, 8vw, 100px);
      margin-bottom: 0;
      text-align: left;
    }

    @include from(12) {
      flex: 1 1 auto;
      padding-right: 40px;
      margin-bottom: 0;
      text-align: left;
    }

    @include from(16) {
      padding-right: 100px;
    }

    .title {
      margin-bottom: 12px;
    }

    // unspecified if this will be plain-text or rich text
    .text {
      &,
      p {
        @extend p;
      }
    }
  }

  .links {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    @include between(8, 10) {
      flex: none;
    }

    @include from(12) {
      flex: none;
    }

    .btn {
      margin-bottom: 12px;
    }

    a:not(.btn) {
      color: primary-color(black);
      border-bottom: 1px solid transparent;
      @include rem(font-size, 14px);
      letter-spacing: -.01em;

      @include hover {
        border-bottom: 1px solid primary-color(black);
      }
    }
  }

  .gated-content-slider-container {
    position: relative;
    background: linear-gradient(90deg, rgba(primary-color(lightgray), 1) 0%, rgba(primary-color(lightgray), 0) 100%);

    @include between(8, 10) {
      background: linear-gradient(90deg, rgba(primary-color(lightgray), 1) 0%, rgba(primary-color(lightgray), 0) 48%);
    }

    @include from(12) {
      background: linear-gradient(90deg, rgba(primary-color(lightgray), 1) 0%, rgba(primary-color(lightgray), 0) 48%);
    }

    .gated-content-slider-controls {
      position: absolute;
      bottom: 20px;
      right: is-clamp(25px, 5vw, 45px);
      display: inline-flex;

      @include between(8, 10) {
        right: calc(48% - 45px);
      }

      @include from(12) {
        right: calc(48% - 45px);
      }

      button {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px 8px;
        border-radius: $border-radius;
        background-color: transparent;
        color: primary-color(purple);

        @include hover {
          background-color: primary-color(purple);
          color: primary-color(white);
        }
      }

      .slick-arrow {

        &.slick-hidden {

          + .slick-pause {
            display: none;
          }
        }
      }
    }

    .slick-pause {
      width: 30px;
      height: 26px;
      margin: 0 2px;

      &[data-active='false'] {

        &::before {
          @include rem(font-size, 20px);
          content: '\e91d';
        }
      }
    }
  }

  .gated-content-slider {
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s $te;

    &.slick-initialized {
      opacity: 1;
      visibility: visible;
    }

    .slick-slider {
      position: static;
    }

    .slick-track {
      display: flex;
      align-items: stretch;
      justify-content: center;

      .slick-slide {
        height: auto;

        > div {
          height: 100%;
        }
      }
    }

    .slide {
      height: 100%;

      .slide-inner {
        height: 100%;

        @include between(8, 10) {
          display: flex;
        }

        @include from(12) {
          display: flex;
        }
      }

      .title {
        margin-bottom: 10px;
        color: primary-color(purple);
      }

      .text {
        padding: is-clamp(25px, 5vw, 45px) is-clamp(25px, 5vw, 45px) 75px;
        text-align: center;

        @include between(8, 10) {
          width: 52%;
          text-align: left;
        }

        @include from(12) {
          width: 52%;
          text-align: left;
        }
      }

      .image {
        position: relative;
        display: none;
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 30% 100%);

        @include between(8, 10) {
          display: block;
          width: 48%;
        }

        @include from(12) {
          display: block;
          width: 48%;
        }

        picture {
          @include absolute(0, 0, null, 0);
          height: 100%;

          img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
          }
        }
      }
    }

    .slick-dots {
      position: absolute;
      z-index: 1;
      bottom: 25px;
      left: is-clamp(25px, 5vw, 45px);
      width: auto;
      display: inline-flex;

      li {
        margin: 0 4px;

        button {
          width: 13px;
          height: 13px;
          background-color: primary-color(white);
          font-size: 0;
          border-radius: 100%;
          transition: $ts $te;

          @include hover {
            background-color: primary-color(green);
          }
        }

        &.slick-active {

          button {
            background-color: primary-color(green);
          }
        }
      }
    }
  }
}
