// Stats Panel
//
// Markup: stats-panel.twig
//
// Styleguide Components.Stats Panel
.stats-panel {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  padding: 75px 0 50px;

  @include from(8) {
    padding-bottom: 125px;
  }

  @include from(10) {
    padding-bottom: 175px;
  }

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    @include grid-placement;
    @include wide-padding;
  }

  header {
    margin-bottom: 40px;

    @include from(8) {
      max-width: 550px;
      margin: 0 auto 40px;
      text-align: center;
    }
  }

  // stats wrapper
  .stats {

    @include from(8) {
      max-width: 1235px;
      margin: 0 auto;
      display: flex;
      justify-content: space-around;
    }
  }

  // each circle area
  .stat {
    position: relative;

    &:not(:last-child) {

      @include to(7) {
        margin-bottom: is-clamp(30px, 5vw, 60px);
      }
    }

    @include from(8) {
      width: 27.5%;
      margin: 0;

      &::after {
        content: '';
        display: block;
        padding-bottom: 100%;
      }
    }

    // reworked this: removed padding and moved to margins on interior elements in order
    // to fix safari issue with responsive circles

    .stat-inner {
      padding: is-clamp(18px, 3vw, 25px) 0 0 25px;
      transform-style: preserve-3d;

      @include from(8) {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        padding: 0;
        // padding: 0 is-clamp(30px, 3.33vw, 60px);
        text-align: center;
      }

      &::before,
      &::after {
        content: '';
        position: absolute;
        z-index: -1;
        display: block;
        border-radius: 100%;
        pointer-events: none;
      }

      // creates the color gradient
      &::before {
        top: 0;
        left: 0;
        width: 68px;
        height: 68px;
        background-image: linear-gradient(180deg, rgba(primary-color(green), 1) 0%, rgba(primary-color(white), 1) 35%);
        opacity: .7;
        transform: rotate(-45deg);

        @include from(8) {
          transform: rotate(0deg);
          width: 100%;
          height: auto;
          padding-bottom: 100%;
          background-image: linear-gradient(180deg, rgba(primary-color(green), 1) 0%, rgba(primary-color(white), 0) 50%);
        }
      }

      // white circle
      &::after {
        top: 8px;
        left: 8px;
        width: 52px;
        height: 52px;
        background-color: primary-color(white);

        @include from(8) {
          top: 10px;
          left: 10px;
          width: calc(100% - 20px);
          height: auto;
          padding-bottom: calc(100% - 20px);
        }

        @include from(10) {
          top: 14px;
          left: 14px;
          width: calc(100% - 28px);
          padding-bottom: calc(100% - 28px);
        }
      }
    }

    &-value,
    &-title,
    &-subtitle {

      @include from(8) {
        margin: 0 is-clamp(30px, 3.33vw, 60px);
      }
    }

    // statistic number
    &-value {
      position: relative;
      margin-bottom: 10px;
      font-family: $inter-b;
      @include font-clamp(rem(47px), 7.83vw, rem(50px));
      @include line-height(54, 47);
      color: primary-color(black);

      @include from(8) {
        margin-bottom: is-clamp(10px, 1.11vw, 45px);
        @include font-clamp(rem(40px), 4.44vw, rem(76px));
        line-height: 1;
      }
    }

    &-title {
      margin-bottom: 10px;
      @include rem(font-size, 20px);
      @include line-height(25, 20);

      @include from(8) {
        @include font-clamp(rem(16px), 1.77vw, rem(25px));
      }
    }

    &-subtitle {
      @include rem(font-size, 14px);

      @include from(8) {
        @include font-clamp(rem(13px), 1.44vw, rem(18px));
      }
    }

    // larger scaled stat (yellow)
    &:nth-child(2) {

      @include from(8) {
        transform: scale(1.2);
        transform-origin: top;
      }

      .stat-value {

        @include from(8) {
          margin-left: is-clamp(30px, 3.33vw, 75px);
          margin-right: is-clamp(30px, 3.33vw, 75px);
        }
      }

      .stat-title {

        @include from(8) {
          margin-left: is-clamp(30px, 3.33vw, 75px);
          margin-right: is-clamp(30px, 3.33vw, 75px);
          @include font-clamp(rem(16px), 1.77vw, rem(23px));
        }
      }

      .stat-subtitle {

        @include from(8) {
          margin-left: is-clamp(30px, 3.33vw, 75px);
          margin-right: is-clamp(30px, 3.33vw, 75px);
          @include font-clamp(rem(13px), 1.44vw, rem(16px));
        }
      }

      .stat-inner {

        @include from(8) {
          margin-top: 30px;
          // padding: 0 is-clamp(30px, 3.33vw, 75px);
        }

        @include from(10) {
          margin-top: 60px;
        }

        &::before {
          background-image: linear-gradient(180deg, rgba(primary-color(yellow), 1) 0%, rgba(primary-color(white), 1) 35%);

          @include from(8) {
            background-image: linear-gradient(180deg, rgba(primary-color(yellow), 1) 0%, rgba(primary-color(white), 0) 65%);
          }
        }
      }
    }

    // purple
    &:nth-child(3) {

      .stat-inner {

        &::before {
          background-image: linear-gradient(180deg, rgba(primary-color(purple), 1) 0%, rgba(primary-color(white), 1) 35%);

          @include from(8) {
            background-image: linear-gradient(180deg, rgba(primary-color(purple), 1) 0%, rgba(primary-color(white), 0) 50%);
          }
        }
      }
    }
  }
}
