.hero-event {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .08);

  &[data-margin='true'] {
    @include component-margin;
  }

  .inner {
    position: relative;
    overflow: hidden;
    @include grid-placement(full-bleed);

    @include between(6, 9) {
      padding-top: is-clamp(140px, 23.33vw, 220px);
    }
  }

  .background-image {

    picture {

      @include from(6) {
        @include absolute(0, 0, null, 0);
        z-index: 0;
        height: 50%;
      }

      @include from(10) {
        height: 100%;
      }

      img {

        @include from(6) {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
        }
      }

      &::after {

        @include from(6) {
          content: '';
          display: block;
          position: absolute;
          z-index: 1;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(primary-color(black), .2);
        }

        @include from(10) {
          background-color: rgba(primary-color(black), .3);
        }
      }
    }
  }

  .inner-content {
    position: relative;
    @include grid-placement;
  }

  .content {
    position: relative;
    max-width: 1300px;
    padding: 0 is-clamp(40px, 8vw, 70px);
    transform-style: preserve-3d;

    @include from(10) {
      width: 740px;
      margin: 0 auto;
      padding: 0;
      background-color: transparent;
    }

    &::before {
      @include pseudo-abs($width: 150%);
      left: 50%;
      transform: translateX(-50%);
      background-color: primary-color(white);

      @include from(6) {
        border-top-left-radius: 150% * 2;
        border-top-right-radius: 150% * 2;
      }

      @include from(10) {
        display: none;
      }
    }

    &::after {
      display: none;

      @include between(6, 9) {
        display: block;
        @include pseudo-abs($width: 150%);
        z-index: -1;
        top: -30px;
        left: 50%;
        transform: translateX(-50%);
        background-image: linear-gradient(90deg, rgba(#f7b03b, 0) 50%, rgba(#f7b03b, 1) 90%);
        border-top-left-radius: 150% * 2;
        border-top-right-radius: 150% * 2;
      }
    }

    .circle {
      display: none;

      @include from(10) {
        display: block;
        position: absolute;
        z-index: -1;
        top: calc(50% - 368px);
        left: 0;
        width: 100%;
        padding-bottom: 100%;
        background-image: linear-gradient(90deg, rgba(#ea4927, 1) 0%, rgba(#ea4927, 0) 20%, rgba(#f7b03b, 0) 80%, rgba(#f7b03b, 1) 100%);
        border-radius: 100%;
        transform: rotate(220deg);
      }

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 24px;
        left: 24px;
        width: calc(100% - 48px);
        height: calc(100% - 48px);
        background-color: primary-color(white);
        border-radius: 100%;
      }
    }
  }

  .text {
    position: relative;
    max-width: 540px;
    margin: 0 auto;
    padding: is-clamp(40px, 8vw, 100px) 0;
    text-align: center;

    @include between(6, 9) {
      min-height: 230px;
    }

    .eyebrow {
      margin-bottom: 14px;
      @include font-clamp(rem(12px), 2.4vw, rem(20px));
    }

    .subtitle {
      margin: -8px 0 25px;
      font-family: $inter-b;
      @include font-clamp(rem(16px), 3.2vw, rem(22px));
      @include line-height(30, 22);
      color: primary-color(black);
    }

    p {
      margin-bottom: 30px;
      color: #4d4d4f;
    }

    .button-row {
      display: flex;
      flex-direction: column;
      align-items: center;

      .btn-text {
        margin-top: 30px;
      }
    }
  }
}
