// Related Content
//
// Markup: related-content.twig
//
// Styleguide Components.Related Content
.related-content {
  margin-bottom: 65px;

  .item {
    padding-bottom: 30px;
    border-bottom: 1px solid rgba($hr-color, .4);

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    &.js-link-event {

      @include hover {

        .h5 {

          a {
            border-bottom: 1px solid primary-color(purple);
          }
        }
      }
    }
  }

  .eyebrow {
    margin-bottom: 5px;
  }

  .h5 {
    margin-bottom: 10px;

    a {
      display: inline;
      border-bottom: 1px solid transparent;
      transition: $ts $te;
    }
  }

  .meta {
    margin-bottom: 20px;
    @include rem(font-size, 14px);
  }

  .meta-author {
    margin-right: 30px;
  }

  p {
    @include rem(font-size, 16px);
    @include line-height(24, 16);
  }
}
