// sass-lint:disable-all
@media print {

  @page {
    size: letter;
    margin: .25in auto; // or 0 auto
  }

  div,
  article,
  main {
    display: inline-block !important;
  }

  h1,
  .h1,
  h2,
  .h2,
  h3,
  .h3,
  h4,
  .h4,
  h5,
  .h5,
  h6,
  .h6,
  p,
  tr,
  blockquote {
    display: block;
    color: primary-color(gray) !important;
    border: 0 !important;
  }

  picture {
    page-break-inside: avoid;
  }

  button,
  .btn,
  input[type='submit'],
  input[type='reset'],
  input[type='button'] {
    color: primary-color(gray);
    border-color: primary-color(gray);
  }

  img {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    page-break-inside: avoid;
  }

  .accordion-content,
  .content-expander-content {
    display: block !important;

    p {
      page-break-inside: avoid;
    }
  }

  .accordion-tabs-content {
    opacity: 1 !important;
    visibility: visible !important;
    height: auto !important;
  }

  .accordion-tabs-content-inner {
    display: block !important;
  }

  .aota-color-bars,
  .utility-nav,
  .site-header-nav-container,
  .site-header-menu-dropdown,
  .site-header-member-btn,
  .mobile-nav-toggles,
  .mobile-menu,
  .skiplink,
  .breadcrumb,
  .article-detail-sidebar,
  .tags,
  .accordion-trigger-icon,
  .content-expander-btn,
  .alert-banner,
  .scrolling-banner,
  .mobile-explore-menu,
  #CookiebotWidget {
    display: none !important;
  }

  .site-header {
    box-shadow: none;
  }

  .site-footer {

    .top {
      display: none !important;
    }

    .site-footer-nav {
      display: none !important;
    }
  }

  .article-detail-body,
  .text-block {
    display: block !important;
  }

}
