.mobile-explore-menu {
  @include grid-placement(full-bleed);
  position: relative;
  z-index: 10;
  border-bottom: 1px solid primary-color(lightgray);

  @include from(10) {
    display: none;
  }

  &-toggle {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 18px 20px 12px;
    font-family: $inter-b;
    @include rem(font-size, 12px);
    @include line-height(15, 12);
    @include rem(letter-spacing, 2px);
    text-transform: uppercase;
    color: primary-color(darkgray);
    text-align: left;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
    transition: $ts $te;
    @include icon(caret-down, after) {
      margin-left: 8px;
      @include rem(font-size, 8px);
      transition: $ts $te;
    }

    &[aria-expanded='true'] {
      @include icon(caret-up, after, null);
      box-shadow: none;
    }

    @include hover {

      &::after {
        color: primary-color(red);
      }
    }
  }

  &-nav {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 15px 20px 35px;
    background-color: primary-color(white);
    border-bottom: 6px solid primary-color(red);
    box-shadow: 0 4px 4px rgba(0, 0, 0, .25);

    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 73px;
      background: linear-gradient(180deg, rgba(255, 251, 251, 0) 35.71%, #fffbfb 100%);
      pointer-events: none;
    }
  }

  &-header {
    margin-bottom: 10px;
    font-family: $inter-b;
    @include rem(font-size, 21px);
    color: primary-color(purple);

    a {
      color: primary-color(purple);
      border-bottom: 2px solid transparent;

      @include hover {
        border-color: primary-color(purple);
      }
    }
  }

  &-item-link {
    display: block;
    padding: 7px 0;
    font-family: $inter;
    @include rem(font-size, 15px);
    @include line-height(18, 15);
    color: primary-color(black);

    span {
      padding: 0;
    }

    &[data-active='true'] {
      font-family: $inter-b;
    }

    @include hover {
      color: primary-color(black);
      text-decoration: underline;
    }
  }

  &-children {
    position: relative;
    margin: 4px 0;
    padding-left: 20px;

    &::before {
      content: '';
      position: absolute;
      top: 7px;
      left: 0;
      display: block;
      width: 2px;
      height: calc(100% - 14px);
      background-color: primary-color(red);
    }
  }
}
