// Email Signup
//
// .-modifier - Modifier description
//
// Markup: email-signup.twig
//
// Styleguide Components.Email Signup
.email-signup {
  $_bp-desktop: 8;
  $_bp-desktop-lg: 12;
  $_gap-desktop: 50px;
  $_intro-minwidth-desktop: 400px;

  @include grid-placement;
  @include component-padding;

  .inner {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    background: primary-color(purple);
    padding: $_gap-desktop 0;
    row-gap: $_gap-desktop; // sass-lint:disable-line no-misspelled-properties

    &::before {
      @include absolute(0, null, null, 0);
      content: '';
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(120deg, rgba(primary-color(white), 0) 20%, rgba(primary-color(white), .15));
      clip-path: polygon(0 0, 100% 0, 0 100%, 0% 100%);
      pointer-events: none;
    }

    @include from($_bp-desktop) {
      &::before {
        width: max(calc(#{$_intro-minwidth-desktop} + (#{$_gap-desktop} / 2)), calc(30% + (#{$_gap-desktop} / 2)));
        clip-path: polygon(0 0, 100% 0, calc(100% - #{$_gap-desktop}) 100%, 0% 100%);
      }
    }
  }

  .formstack-wrap {
    padding: 0 $_gap-desktop;
    width: 100%;
    align-self: center;

    @include from($_bp-desktop) {
      width: 70%;
      max-width: calc(100% - #{$_intro-minwidth-desktop});
      // padding: 0 $_gap-desktop;
    }
  }

  .intro {
    position: relative;
    padding: 0 $_gap-desktop;

    @include from($_bp-desktop) {
      width: 30%;
      min-width: $_intro-minwidth-desktop;
    }

    .title,
    .description {
      color: primary-color(white);
    }
  }

  // FORMSTACK STYLES
  // NOTE: even with "Use No CSS" embed script
  // Formstack will minimally include an external stylesheet
  // and sometimes even inline styles
  .fsBody {
    padding: 0 !important;
    font-family: $inter;

    .fsForm {
      background: transparent;
      display: flex;
      flex-flow: column;
      align-items: center;
      column-gap: 16px; // sass-lint:disable-line no-misspelled-properties
      max-width: unset;
      width: 100%;
      margin: 0 !important;

      @include from($_bp-desktop-lg) {
        flex-flow: row wrap;
      }

      &,
      .fsSubmit {
        padding: 0 !important;
      }

      .fsSubmit {
        flex: none;
        margin: 23px 0 0 !important;
      }
    }

    .fsPage {
      flex: 3;
    }

    .fs3Col {
      min-width: unset;
    }

    .fsRowBody,
    .fsFieldRow {
      margin-bottom: 0 !important;

      input::placeholder {
        color: primary-color(gray);
      }
    }

    .fsFieldRow {
      display: flex;
      flex-flow: row wrap;
      gap: 16px; // sass-lint:disable-line no-misspelled-properties
    }

    .fsFieldCell {
      &,
      &.fsSpan33 {
        width: unset;
        flex: 1 0 180px;
      }
    }

    .fsLabel,
    label {
      font-size: .75rem !important;
      color: primary-color(white);

      &::after {
        display: none;
      }
    }

    .fsRequiredMarker {
      color: primary-color(yellow) !important;
      line-height: 1;
    }

    .clear,
    .fs-clear {
      display: none;
    }

    .fsSubmitButton {
      background-color: primary-color(yellow) !important;
      color: primary-color(darkgray) !important;
    }

    div.fsError {
      width: 100%;
      background-color: lighten(primary-color(red), 35%);
      color: primary-color(red);
      font-weight: bold;
      border: 0;
      text-align: left;
      padding: 20px;
      line-height: 1.6;
    }

    .fsValidationError {
      background-color: transparent;
      box-shadow: none;

      .fsLabel {
        background-image: none;
        padding-left: 0;
        padding-right: 26px;
        position: relative;

        &::after {
          @include absolute(2px, 0, null, null);
          content: '!';
          font-weight: bold;
          width: 16px;
          height: 16px;
          font-size: 12px;
          display: inline-flex;
          justify-content: center;
          z-index: 2;
          color: primary-color(white);
        }

        &::before {
          @include absolute(null, 0, null, null);
          content: ' ';
          background: primary-color(red);
          padding: 2px;
          border-radius: 50%;
          width: 16px;
          height: 16px;
          display: inline-flex;
          justify-content: center;
          z-index: 1;
        }
      }
    }
  }
}
