// Image Grid
//
// Markup: image-grid.twig
//
// Styleguide Components.Image Grid
.image-grid {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  @include component-padding;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  &::before,
  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 150px;
    height: 240px;
    transition: $ts $te;

    @include between(10, 12) {
      height: 180px;
      width: 90px;
    }
  }

  &::before {
    background-image: linear-gradient(0deg, rgba(primary-color(yellow), 1) 0%, rgba(primary-color(yellow), 0) 100%);
    clip-path: polygon(0 0, calc(0% + 30px) 0%, 100% 100%, 0% 100%);

    @include between(10, 12) {
      clip-path: polygon(0 0, calc(0% + 15px) 0%, 100% 100%, 0% 100%);
    }
  }

  &::after {
    width: 120px;
    background-color: primary-color(green);
    clip-path: polygon(0 0, 0% 100%, 100% 100%);

    @include between(10, 12) {
      width: 75px;
    }
  }

  .inner {
    @include grid-placement;
    overflow: hidden;
    max-width: 1400px;
    margin: 0 auto;
  }

  header {
    max-width: 700px;
    margin: 0 0 is-clamp(40px, 8vw, 70px);

    @include from(8) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }

    p {
      max-width: 500px;

      @include from(8) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .button-row {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-top: 30px;

      @include from(8) {
        flex-direction: row;
        justify-content: center;
        align-items: center;
      }

      .btn {

        + .btn-text {

          @include to(7) {
            margin-top: 20px;
          }
        }
      }

      a {

        @include from(8) {
          margin: 0 20px;
        }
      }
    }
  }

  // global styles for all layouts
  .items {
    position: relative;
    z-index: 1;

    .item {
      margin-bottom: 40px;

      .item-image {
        overflow: hidden;

        img {
          transition: .3s $te;
        }
      }

      .item-text {
        padding: is-clamp(20px, 4vw, 40px);
        background-color: primary-color(white);
      }

      .eyebrow {
        margin-bottom: 5px;
      }

      .h5 {
        margin-bottom: 10px;

        a {
          display: inline;
          border-bottom: 1px solid transparent;
          transition: $ts $te;
        }
      }

      .meta {
        margin-bottom: 20px;
        @include rem(font-size, 14px);
      }

      .meta-author {
        margin-right: 30px;
      }

      p {
        @include rem(font-size, 16px);
        @include line-height(24, 16);
      }

      &.js-link-event {

        @include hover {

          .item-image {

            img {
              transform: scale(1.05);
            }
          }

          .h5 {

            a {
              border-bottom: 1px solid primary-color(purple);
            }
          }
        }
      }
    }
  }

  &[data-layout='default'] {

    .items {
      max-width: 800px;
      margin: 0 auto;

      @include from(10) {
        max-width: none;
        display: flex;
        flex-wrap: wrap;
      }
    }

    .item {

      @include from(5) {
        display: flex;
        align-items: flex-start;
      }

      @include from(10) {
        width: 50%;
        margin-bottom: is-clamp(40px, 3.9vw, 70px);
      }

      &:nth-child(odd) {

        @include from(10) {
          padding-right: is-clamp(10px, 1.953vw, 50px);
        }
      }

      &:nth-child(even) {

        @include from(10) {
          padding-left: is-clamp(10px, 1.953vw, 50px);
        }
      }

      .item-image {

        @include from(5) {
          flex: none;
          width: is-clamp(160px, 32vw, 232px);
        }

        @include from(10) {
          width: is-clamp(140px, 13.67vw, 232px);
        }
      }

      .item-text {

        @include from(5) {
          flex: 1 1 auto;
          padding: 0 0 0 is-clamp(20px, 4vw, 40px);
          background-color: transparent;
        }

        @include from(10) {
          padding: 0 0 0 is-clamp(20px, 1.95vw, 40px);
        }
      }
    }
  }

  &[data-layout='2'] {

    .items {

      @include from(6) {
        display: flex;
        margin: 0 -10px;
      }

      @include from(8) {
        margin: 0 -20px;
      }

      @include from(10) {
        margin: 0 -30px;
      }

      @include from(12) {
        margin: 0 -50px;
      }
    }

    .item {

      @include from(6) {
        width: calc(50% - 20px);
        margin: 0 10px;
      }

      @include from(8) {
        width: calc(50% - 40px);
        margin: 0 20px;
      }

      @include from(10) {
        width: calc(50% - 60px);
        margin: 0 30px;
      }

      @include from(12) {
        width: calc(50% - 100px);
        margin: 0 50px;
      }
    }
  }

  &[data-layout='3'] {

    .items {

      @include from(5) {
        display: flex;
        flex-wrap: wrap;
      }

      @include from(9) {
        flex-wrap: nowrap;
        margin: 0 -20px;
      }

      @include from(12) {
        margin: 0 -30px;
      }

      @include from(16) {
        margin: 0 -50px;
      }
    }

    .item {

      @include between(5, 8) {
        display: flex;
        align-items: flex-start;
      }

      @include from(9) {
        width: calc(33.33% - 40px);
        margin: 0 20px;
      }

      @include from(12) {
        width: calc(33.33% - 60px);
        margin: 0 30px;
      }

      @include from(16) {
        width: calc(33.33% - 100px);
        margin: 0 50px;
      }

      .item-image {

        @include between(5, 8) {
          flex: none;
          width: is-clamp(160px, 32vw, 232px);
        }
      }

      .item-text {

        @include between(5, 8) {
          flex: 1 1 auto;
          padding: 0 0 0 is-clamp(20px, 4vw, 40px);
          background-color: transparent;
        }

        @include from(9) {
          padding: is-clamp(20px, 2.22vw, 40px) is-clamp(10px, 1.66vw, 40px);
        }
      }
    }
  }

  &[data-layout='4'] {

    .items {

      @include from(5) {
        display: flex;
        flex-wrap: wrap;
      }

      @include from(9) {
        margin: 0 -20px;
      }

      @include from(12) {
        margin: 0 -30px;
      }

      @include from(16) {
        margin: 0 -35px;
      }
    }

    .item {

      @include between(5, 11) {
        display: flex;
        align-items: flex-start;
      }

      @include from(9) {
        width: calc(50% - 40px);
        margin: 0 20px 40px;
      }

      @include from(12) {
        width: calc(25% - 60px);
        margin: 0 30px;
      }

      @include from(16) {
        width: calc(25% - 70px);
        margin: 0 35px;
      }

      .item-image {

        @include between(5, 11) {
          flex: none;
          width: is-clamp(160px, 32vw, 232px);
        }

        @include from(9) {
          width: is-clamp(140px, 15.55vw, 232px);
        }

        @include from(12) {
          width: 100%;
        }
      }

      .item-text {

        @include between(5, 11) {
          flex: 1 1 auto;
          padding: 0 0 0 is-clamp(20px, 4vw, 40px);
          background-color: transparent;
        }

        @include from(9) {
          padding-left: is-clamp(20px, 2.22vw, 40px);
        }

        @include from(12) {
          padding: is-clamp(20px, 1.66vw, 40px);
        }
      }
    }
  }
}
