// Rail Promo
//
// Markup: rail-promo.twig
//
// Styleguide Components.Rail Promo

$colors: yellow, purple;

.rail-promo {
  @include grid-placement;
  max-width: 500px;
  background: linear-gradient(
    180deg, rgba(237, 237, 237, 1) 0,
    rgba(237, 237, 237, 0) 100%
  );

  &.-vertical {
    background: linear-gradient(180deg, rgba(236, 236, 236, 1) 0%, rgba(255, 255, 255, 1) 85%);
  }

  .header {
    padding: 0 50px;
    @each $color in $colors {
      &[data-color='#{"" + $color}'] {
        background-color: primary-color($color);
      }
    }

    .h3 {
      line-height: 80px;

      &.white-text {
        color: white;
      }
    }
  }

  .image {
    width: 100%;
    height: max-content;

    img {
      margin: 0 auto;
    }
  }

  .content {
    padding: 50px 50px 56px;

    .wysiwyg-content {
      margin-bottom: 36px;
    }

    .button {
      margin-bottom: 23px;
    }
  }

}
