// Fifty Fifty-Square
//
// Markup: fifty-fifty-square.twig
//
// Styleguide Components.Fifty Fifty-Square
.fifty-fifty-square {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  padding: is-clamp(40px, 5vw, 80px) 0;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='true'] {
    padding: is-clamp(40px, 5vw, 115px) 0;
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);

    @include from(9) {
      background-image: url('../images/bg-fifty-fifty-square-right.svg');
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: top right;
    }
  }

  .inner {
    position: relative;
    z-index: 1;
    @include grid-placement;
    @include wide-padding;

    @include from(9) {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: flex-start;
    }
  }

  .image {
    position: relative;
    max-width: 500px;
    margin: 0 auto 40px;

    @include from(9) {
      width: 50%;
      max-width: 650px;
      margin: 0;
    }

    picture {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      max-width: 650px;
      margin: 0 auto;
      transform-style: preserve-3d;

      img {
        position: relative;
      }

      &::before,
      &::after {
        content: '';
        position: absolute;
        z-index: 1;
        display: block;
        width: 16%;
        height: 50%;
        pointer-events: none;
        transition: 1s $te;
      }

      &::before {
        top: 0;
        left: -100%;
        background-image: linear-gradient(0deg, rgba(primary-color(green), 1) 70%, rgba(primary-color(green), 0) 150%);
        clip-path: polygon(0 0, 0% 100%, 100% 0);
      }

      &::after {
        bottom: 0;
        right: -100%;
        background-image: linear-gradient(180deg, rgba(primary-color(yellow), 1) 20%, rgba(primary-color(yellow), 0) 150%);
        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        opacity: .9;
      }
    }

    &.-animated {

      picture {

        &::before {
          left: 0;
        }

        &::after {
          right: 0;
        }
      }
    }

    .video-link {
      position: absolute;
      left: 0;
      bottom: 0;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 100%;
      height: 100%;
      cursor: pointer;

      > span {
        display: flex;
        align-items: center;
        padding: is-clamp(15px, 3vw, 30px);
        font-family: $inter-b;
        @include font-clamp(rem(18px), 3.6vw, rem(22px));
        @include line-height(38, 22);
        color: primary-color(white);
        transform-style: preserve-3d;
        pointer-events: none;

        @include icon(play) {
          margin-right: is-clamp(10px, 2vw, 20px);
          color: primary-color(white);
          @include font-clamp(rem(30px), 6vw, rem(48px));
          transition: $ts $te;
        }

        &::after {
          content: '';
          position: absolute;
          z-index: -1;
          bottom: -1px;
          left: 0;
          display: block;
          width: 100%;
          height: 100%;
          background-image: linear-gradient(180deg, rgba(primary-color(black), 0) 0%, primary-color(black) 75%);
        }
      }

      @include hover {

        > span {

          &::before {
            color: primary-color(yellow);
          }
        }
      }
    }
  }

  .text {
    max-width: $mobile-max-width;
    margin: 0 auto;

    @include from(9) {
      width: 50%;
      max-width: none;
      margin: 0;
      padding: 40px is-clamp(50px, 5.55vw, 100px) 0 0;
      text-align: left;
    }

    @include from(16) {
      padding-right: 175px;
    }

    header {
      margin-bottom: 30px;

      .h2 {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          top: is-clamp(15px, 3vw, 20px);
          left: -70px;
          display: none;
          width: 52px;
          height: 3px;
          background-color: primary-color(red);

          @include between(8, 8) {
            display: block;
          }

          @include from(14) {
            display: block;
            left: -65px;
          }

          @include from(16) {
            left: -77px;
          }
        }
      }
    }

    .btn {
      margin-top: 30px;
    }

    .link-list {
      margin: 30px 0 0;

      li {

        & + li {
          margin-top: is-clamp(15px, 1.66vw, 25px);
        }
      }
    }
  }

  &[data-image-position='left'] {

    &[data-gradient='true'] {
      background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);

      @include from(9) {
        background-image: url('../images/bg-fifty-fifty-square-left.svg');
      }
    }

    .inner {

      @include from(9) {
        flex-direction: row;
      }
    }

    .text {

      @include from(9) {
        padding-right: 0;
        padding-left: is-clamp(50px, 5.55vw, 100px);
      }

      @include from(16) {
        padding-right: 75px;
      }
    }
  }
}
