.hero-image {
  @extend %grid-columns;
  position: relative;
  z-index: 1;
  @include grid-placement(full-bleed);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .08);

  &[data-margin='true'] {
    @include component-margin;
  }

  .kss-body & {
    margin: 0;
  }

  .inner {
    @include grid-placement(full-bleed);
    display: flex;
    flex-direction: column;

    @include from(10) {
      @include grid-placement(right-bleed);
      flex-direction: row-reverse;
      align-items: center;
      height: 100%; // for safari
    }
  }

  .photo {
    position: relative;
    overflow: hidden;
    min-height: is-clamp(255px, 51vw, 385px);

    @include from(10) {
      flex: 1 1 auto;
      height: 100%;
      min-height: is-clamp(385px, 37.59vw, 485px);
    }

    picture {
      @include absolute(0, 0, null, 0);
      height: 100%;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }

      &::after {
        display: none;

        @include from(10) {
          @include pseudo-abs($width: calc(10%), $height: 33%);
          top: 0;
          right: -1px;
          background-color: primary-color(red);
          clip-path: polygon(100% 0, 0 0, 100% 100%);
          transition: $ts $te;
        }

        @include from(12) {
          width: 16%;
          height: 50%;
        }

        @include from(13) {
          width: 12%;
          height: 33%;
        }
      }
    }

    &::before {
      @include pseudo-abs($width: calc(100% + 2px), $height: 110px);
      z-index: 1;
      left: -1px;
      bottom: 0;
      background-image: linear-gradient(272.82deg, primary-color(yellow) 29.88%, rgba(primary-color(yellow), 0) 97.03%);
      clip-path: polygon(0% 0%, 100% 60px, 100% 100%, 0% 100%);
      backface-visibility: hidden;
      transition: width $ts $te;

      @include from(7) {
        background-image: linear-gradient(272.82deg, primary-color(yellow) 0%, rgba(primary-color(yellow), 0) 80%);
      }

      @include from(10) {
        width: 160px;
        height: calc(100% + 2px);
        background-image: linear-gradient(0deg, primary-color(yellow) 0%, rgba(primary-color(yellow), 0) 50%);
        clip-path: polygon(0% 0%, 60px 0%, 100% 100%, 0% 100%);
      }

      @include from(12) {
        width: 260px;
      }

      @include from(13) {
        width: 365px;
      }
    }

    &::after {
      @include pseudo-abs($width: calc(100% + 2px), $height: 60px);
      z-index: 2;
      left: -1px;
      bottom: -1px;
      background-color: primary-color(white);
      clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
      backface-visibility: hidden;
      transition: width $ts $te;

      @include from(10) {
        width: 100px;
        height: calc(100% + 2px);
      }

      @include from(12) {
        width: 200px;
      }

      @include from(13) {
        width: 305px;
      }
    }
  }

  .text {
    padding: 0 $mobile-padding 60px;

    @include from(10) {
      width: 46%;
      max-width: 760px;
      padding: is-clamp(40px, 3.9vw, 80px) $desktop-padding is-clamp(40px, 3.9vw, 80px) 0;
    }

    @include from(14) {
      padding-left: $wide-padding;
      padding-right: 75px;
    }
  }

  .eyebrow {
    margin-bottom: 14px;
    @include font-clamp(rem(12px), 2.4vw, rem(20px));
  }

  p {
    color: #4d4d4f;
  }

  .button-row {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 35px;

    @include from(12) {
      flex-direction: row;
      align-items: center;
    }

    .btn {

      @include from(12) {
        flex: none;
      }

      + .btn-text {
        margin: 35px 0 0;

        @include from(12) {
          flex: 1 1 auto;
          margin: 0 0 0 35px;
        }
      }
    }
  }

  &[data-subpages='true'] {

    .photo {
      display: flex;
      align-items: center;

      picture {

        &::after {

          @include from(12) {
            width: 10%;
            height: 20%;
          }

          @include from(13) {
            width: 12%;
            height: 33%;
          }
        }
      }

      &::before {

        @include from(12) {
          width: calc(63% + 60px);
          clip-path: polygon(0% 0%, calc(100% - 295px) 0%, 100% 100%, 0% 100%);
        }
      }

      &::after {

        @include from(12) {
          width: 63%;
          clip-path: polygon(0% 0%, calc(100% - 295px) 0%, 100% 100%, 0% 100%);
        }
      }
    }

    picture {

      img {

        @include from(12) {
          left: 15%;
        }
      }
    }

    .subpage-menu {
      display: none;
      position: relative;
      z-index: 3;
      max-width: 475px;

      @include from(12) {
        display: block;
        margin: 0 auto;
      }

      @include from(13) {
        margin: 0 0 0 is-clamp(60px, 4.6vw, 120px);
      }

      > ul {
        display: flex;
        flex-wrap: wrap;
        margin-top: 15px;

        > li {
          margin: 10px 15px 10px 0;
        }
      }

      &-label {
        display: inline-flex;
        align-items: center;
        padding: 0;
        font-family: $inter-b;
        @include rem(font-size, 12px);
        @include line-height(15, 12);
        @include rem(letter-spacing, 2px);
        text-transform: uppercase;
        color: primary-color(black);
        background-color: rgba(primary-color(white), .75);

        &::before {
          content: '';
          display: block;
          width: 50px;
          height: 2px;
          margin-right: 24px;
          background-color: primary-color(red);
        }
      }

      &-link {
        @include rem(font-size, 12px);
      }
    }
  }
}
