// Headers
//
// Wrapper:
// max-width: 1200px;
//
// Markup:
// <div class="wysiwyg-content">
//   <h1>Header 1 (60px) - Lorem Ipsum Solor Domet</h1>
//   <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore</p>
//   <h2>Header 2 (48px) - Lorem Ipsum Solor Domet</h2>
//   <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore</p>
//   <h3>Header 3 (36px) - Lorem Ipsum Solor Domet</h3>
//   <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore</p>
//   <h4>Header 4 (28px) - Lorem Ipsum Solor Domet</h4>
//   <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore</p>
//   <h5>Header 5 (20px) - Lorem Ipsum Solor Domet</h5>
//   <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore</p>
//   <h6>Header 6 (18px) - Lorem Ipsum Solor Domet</h6>
//   <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore</p>
// </div>
//
// Weight: -99
//
// Styleguide Typography.Headers
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: primary-color(black);
  font-family: $inter-b;
  margin: 0 0 20px;

  &:last-child {
    margin-bottom: 0;
  }

  a {
    text-decoration: none;
  }
}

/////////////////////////////////////////////////////////////
// NOTE: Adding .wysiwyg-content below is to handle
//       headers in a WYSIWYG.
//
//       For Drupal projects, also add .mce-content-body to
//       add styles in the backend WYSIWYG.
/////////////////////////////////////////////////////////////
.wysiwyg-content h1,
.wysiwyg-content h2,
.wysiwyg-content h3,
.wysiwyg-content h4,
.wysiwyg-content h5,
.wysiwyg-content h6 {
  margin-top: 45px;

  &:first-child {
    margin-top: 0;
  }
}

.wysiwyg-content h1,
.h1 {
  @include font-clamp(rem(40px), 8vw, rem(54px));
  @include line-height(46, 40);
}

.h2 {
  @include font-clamp(rem(28px), 5.6vw, rem(40px));
  @include line-height(46, 40);
}

.h3 {
  @include font-clamp(rem(22px), 4.4vw, rem(26px));
  @include line-height(36, 26);

  &.-caps {
    @include font-clamp(rem(22px), 4.4vw, rem(30px));
  }
}

.wysiwyg-content h4,
.h4 {
  @include font-clamp(rem(20px), 4vw, rem(22px));
  @include line-height(30, 22);
}

.h5 {
  @include font-clamp(rem(18px), 3.6vw, rem(18px));
  @include line-height(24, 18);
}

.h6 {
  position: relative;
  padding-left: 70px;
  font-family: $inter;
  @include rem(font-size, 16px);
  @include line-height(20, 16);
  text-transform: uppercase;
  @include rem(letter-spacing, 2px);
  // @include font-clamp(rem(16px), 3.2vw, rem(16px));
  // @include line-height(28, 18);

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 9px;
    left: 0;
    width: 50px;
    height: 2px;
    background-color: primary-color(red);
  }
}

.wysiwyg-content h2 {
  @include font-clamp(rem(22px), 4.4vw, rem(26px));
  @include line-height(36, 26);

  &.-large {
    @include font-clamp(rem(32px), 6.4vw, rem(40px));
    @include line-height(46, 40);
  }
}

.wysiwyg-content h3 {
  @include font-clamp(rem(20px), 4vw, rem(22px));
  @include line-height(30, 22);
}

.wysiwyg-content h4 {
  @include font-clamp(rem(18px), 3.6vw, rem(20px));
  @include line-height(30, 20);
}

.wysiwyg-content h5 {
  @include font-clamp(rem(17px), 3.4vw, rem(18px));
  @include line-height(30, 18);
}

.wysiwyg-content h6 {
  @include font-clamp(rem(16px), 3.2vw, rem(16px));
  @include line-height(30, 18);
  text-transform: uppercase;
}

.eyebrow {
  margin-bottom: 15px;
  font-family: $inter-b;
  @include rem(font-size, 12px);
  @include line-height(15, 12);
  @include rem(letter-spacing, 2px);
  text-transform: uppercase;
  color: primary-color(darkgray);
}

.section-header {
  position: relative;
  padding-left: 50px;
  font-family: $inter-b;
  font-weight: normal;
  @include font-clamp(rem(20px), 4vw, rem(22px));
  @include line-height(38, 22);
  text-transform: uppercase;
  @include rem(letter-spacing, 2px);

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 16px;
    left: 0;
    width: 30px;
    height: 3px;
    background-color: primary-color(red);

    @include from(5) {
      top: 17px;
    }
  }
}

// Paragraphs
//
// Wrapper:
// max-width: 700px;
//
// Markup:
// <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
// <p>Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum. Donec elementum ligula eu sapien consequat eleifend.</p>
// <p>Donec nec dolor erat, condimentum sagittis sem. Praesent porttitor porttitor risus, dapibus rutrum ipsum gravida et. Integer lectus nisi, facilisis sit amet eleifend nec, pharetra ut augue. Integer quam nunc, consequat nec egestas ac, volutpat ac nisi.</p>
//
// Weight: -98
//
// Styleguide Typography.Paragraphs
p {
  @include rem(font-size, 16px);
  @include line-height(28, 16);
  margin: 0 0 20px;

  @include from(7) {
    @include rem(font-size, 18px);
    @include line-height(28, 18);
  }

  &:last-child {
    margin-bottom: 0;
  }

  &.-small {
    @include rem(font-size, 16px);
  }
}


ol,
ul {
  @include kill-bullets;
}


// Unordered Lists
//
// Wrapper:
// max-width: 700px;
//
// Markup:
// <div class="wysiwyg-content">
//   <ul>
//     <li>Lorem ipsum dolor</li>
//     <li>Sit amet, consectetur
//       <ul>
//         <li>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna
//           <ul>
//             <li>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna</li>
//             <li>Sit amet, consectetur</li>
//           </ul>
//         </li>
//         <li>Sit amet, consectetur</li>
//       </ul>
//     </li>
//     <li>Adipisicing elit, sed</li>
//   </ul>
// </div>
//
// Weight: -97
//
// Styleguide Typography.Unordered Lists

// Ordered Lists
//
// Wrapper:
// max-width: 700px;
//
// Markup:
// <div class="wysiwyg-content">
//   <ol>
//     <li>Lorem ipsum dolor</li>
//     <li>Sit amet, consectetur
//       <ol>
//         <li>Lorem ipsum dolor</li>
//         <li>Sit amet, consectetur
//           <ol>
//             <li>Lorem ipsum dolor</li>
//             <li>Sit amet, consectetur</li>
//             <li>Adipisicing elit, sed</li>
//           </ol>
//         </li>
//         <li>Adipisicing elit, sed</li>
//       </ol>
//     </li>
//     <li>Adipisicing elit, sed</li>
//     <li>Do eiusmod tempor</li>
//   </ol>
// </div>
//
// Weight: -96
//
// Styleguide Typography.Ordered Lists
.wysiwyg-content {

  ol,
  ul {
    // Due to absolute positioning, we cannot let elements float to the left
    // of these stylized lists
    clear: left;
    margin: 0 0 20px;

    &:last-child {
      margin-bottom: 0;
    }

    li {
      position: relative;
      @include rem(font-size, 16px);
      @include line-height(24, 16);
      padding-left: 22px;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }

      &::before {
        position: absolute;
        left: 0;
        color: primary-color(purple);
      }

      ul,
      ol {
        margin-top: 10px;
      }
    }

    ul {

      & > li {

        &::before {
          content: '\25E6';
        }
      }
    }
  }

  ol {
    counter-reset: section;

    & > li {
      counter-increment: section;
      padding-left: 32px;

      &::before {
        content: counter(section) '.';
      }
    }

    ol {

      & > li {
        padding-left: 32px;

        &::before {
          content: counter(section, lower-alpha) '.';
        }
      }

      ol {

        & > li {
          padding-left: 30px;

          &::before {
            content: counter(section, lower-roman) '.';
          }
        }
      }
    }
  }

  ul {

    & > li {

      &::before {
        content: '\2022';
        @include rem(font-size, 20px);
        @include line-height(22, 20);
      }
    }
  }
}

// Blockquotes
//
// Wrapper:
// max-width: 400px;
//
// Markup:
// <div class="text-block wysiwyg-content">
//   <blockquote>
//     <p>Lorem ipsum dolor sit amet. Volutpat est velit egestas dui id ornare arcu. Ut ornare lectus sit amet est.</p>
//   </blockquote>
// </div>
//
// Weight: -95
//
// Styleguide Typography.Blockquotes
blockquote {
  margin: 0;
}
