// Feature Image List
//
// Markup: feature-image-list.twig
//
// Styleguide Components.Feature Image List
.feature-image-list {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  @include component-padding;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    position: relative;
    @include grid-placement;
    @include wide-padding;
  }

  header {
    max-width: 700px;
    margin: 0 0 is-clamp(40px, 8vw, 70px);

    @include from(8) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }

    p {
      max-width: 500px;

      @include from(8) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .button-row {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-top: 30px;

      @include from(8) {
        flex-direction: row;
        justify-content: center;
        align-items: center;
      }

      a {

        @include from(8) {
          margin: 0 20px;
        }
      }
    }
  }

  .articles-container {

    @include from(7) {
      display: flex;
      align-items: flex-start;
    }
  }

  // main article
  .feature {
    margin-bottom: 50px;

    @include from(7) {
      width: 50%;
      margin: 0;
      padding-right: 20px;
    }

    @include from(9) {
      padding-right: 30px;
    }

    @include from(13) {
      width: auto;
      padding-right: 50px;
    }

    @include from(16) {
      padding-right: 100px;
    }

    .feature-image {
      position: relative;
      margin-bottom: 30px;
      overflow: hidden;

      img {
        transform: scale(1);
        transition: .3s $te;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: -1px;
        right: -1px;
        display: block;
        width: 16%;
        height: 50%;
        background-image: linear-gradient(0deg, rgba(primary-color(yellow), 1) 10%, rgba(primary-color(yellow), 0) 150%);
        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        pointer-events: none;
      }
    }

    .h3 {
      @include line-height(30, 26);

      a {
        display: inline;
        border: 1px solid transparent;
        transition: $ts $te;
      }
    }

    p {
      margin-top: 20px;
    }

    @include hover {

      .h3 {

        a {
          border-bottom: 1px solid primary-color(purple);
        }
      }

      .feature-image {

        img {
          transform: scale(1.03);
        }
      }
    }
  }

  .feature-image-list-articles {

    @include from(7) {
      width: 50%;
      padding-left: 20px;
    }

    @include from(9) {
      padding-left: 30px;
    }

    @include from(13) {
      width: 650px;
      flex: none;
      padding-left: 50px;
    }

    @include from(16) {
      padding-left: 0;
    }

    .item {
      display: flex;
      margin-bottom: 40px;

      &:not(:last-child) {
        padding-bottom: 40px;
        border-bottom: 1px solid rgba($hr-color, .4);
      }

      .item-image {
        overflow: hidden;
        width: 112px;
        flex: none;

        @include between(7, 10) {
          display: none;
        }

        img {
          transition: .3s $te;
        }
      }

      .item-text {
        flex: 1 1 auto;
        padding-right: 30px;

        @include between(7, 10) {
          padding: 0;
        }
      }

      .eyebrow {
        margin-bottom: 5px;
      }

      .h5 {
        margin-bottom: 10px;

        a {
          display: inline;
          border-bottom: 1px solid transparent;
          transition: $ts $te;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }

      .meta {
        margin-bottom: 20px;
        @include rem(font-size, 14px);
      }

      .meta-author {
        margin-right: 30px;
      }

      p {
        @include rem(font-size, 16px);
        @include line-height(24, 16);
      }

      &.js-link-event {

        @include hover {

          .item-image {

            img {
              transform: scale(1.05);
            }
          }

          .h5 {

            a {
              border-bottom: 1px solid primary-color(purple);
            }
          }
        }
      }
    }
  }

  &[data-feature-position='right'] {

    .articles-container {

      @include from(7) {
        flex-direction: row-reverse;
      }
    }

    .feature {
      margin-bottom: 50px;

      @include from(7) {
        padding-right: 0;
        padding-left: 20px;
      }

      @include from(9) {
        padding-left: 30px;
      }

      @include from(13) {
        padding-left: 50px;
      }

      @include from(16) {
        padding-left: 100px;
      }
    }

    .feature-image-list-articles {

      @include from(7) {
        padding-left: 0;
        padding-right: 20px;
      }

      @include from(9) {
        padding-right: 30px;
      }

      @include from(13) {
        padding-right: 50px;
      }

      @include from(16) {
        padding-right: 0;
      }
    }
  }
}
