// Content Expander
//
// Markup: content-expander.twig
//
// Styleguide Components.Content Expander
.content-expander {
  width: 100%;
  max-width: 900px;
  margin: 0 auto 60px;
  padding: is-clamp(20px, 4vw, 45px);
  background-image: linear-gradient(100deg, primary-color(lightgray) 0%, primary-color(white) 85%);

  .content-expander-btn {
    display: flex;
    align-items: center;
    margin-top: 20px;
    font-family: $inter-b;
    @include rem(font-size, 16px);
    @include line-height(19, 16);
    color: primary-color(purple);
    text-transform: uppercase;
    @include rem(letter-spacing, 1px);

    &::after {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 28px;
      height: 28px;
      margin-left: 18px;
      background-color: primary-color(white);
      border: 2px solid primary-color(purple);
      border-radius: 100%;
      transition: $ts $te;
      @include rem(font-size, 13px);
      line-height: 1;
      color: primary-color(purple);
      font-family: 'icomoon';
    }

    @include hover {

      &::after {
        background-color: primary-color(purple);
        color: primary-color(white);
      }
    }
  }

  &-description {

    .content-expander-btn {

      &::after {
        content: '\e90c';
      }

      &[aria-expanded='true'] {
        display: none;
      }
    }
  }

  &-content {
    display: none;
    margin-top: 20px;

    .content-expander-btn {

      &::after {
        content: '\e90a';
      }
    }
  }
}
