.article-detail {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);

  &:not(.-standard) {

    &::before {
      display: none;

      @include from(6) {
        content: '';
        position: absolute;
        z-index: 0;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 280px;
        background-image: linear-gradient(0deg, rgba(primary-color(white), 1) 0%, rgba(#f0f0f0, 1) 100%);
        pointer-events: none;
      }
    }
  }

  &-inner {
    position: relative;
    @include grid-placement;
    padding: is-clamp(40px, 6.66vw, 75px) 0;
    background-color: primary-color(white);

    @include from(11) {
      display: flex;
    }

    @include from(6) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @include from(6) {
      padding-left: 40px;
      padding-right: 40px;
    }

    @include from(12) {
      padding-left: 60px;
    }

    @include from(13) {
      padding-right: 60px;
    }

    @include from(14) {
      padding-left: 80px;
    }

    @include from(16) {
      padding-left: 105px;
    }
  }

  &-main {
    position: relative;
    margin-bottom: is-clamp(40px, 6.66vw, 75px);

    @include from(11) {
      flex: 1 1 auto;
      margin-right: is-clamp(65px, 6.36vw, 155px);
      margin-bottom: 0;
      // hack for slick carousel being a child of a flex item:
      min-height: 0;
      min-width: 0;
    }
  }

  &-sidebar {
    position: relative;

    @include from(11) {
      width: 340px;
      flex: none;
    }

    @include from(13) {
      width: 400px;
    }

    @include from(14) {
      width: 500px;
    }
  }

  &-header {
    margin-bottom: is-clamp(40px, 6.66vw, 60px);

    .eyebrow {
      @include font-clamp(rem(16px), 3.2vw, rem(20px));
      @include line-height(28, 20);
    }

    .article-detail-meta {
      @include rem(font-size, 14px);
      color: #4d4d4d;

      .byline {
        margin-right: 20px;
      }
    }
  }

  &-body {
    position: relative;
  }

  &-social-share {
    display: none;
    position: absolute;
    z-index: 1;
    top: 0;
    right: 100%;
    width: 100px;
    height: 100%;

    @include from(12) {
      display: block;
    }

    @include from(14) {
      width: 80px;
    }

    @include from(16) {
      width: 105px;
    }

    .social-share {
      margin: 0 auto;

      @include from(12) {
        position: sticky;
        top: 20px;
      }
    }
  }

  &-gated {

    > .text-block {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(primary-color(white), 1) 0%, rgba(primary-color(white), 0) 50%);
      }
    }
  }
}
