$tab_breakpoint: 8;

.accordion-tabs {

  .tabs {
    display: none;
  }

  .accordion-tabs-item {
    position: relative;
    min-height: 70px;

    .tab-trigger-mobile {
      display: block;
      width: 100%;
      min-height: 50px;

      @include from(8) {
        display: none;
      }
    }

    .accordion-tabs-content {
      opacity: 0;
      visibility: hidden;
      height: 0;
      transition: opacity .5s ease-in-out;

      > .accordion-tabs-content-inner {
        display: none;

        > .accordion {

          @include to(6) {
            padding: 10px 10px 0;
          }
        }
      }
    }

    &.-active {

      .accordion-tabs-content {
        opacity: 1;
        visibility: visible;
        height: auto;

        > .accordion-tabs-content-inner {
          display: block;
        }
      }
    }
  }

  &.-sidetabs {

    .tabs {

      > ul {
        display: block;
      }
    }

    .tab-trigger {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      background-color: primary-color(white);
      color: primary-color(black);
      @include font-weight(600);
      @include font-clamp(rem(16px), 1.77vw, rem(20px));
      @include line-height(22, 16);
      text-align: left;
      padding: 20px 30px;
      border-bottom: 1px solid primary-color(gray);

      @include icon(caret, after) {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: none;
        width: 27px;
        height: 27px;
        margin-left: 20px;
        background-color: primary-color(white);
        color: primary-color(purple);
        @include rem(font-size, 12px);
        border: 2px solid primary-color(purple);
        border-radius: 4px;
        transition: $ts $te;
      }

      &.-active {
        background-color: primary-color(purple);
        color: primary-color(white);

        @include hover {
          color: primary-color(white);

          &::after {
            background-color: primary-color(white);
            color: primary-color(purple);
          }
        }
      }

      @include hover {
        color: primary-color(purple);

        &::after {
          background-color: primary-color(purple);
          color: primary-color(white);
        }
      }
    }

    .accordion-tabs-item {
      min-height: 50px;

      @include from($tab_breakpoint) {
        min-height: 0;
      }

      .tab-trigger-mobile {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: primary-color(white);
        color: primary-color(black);
        @include font-weight(600);
        @include font-clamp(rem(16px), 2.66vw, rem(20px));
        @include line-height(22, 16);
        text-align: left;
        padding: 20px 30px;
        border-bottom: 1px solid primary-color(gray);

        @include from(10) {
          display: none;
        }

        @include icon(caret, after) {
          display: flex;
          justify-content: center;
          align-items: center;
          flex: none;
          width: 27px;
          height: 27px;
          margin-left: 20px;
          background-color: primary-color(white);
          color: primary-color(purple);
          @include rem(font-size, 12px);
          border: 2px solid primary-color(purple);
          border-radius: 4px;
          transform: rotate(90deg);
          opacity: 1;
          transition: $ts $te;
        }

        @include hover {
          color: primary-color(purple);

          &::after {
            background-color: primary-color(purple);
            color: primary-color(white);
          }
        }
      }

      &.-active {

        .tab-trigger-mobile {
          background-color: primary-color(purple);
          color: primary-color(white);

          &::after {
            opacity: 0;
          }

          @include hover {
            color: primary-color(white);

            &::after {
              background-color: primary-color(white);
              color: primary-color(purple);
            }
          }
        }

        .accordion-tabs-content-inner {
          border-bottom: 1px solid primary-color(gray);

          @include from($tab_breakpoint) {
            border: 0;
          }
        }
      }
    }
  }
}

@include from($tab_breakpoint) {

  .accordion-tabs {

    .tabs {
      display: block;

      ul {
        display: flex;
        justify-content: center;
      }

      li {
        position: relative;
        margin: 0 15px;

        .tab-trigger {
          position: relative;
          display: flex;
        }
      }
    }

    .accordion-tabs-item {
      min-height: 0;

      &::before {
        display: none;
      }
    }
  }
}
