// Component Margin
//
// Applies a consistent margin-bottom to elemenets
//
// Definition:
// @mixin component-margin {
//   margin-bottom: $component-margin;
// }
//
// Usage:
// .sample {
//   @include component-margin;
// }
//
// Compiled:
// .sample {
//   margin-bottom: 80px;
// }
//
// Styleguide Sass Mixins.Component Margin
@mixin component-margin {
  margin-bottom: $component-margin;
}
