.scrolling-banner {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  position: fixed;
  left: 50%;
  bottom: 0;
  z-index: 1;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 20px;
  background-color: primary-color(yellow);
  transform: translate(-50%, 110%);
  transition: .5s $te;

  @include from(8) {
    background-image:
    url('../images/bg-alert-banner-left.svg'),
    url('../images/bg-alert-banner-right.svg');
    background-position:
    left -160px center,
    right -60px center;
    background-repeat: no-repeat;
    background-size: auto 100%;
  }

  @include from(10) {
    padding: 20px 30px;
  }

  @include from(12) {
    background-position:
    left center,
    right center;
  }

  &.-unfixed {
    position: relative;
    transform: translate(-50%, 0);
  }

  &.-fixed {
    transform: translate(-50%, 0);
  }

  .inner {
    @include content-constraint;
    @include grid-placement(full-bleed);
    text-align: center;

    @include from(12) {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &-message {
    padding-left: 20px;

    .h3 {
      @include font-clamp(rem(16px), 4vw, rem(26px));
    }
  }

  .btn {
    margin-bottom: 20px;
    @include rem(font-size, 12px);

    @include from(7) {
      @include rem(font-size, 14px);
    }

    @include from(12) {
      flex: none;
      margin: 0;
    }
  }

  &-close {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 38px;
    background-color: primary-color(white);
    border-bottom-left-radius: 13px;
    transition: $ts $te;
    @include icon(close) {
      @include rem(font-size, 18px);
    }

    @include hover {
      background-color: primary-color(purple);
      color: primary-color(white);
    }
  }
}

.scrolling-banner-spacer {
  display: grid;
  @include grid-placement;
  padding: 20px;
  visibility: hidden;
  pointer-events: none;

  @include from(10) {
    padding: 20px 30px;
  }

  &.-hidden {
    display: none;
  }

  .inner {
    @include content-constraint;
    @include grid-placement(full-bleed);
    text-align: center;

    @include from(12) {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &-message {
    padding-left: 20px;
  }

  .btn {
    margin-bottom: 20px;

    @include from(12) {
      flex: none;
      margin: 0;
    }
  }
}
