// People Grid
//
// Markup: people-grid.twig
//
// Styleguide Components.People Grid
.people-grid {
  position: relative;
  @include grid-placement;
  padding: is-clamp(40px, 5vw, 100px) 0 20px;

  &[data-margin='true'] {
    @include component-margin;
  }

  header {
    max-width: 800px;
    margin: 0 auto is-clamp(40px, 8vw, 70px);
    text-align: center;
  }

  .accordion-tabs {

    .tabs {

      li {

        &.-active {

          &::after {
            content: '';
            position: absolute;
            bottom: -8px;
            left: calc(50% - 10px);
            display: block;
            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-top: 10px solid primary-color(yellow);
          }
        }

        button {

          &.-active {
            background-color: primary-color(yellow);
            border-color: primary-color(yellow);
          }
        }
      }
    }
  }

  .accordion-tabs-item {

    &.-active {

      .tab-trigger-mobile {
        background-color: primary-color(yellow);
      }

      &::after {
        content: '';
        position: absolute;
        top: 50px;
        left: calc(50% - 10px);
        display: block;
        width: 0;
        height: 0;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-top: 10px solid primary-color(yellow);

        @include from(8) {
          display: none;
        }
      }
    }
  }

  .tab-trigger-mobile {
    background-color: primary-color(white);
    padding: 14px 20px;
    color: primary-color(black);
    font-family: $inter-b;
    @include rem(font-size, 14px);
    @include line-height(17, 14);
    @include rem(letter-spacing, 1px);
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
    border: 2px solid primary-color(yellow);
    border-radius: 100px;
    transition: background-color .2s $te 0s, color .2s $te, border-color .2s $te;
  }

  .people-grid-container {
    padding: 70px 0 0;

    @include from(6) {
      display: flex;
      flex-wrap: wrap;
    }

    @include between(6, 9) {
      max-width: 700px;
      margin: 0 auto;
    }

    @include from(10) {
      margin: 0 -20px;
    }
  }

  .person {
    margin: 0 auto 40px;
    text-align: center;
    @include rem(font-size, 14px);
    @include line-height(12, 14);

    @include from(6) {
      width: calc(50% - 40px);
      margin: 0 20px is-clamp(40px, 6.66vw, 90px);
    }

    @include from(10) {
      width: calc(25% - 40px);
    }

    .photo-container {

      @include between(6, 8) {
        padding: 0 20px;
      }
    }

    .photo {
      max-width: 254px;
      margin: 0 auto 20px;
      box-shadow: 0 0 0 1px transparent;
      border-radius: 100%;
      transition: $ts $te;
    }

    .photo-inner {
      overflow: hidden;
      border-radius: 100%;
    }

    // hacky safari thing
    @media not all and (min-resolution: .001dpcm) {
      @supports (-webkit-appearance: none) {
        .photo-inner {
          -webkit-mask-image: -webkit-radial-gradient(white, black);
        }
      }
    }

    img {
      transform: scale(1);
      transition: $ts $te;
    }

    .title {
      margin-bottom: 10px;
      font-family: $inter-b;
      @include rem(font-size, 12px);
      @include line-height(18, 12);
      text-transform: uppercase;
      @include rem(letter-spacing, 2px);
      color: primary-color(darkgray);
    }

    .h4 {
      display: inline;
      border-bottom: 1px solid transparent;
      transition: $ts $te;

      a {
        display: inline;
      }
    }

    .degrees {
      margin-top: 10px;
    }

    .term {
      margin-top: 10px;
    }

    .email {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      margin-top: 20px;
      @include icon(email) {
        margin-right: 8px;
        transition: $ts $te;
      }

      span {
        padding-bottom: 3px;
        border-bottom: 1px solid transparent;
        transition: $ts $te;
      }

      &:hover {

        span {
          border-bottom: 1px solid primary-color(purple);
        }
      }

      &:focus {

        span {
          color: primary-color(red);
          border-bottom: 1px solid primary-color(red);
        }

        &::before {
          color: primary-color(red);
        }
      }
    }

    .js-link-event {

      @include hover {

        .h4 {
          border-bottom: 1px solid primary-color(purple);
        }

        .photo {
          box-shadow: 0 0 0 8px primary-color(red);
        }

        img {
          transform: scale(1.1);
        }
      }
    }
  }
}
