.site-footer {
  @extend %grid-columns;
  background-image: linear-gradient(0deg, primary-color(lightgray) 0%, primary-color(white) 100%);
  opacity: 1;
  transition: opacity $ts $te;

  .aota-color-bars {
    @include grid-placement(full-bleed);
    width: 100%;
    height: 8px;
  }

  &-inner {
    @include grid-placement(full-bleed);
    padding: 45px 0;

    @include from(7) {
      padding: 0;
    }
  }

  // Top Container
  .top {

    @include from(7) {
      padding: 45px $mobile-padding 40px;
      border-bottom: 1px solid primary-color(gray);
    }

    @include from(10) {
      padding: 45px $desktop-padding 40px;
    }

    .top-inner {
      @include content-constraint;

      @include from(7) {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }
  }

  .logo {
    display: block;
    width: 332px;
    margin: 0 auto;

    @include from(7) {
      margin: 0;
    }
  }

  // Social Media Links
  &-social {
    margin-top: 40px;
    border-top: 1px solid primary-color(gray);

    @include from(7) {
      margin: 0;
      border: 0;
    }

    ul {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 25px 0;

      @include from(7) {
        margin: 0;
      }
    }

    li {
      flex: none;

      & + li {
        margin-left: 18px;
      }
    }

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 35px;
      height: 35px;
      background-color: primary-color(black);
      @include rem(font-size, 18px);
      color: #f9f9f9;
      border-radius: 100%;

      @include hover {
        background-color: primary-color(purple, tint1);
      }
    }
  }

  // Bottom Container
  .bottom {
    padding: 0 $mobile-padding;

    @include from(7) {
      padding: 10px $mobile-padding;
    }

    @include from(10) {
      padding: 10px $desktop-padding 45px;
    }

    .bottom-inner {
      @include content-constraint;

      @include from(10) {
        display: flex;
        justify-content: space-between;
      }
    }
  }

  // Nav (nav)
  &-nav {
    margin-bottom: 20px;

    @include from(7) {
      margin-bottom: 10px;
    }

    // Items
    ul {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
    }

    // Item
    li {
      position: relative;
      flex: none;

      // Sibling Items
      & + li {
        margin-bottom: 10px;
        margin-left: 15px;
      }
    }

    // Link
    a {
      display: inline;
      color: primary-color(black);
      @include rem(font-size, 14px);
      border-bottom: 1px solid transparent;
      transition: $ts $te;

      @include hover {
        color: primary-color(purple);
        border-color: primary-color(purple);
      }
    }
  }

  &-copyright {
    text-align: center;

    p {
      @include rem(font-size, 14px);
      @include line-height(17, 14);
    }
  }
}
