// Three Column-List
//
// Markup: three-column-list.twig
//
// Styleguide Components.Three Column-List
.three-column-list {
  @include grid-placement;
  @include component-padding;

  @include from(10) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .inner {
    overflow: hidden;
    @include wide-padding;
  }

  header {
    max-width: 550px;
    margin: 0 auto is-clamp(40px, 8vw, 80px);
    text-align: center;
  }

  .list-container {
    max-width: 400px;
    margin: 0 auto 40px;

    @include from(5) {
      max-width: none;
    }

    @include from(11) {
      max-width: none;
      display: flex;
      margin: 0 -30px 40px;
    }

    @include from(13) {
      margin: 0 -40px 40px;
    }
  }

  .list-column {

    @include from(11) {
      width: 33.33%;
      padding: 0 30px;
      border-right: 1px solid $hr-color;
    }

    @include from(13) {
      padding: 0 40px;
    }

    &:last-child {
      border: 0;
    }
  }

  .category {
    margin-bottom: 45px;

    .h6 {

      @include from(11) {
        margin-bottom: 30px;
      }
    }

    &:last-child {

      @include from(11) {
        margin-bottom: 0;
      }

      .btn {

        &:last-child {

          @include from(11) {
            margin-bottom: 0;
          }
        }
      }
    }

    .btn {
      display: block;
      margin: 0 0 24px;
      text-align: left;
      @include rem(font-size, 12px);
      white-space: normal;

      @include between(5, 10) {
        display: inline-block;
        margin: 0 15px 20px 0;
      }
    }
  }

  .button-row {
    text-align: center;
  }
}
