.testimonial {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  position: relative;
  background-image: linear-gradient(90deg, rgba(primary-color(white), 1) 25%, rgba(primary-color(lightgray), 1) 100%);
  transform-style: preserve-3d;

  &[data-borders='true'] {
    padding: is-clamp(15px, 3vw, 30px) 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 35px;
      background-image: linear-gradient(90deg, rgba(primary-color(lightgray), 1) 0%, rgba(primary-color(white), 1) 75%);
    }
  }

  &[data-margin='true'] {
    @include component-margin;
  }

  .inner {
    position: relative;
    overflow: hidden;
    @include grid-placement(full-bleed);

    @include between(6, 9) {
      padding-top: is-clamp(180px, 30vw, 300px);
    }
  }

  .background-image {
    display: none;

    @include from(6) {
      display: block;
    }

    picture {

      @include from(6) {
        @include absolute(0, 0, null, 0);
        z-index: 0;
        height: 50%;
      }

      @include from(10) {
        height: 100%;
      }

      img {

        @include from(6) {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
        }
      }
    }
  }

  .inner-content {
    position: relative;
    @include grid-placement;

    @include from(10) {
      padding-top: is-clamp(60px, 6vw, 90px);
    }
  }

  .content {
    position: relative;
    max-width: 1300px;
    padding: 0 is-clamp(40px, 8vw, 70px);
    transform-style: preserve-3d;

    @include from(10) {
      width: 840px;
      margin: 0 auto;
      padding: 0;
      background-color: transparent;
    }

    @include from(12) {
      width: 890px;
    }

    &::before {
      @include pseudo-abs($width: 150%);
      left: 50%;
      transform: translateX(-50%);
      background-color: primary-color(white);

      @include from(6) {
        border-top-left-radius: 150% * 2;
        border-top-right-radius: 150% * 2;
      }

      @include from(10) {
        display: none;
      }
    }

    &::after {
      display: none;

      @include between(6, 9) {
        display: block;
        @include pseudo-abs($width: 150%);
        z-index: -1;
        top: -30px;
        left: 50%;
        transform: translateX(-50%);
        background-image: linear-gradient(90deg, rgba(#f7b03b, 0) 50%, rgba(#f7b03b, 1) 90%);
        border-top-left-radius: 150% * 2;
        border-top-right-radius: 150% * 2;
      }
    }

    .circle {
      display: none;

      @include from(10) {
        display: block;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        width: 100%;
        padding-bottom: 100%;
        background-image: linear-gradient(82deg, rgba(#ea4927, 1) 0%, rgba(#ea4927, 0) 20%, rgba(#f7b03b, 0) 65%, rgba(#f7b03b, 1) 100%);
        border-radius: 100%;
        transform: rotate(0deg);
        transition: .4s $te;
        transition-delay: .1s;
      }

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 30px;
        left: 30px;
        width: calc(100% - 60px);
        height: calc(100% - 60px);
        background-color: primary-color(white);
        border-radius: 100%;
      }
    }
  }

  .text {
    position: relative;
    max-width: 540px;
    margin: 0 auto;
    padding: is-clamp(40px, 8vw, 85px) 0 is-clamp(60px, 12vw, 70px);
    text-align: center;

    @include between(6, 9) {
      min-height: 230px;
    }
  }

  .testimonial-slider {
    position: relative;
    padding-bottom: 50px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s $te;

    &.slick-initialized {
      opacity: 1;
      visibility: visible;
    }

    .slick-arrow {
      position: absolute;
      z-index: 1;
      top: calc(50% - 22px);
      left: -20px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 44px;
      height: 44px;
      border: 2px solid transparent;
      border-radius: 100%;
      font-size: 0;
      @include icon(arrow-long-next) {
        position: relative;
        left: 0;
        @include rem(font-size, 18px);
        color: primary-color(purple);
        transition: $ts $te;
      }

      @include from(7) {
        top: calc(50% - 32px);
        left: auto;
        right: 100%;
        width: 64px;
        height: 64px;

        &::before {
          @include rem(font-size, 25px);
        }
      }

      &.slick-prev {
        @include icon(arrow-long-prev);

        &:hover {

          &::before {
            left: -4px;
          }
        }
      }

      &.slick-next {
        left: auto;
        right: -20px;

        @include from(7) {
          right: auto;
          left: 100%;
        }

        &:hover {

          &::before {
            left: 4px;
          }
        }
      }

      &:focus {
        border-color: primary-color(purple);

        &:hover {

          &::before {
            left: 0;
          }
        }
      }
    }

    .slick-dots {
      position: absolute;
      z-index: 1;
      bottom: 0;
      width: 100%;
      display: flex;
      justify-content: center;

      li {
        margin: 0 4px;

        button {
          width: 13px;
          height: 13px;
          background-color: primary-color(lightgray);
          font-size: 0;
          border-radius: 100%;
          transition: $ts $te;

          @include hover {
            background-color: primary-color(purple);
          }
        }

        &.slick-active {

          button {
            background-color: primary-color(purple);
          }
        }
      }
    }
  }

  .slide {
    padding: 0 is-clamp(40px, 10vw, 50px);

    .image-holder {
      position: relative;
      width: 100px;
      margin: 0 auto is-clamp(25px, 5vw, 55px);

      @include from(6) {
        width: 125px;
      }

      @include from(8) {
        width: 146px;
      }

      // forcing img height in case sitecore adds rectangular image
      img {
        width: 100px;
        height: 100px;
        border-radius: 100%;

        @include from(6) {
          width: 125px;
          height: 125px;
        }

        @include from(8) {
          width: 146px;
          height: 146px;
        }
      }

      @include icon(quote) {
        display: flex;
        justify-content: center;
        align-items: center;
        width: is-clamp(40px, 8vw, 71px);
        height: is-clamp(40px, 8vw, 71px);
        margin: 0 auto;
        background-color: primary-color(green);
        border-radius: 100%;
        color: primary-color(white);
        @include font-clamp(rem(16px), 3.2vw, rem(30px));
      }

      &[data-image='true'] {

        &::before {
          position: absolute;
          top: 0;
          left: -20px;

          @include from(6) {
            left: -30px;
          }

          @include from(8) {
            left: -40px;
          }
        }
      }
    }

    p {
      margin-bottom: 0;
      @include font-clamp(rem(16px), 3.2vw, rem(30px));
      @include line-height(38, 30);
      @include font-weight(600);
    }

    .attribution {
      margin-top: is-clamp(10px, 2vw, 20px);

      .attribution-name {
        @include font-clamp(rem(12px), 2.4vw, rem(16px));
      }

      .attribution-title {
        margin-top: 8px;
        @include rem(font-size, 13px);
        font-style: italic;
      }
    }
  }
}
