//  Slicer Breakpoints
//  https://github.com/lolmaus/breakpoint-slicer
//
//  Breakpoint:    0      100px   200px   320px   400px   500px   600px   768px       1920px
//                 | ... >
//  Slice #:               1       2       3       4       5       6       7           19
$slicer-breakpoints: 100px 200px 320px 400px 500px 600px 768px 800px 900px 1024px 1100px 1200px 1300px 1440px 1500px 1600px 1700px 1800px 1920px;
$slicer-anti-overlap-corrections: -1px;

$arial: Arial, 'Helvetica Neue', Helvetica, sans-serif;
$inter: 'Inter', Arial, sans-serif;
$inter-sb: 'Inter Semibold', Arial, sans-serif;
$inter-b: 'Inter Bold', Arial, sans-serif;

// type variables
$body-font: $inter;
$header-font: $inter;

///////////////////////////////////////////////////////////////
// NOTE: If you change this, you also need to change $rem-baseline
//       in src/global-scss/helpers/mixins/_rem.scss
///////////////////////////////////////////////////////////////
$base-font-size: 16px;

$component-margin: 80px;
$inner-max-width: 1535px;
$mobile-max-width: 640px;

$mobile-padding: 20px;
$desktop-padding: 30px;
$wide-padding: 70px;

$mobile-menu-break: 10;

$border-radius: 3px;
$hr-color: #c4c4c4;

// transitions
$ts: .25s; // transition speed
$te: ease-in-out; // transition ease
