.breadcrumb {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  display: none;
  background-image: linear-gradient(0deg, rgba(#f0f0f0, 1) 0%, rgba(#eaeaea, 1) 100%);
  font-family: $inter-sb;
  @include rem(font-size, 12px);
  line-height: 1;

  @include from(7) {
    display: grid;
  }

  &-inner {
    @include grid-placement;
    overflow: hidden;
    display: flex;
    align-items: center;
    height: is-clamp(50px, 5vw, 77px);
  }

  // ul
  &-container {
    display: flex;
    align-items: center;
    white-space: nowrap;
    max-width: 100%;
  }

  // li
  &-item {
    position: relative;
    min-width: 0;
    transition: $ts $te;
    flex: 0 1 auto;

    @include hover {
      flex: 1 0 auto;

      .breadcrumb-link {
        text-overflow: clip;
      }
    }

    &:not(:last-child) {
      padding: 0 25px 0 15px;

      @include icon(caret, after) {
        position: absolute;
        top: calc(50% - 5px);
        right: -15px;
        margin: 0 15px;
        @include rem(font-size, 10px);
      }
    }

    &:first-child {
      padding-left: 0;
    }

    &.-current {
      padding-left: 15px;
    }

    &:first-child,
    &.-current {
      flex: 1 0 auto;

      &.-clip {
        max-width: none;
        min-width: 30px;
        flex: 0 1 auto;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transition: $te $ts;
      }
    }
  }

  &-link {
    display: block;
    overflow: hidden;
    padding: 3px 0;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: primary-color(darkgray);
    border-bottom: 1px solid transparent;
    transition: $ts $te;
  }

  a {

    @include hover {
      color: primary-color(purple);
      border-bottom: 1px solid primary-color(purple);
    }
  }
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {

  .breadcrumb-item {
    flex: 1 1;

    &:not(:first-child) {
      flex-basis: 50px;
    }

    &:last-child {
      flex-basis: auto;
    }
  }
}
