// Fifty Fifty-Circle
//
// Markup: fifty-fifty-circle.twig
//
// Styleguide Components.Fifty Fifty-Circle
.fifty-fifty-circle {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  padding: is-clamp(40px, 5vw, 60px) 0;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    @include grid-placement;
    @include wide-padding;

    @include from(9) {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;
    }
  }

  .image {
    position: relative;
    max-width: 500px;
    margin: 0 auto 40px;

    @include from(9) {
      width: 50%;
      max-width: none;
      margin: 0;
    }

    @include from(16) {
      width: 625px;
    }

    picture {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      max-width: 625px;
      margin: 0 auto;
      padding: 25px;
      transform-style: preserve-3d;

      @include between(9, 10) {
        padding: 15px;
      }

      img {
        position: relative;
        z-index: 1;
        border-radius: 100%;
      }

      &::before {
        content: '';
        position: absolute;
        top: 1px;
        left: 1px;
        z-index: -1;
        display: block;
        width: calc(100% - 2px);
        height: calc(100% - 2px);
        background-image: linear-gradient(90deg, rgba(primary-color(green), 1) 0%, rgba(primary-color(green), 1) 50%, rgba(primary-color(yellow), 1) 50%, rgba(primary-color(yellow), 1) 100%);
        border-radius: 100%;
        opacity: 0;
        transition: 1s $te;
      }

      &::after {
        content: '';
        position: absolute;
        z-index: 0;
        display: block;
        width: 100%;
        height: 100%;
        background-image:
        linear-gradient(345deg,
          rgba(primary-color(white), 1) 0%,
          rgba(primary-color(white), 1) 13%,
          rgba(primary-color(white), 0) 40%,
          rgba(primary-color(white), 0) 60%,
          rgba(primary-color(white), 1) 87%,
          rgba(primary-color(white), 1) 100%);
        border-radius: 100%;
      }
    }

    &.-animated {

      picture {

        &::before {
          opacity: 1;
        }
      }
    }
  }

  .text {
    max-width: $mobile-max-width;
    margin: 0 auto;

    @include from(9) {
      width: 50%;
      max-width: none;
      margin: 0;
      padding-right: is-clamp(50px, 5.55vw, 100px);
      text-align: left;
    }

    @include from(16) {
      padding-right: 175px;
    }

    header {
      margin-bottom: 30px;

      .h2 {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          top: is-clamp(15px, 3vw, 20px);
          left: -70px;
          display: none;
          width: 52px;
          height: 3px;
          background-color: primary-color(red);

          @include between(8, 8) {
            display: block;
          }

          @include from(14) {
            display: block;
            left: -77px;
          }
        }
      }
    }

    .btn {
      margin-top: 30px;
    }

    .link-list {
      margin: 30px 0 0;

      li {

        & + li {
          margin-top: is-clamp(15px, 1.66vw, 25px);
        }
      }
    }
  }

  &[data-image-position='left'] {

    .inner {

      @include from(9) {
        flex-direction: row;
      }
    }

    .text {

      @include from(9) {
        padding-right: 0;
        padding-left: is-clamp(50px, 5.55vw, 100px);
      }

      @include from(16) {
        padding-right: 75px;
      }

      header {

        .h2 {

          &::before {

            @include from(8) {
              display: block;
            }

            @include from(14) {
              left: -77px;
            }
          }
        }
      }
    }
  }
}
