// Vertical Image-Grid
//
// Markup: vertical-image-grid.twig
//
// Styleguide Components.Vertical Image-Grid
.vertical-image-grid {
  @include grid-placement;
  @include component-padding;

  &-inner {

    header {
      max-width: 700px;
      margin: 0 0 is-clamp(40px, 8vw, 70px);

      @include from(8) {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
      }

      p {
        max-width: 500px;

        @include from(8) {
          margin-left: auto;
          margin-right: auto;
        }
      }

      .button-row {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 30px;

        @include from(8) {
          flex-direction: row;
          justify-content: center;
          align-items: center;
        }

        .btn {

          + .btn-text {

            @include to(7) {
              margin-top: 20px;
            }
          }
        }

        a {

          @include from(8) {
            margin: 0 20px;
          }
        }
      }
    }
  }

  &-title {
    text-align: center;
    margin-bottom: 70px;
  }

  &-items {
    display: grid;
    grid-template-columns: repeat(auto-fit, 230px);
    justify-content: center;
    gap: 60px; // sass-lint:disable-line no-misspelled-properties
  }

  &-item {
    align-self: flex-start;

    &.js-link-event {

      @include hover {

        .image {
          transform: translateY(-10px);
        }
      }
    }

    .image {
      position: relative;
      margin-bottom: 30px;
      transition: .2s $te;
      width: 100%;

      picture {
        width: 100%;

        img {
          object-fit: cover;
        }
      }
    }

    .eyebrow {
      margin-bottom: 4px;
    }

    .title {
      margin-bottom: 12px;

      a {
        @include hover {
          text-decoration: underline;
          text-decoration-thickness: 2px !important; // sass-lint:disable-line no-misspelled-properties
        }
      }
    }

    .byline,
    .date {
      display: inline-block;
      font-size: .75rem;
      margin-bottom: 20px;
    }

    .byline {
      margin-right: 26px;
    }

    .text {
      color: primary-color(darkgray);
      font-size: 1rem;
    }
  }
}
