.pagination {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;

  &-pages {
    display: flex;

    > * {
      display: block;
      flex: none;
      width: rem(44px);
      height: rem(44px);
      color: blue;
      @include rem(font-size, 18px);
      line-height: rem(44px);
      text-align: center;
      border-radius: 50%;

      & + * {
        margin-left: 5px;
      }
    }

    & > a {

      @include hover {
        background: blue;
        color: primary-color(white);
      }

      &[data-current='true'] {
        pointer-events: none;
        background: #e0e0e0;
      }
    }
  }

  &-nav {
    display: flex;
    justify-content: space-between;
    width: rem(100px);
  }

  &-prev,
  &-next {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: rem(44px);
    height: rem(44px);
    background: #e0e0e0;
    color: blue;
    @include rem(font-size, 30px);
    border-radius: 50%;
    transition: $ts $te;

    @include icon(null, before) {
      flex: none;
    }

    @include hover {
      background: blue;
      color: primary-color(white);
    }
  }

  &-prev {
    @include icon(caret-left, before, false);
  }

  &-next {
    @include icon(caret-right, before, false);
  }
}
