.capability-model {

  &-nav-container {
    @extend %grid-columns;
    @include grid-placement(full-bleed);
    background-color: primary-color(lightgray);
    border-top: 8px solid primary-color(darkgray);
  }

  &-nav {
    @include grid-placement;

    &-mobile {
      display: flex;
      justify-content: center;
      max-width: 600px;
      margin: 0 auto;
      padding: 18px 0;

      .btn {
        flex: none;
        margin-left: 10px;
      }

      @include from($mobile-menu-break) {
        display: none;
      }
    }

    &-desktop {
      display: none;

      @include from($mobile-menu-break) {
        display: block;
      }
    }

    &-items {
      display: flex;
      margin-left: -10px;
    }

    &-item {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: -8px;
        left: 0;
        width: 100%;
        height: 8px;
        background-color: transparent;
        opacity: 0;
        transition: $ts $te;
      }

      &[aria-current='page'] {
        background-color: primary-color(white);

        &::before {
          background-color: transparent;
          opacity: 1;
        }
      }

      // 'overview' - no color
      // 'adults' - red
      // 'pediatrics' - purple
      // 'universal' - yellow
      // 'other' - green

      &[data-section='overview'] {

        &[aria-current='page'] {

          &::before {
            background-color: transparent;
            opacity: 1;
          }
        }
      }

      &[data-section='adults'] {

        &[aria-current='page'] {

          &::before {
            background-color: primary-color(red);
            opacity: 1;
          }
        }
      }

      &[data-section='pediatrics'] {

        &[aria-current='page'] {

          &::before {
            background-color: primary-color(purple);
            opacity: 1;
          }
        }
      }

      &[data-section='universal'] {

        &[aria-current='page'] {

          &::before {
            background-color: primary-color(yellow);
            opacity: 1;
          }
        }
      }

      &[data-section='other'] {

        &[aria-current='page'] {

          &::before {
            background-color: primary-color(green);
            opacity: 1;
          }
        }
      }
    }

    &-link {
      display: block;
      padding: 10px 15px;
      font-family: $inter-sb;
      @include rem(font-size, 14px);
      @include line-height(24, 14);
      color: primary-color(black);
      text-transform: uppercase;

      @include from(10) {
        padding: 12px 30px;
      }

      @include from(12) {
        padding: 12px 38px;
      }

      span {
        border-bottom: 1px solid transparent;
        transition: $ts $te;
      }

      @include hover {

        span {
          border-bottom: 1px solid primary-color(purple);
        }
      }
    }
  }
}
