//sass-lint:disable force-element-nesting
//sass-lint:disable single-line-per-selector
//sass-lint:disable no-important
//sass-lint:disable quotes
//sass-lint:disable force-pseudo-nesting
//sass-lint:disable nesting-depth

.CoveoSearchInterface {

  &.aota-card-list-layout {

    .coveo-results-header {
      position: relative;
      box-shadow: none;

      &::after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 8px;
        background-image: url('../images/color-bars.svg');
        background-repeat: no-repeat;
        background-position: bottom center;
        background-size: 100% 8px;
      }
    }

    .coveo-list-layout.CoveoResult {
      padding: 0;
      border: 0;
    }

    .coveo-result-list-container {

      @include from(6) {
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        margin-left: -10px;
        margin-right: -10px;
      }

      @include from(15) {
        margin-left: -15px;
        margin-right: -15px;
      }
    }

    .CoveoResult {
      margin: 0 0 30px;
      min-height: 200px;

      @include from(6) {
        width: calc(50% - 20px);
        min-height: 285px;
        margin: 0 10px 30px;
      }

      @include between(7, 9) {
        width: calc(33.333% - 20px);
      }

      @include from(12) {
        min-height: 315px;
      }

      @include from(13) {
        width: calc(33.333% - 20px);
      }

      @include from(15) {
        width: calc(33.333% - 30px);
        margin: 0 15px 30px;
      }
    }

    .CoveoResultList {

      .search-results-listing {
        margin: 0;
        padding: 0;

        @include from(6) {
          display: flex;
          flex-direction: column;
          height: 100%;
        }

        .type {
          display: flex;
          align-items: center;
          flex: none;
          height: 54px;
          padding: 0 24px;
          border-left: 6px solid primary-color(green);
          font-family: $inter-b;
          @include rem(font-size, 12px);
          @include line-height(28, 12);
          @include rem(letter-spacing, 2px);
          text-transform: uppercase;
          color: primary-color(darkgray);
          background: linear-gradient(180deg, rgba(#f4f4f4, 1) 1%, rgba(primary-color(white), 1) 100%);

          @include between(7, 9) {
            padding: 0 18px;
          }

          @include between(13, 13) {
            padding: 0 18px;
          }
        }

        .search-results-listing-inner {
          padding: 25px 30px;
          background: linear-gradient(180deg, rgba(#f4f4f4, 1) 1%, rgba(primary-color(white), 1) 100%);

          @include from(6) {
            flex: 1 1 auto;
          }

          @include between(7, 9) {
            padding: 25px 20px;
          }

          @include between(13, 13) {
            padding: 25px 20px;
          }
        }

        .eyebrow {
          margin-bottom: 12px;
        }

        .meta {
          margin-top: 10px;
        }

        .meta-type {
          display: block;
          margin: 0 0 5px;
        }
      }
    }
  }
}
