.site-header {
  @extend %grid-columns;
  position: relative;
  z-index: 10;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .07);

  // temp:
  // margin-bottom: 40px;

  // fake dropshadow for when main nav is under header and menu is open
  &::after {

    @include between(10, 11) {
      content: '';
      position: absolute;
      z-index: 200;
      bottom: 38px;
      left: 0;
      display: block;
      width: 100%;
      height: 7px;
      background-image: linear-gradient(180deg, #ececec 0%, rgba(#ececec, 0) 100%);
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      transition: $ts $te;
    }
  }

  // TOP COLOR STRIP
  .aota-color-bars {
    @include grid-placement(full-bleed);
    width: 100%;
    height: 8px;
    transition: $ts $te;
  }

  // GRAY TOP BAR
  .utility-nav {
    display: none;

    @include from(7) {
      display: block;
      @include grid-placement(full-bleed);
      padding: 0 $mobile-padding;
      background: primary-color(lightgray);
    }

    @include from(10) {
      padding: 0 $desktop-padding;
    }

    .utility-nav-inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @include content-constraint;
      height: 48px;
    }

    // FAMILY OF SITES
    .utility-nav-sites {
      display: none;

      @include from(10) {
        display: block;
      }

      .utility-nav-sites-items {
        display: flex;
        align-items: center;

        a {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 48px;
          padding: 0 30px;
          font-family: $inter-b;
          @include rem(font-size, 11px);
          text-transform: uppercase;
          color: primary-color(black);
          white-space: nowrap;

          &[data-active='true'] {
            background-color: primary-color(white);
          }

          span {
            border-bottom: 1px solid transparent;
            transition: $ts $te;
          }

          @include hover {
            color: primary-color(purple, tint1);

            span {
              border-color: primary-color(purple, tint1);
            }
          }
        }
      }
    }

    // FAMILY OF SITES (Tablet)
    .utility-nav-sites-select {
      display: flex;
      align-items: center;

      @include from(10) {
        display: none;
      }

      .utility-nav-sites-select-label {
        font-family: $inter-b;
        @include rem(font-size, 11px);
        text-transform: uppercase;
        color: primary-color(black);
        white-space: nowrap;
      }

      .utility-nav-sites-select-select {
        width: 215px;
        height: 32px;
        margin: 0 15px;
        padding: 0 15px;
        @include rem(font-size, 11px);
        color: primary-color(black);
      }
    }

    // TOP RIGHT LINKS - USER AREA
    .utility-nav-user-menu {
      margin-left: 20px;
    }

    .utility-nav-user-menu-items {
      display: flex;
      align-items: center;

      li {
        position: relative;
      }

      // USER LINK AND DROPDOWN
      .utility-nav-user-link {
        position: relative;
        display: flex;
        align-items: center;
        padding: 0 15px;
        height: 48px;
        font-family: $inter-b;
        @include rem(font-size, 11px);
        @include line-height(15, 11);
        text-transform: uppercase;
        color: primary-color(black);

        &::before {
          margin-right: 16px;
          @include rem(font-size, 19px);
        }

        span {
          border-bottom: 1px solid transparent;
          transition: $ts $te;
        }

        @include hover {
          color: primary-color(purple, tint1);

          span {
            border-color: primary-color(purple, tint1);
          }
        }

        &.-logged-in {
          @include icon(caret-down, after) {
            position: absolute;
            right: -5px;
          }

          &[aria-expanded='true'] {
            @include icon(caret-up, after);
          }
        }
      }

      // USER DROPDOWN
      .utility-nav-user-dropdown {
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        position: absolute;
        z-index: 300;
        top: 100%;
        left: 0;
        width: calc(100% + #{$mobile-padding});
        padding: 10px 25px;
        background-color: primary-color(lightgray);
        box-shadow: 0 4px 4px rgba(0, 0, 0, .08);
        transition: $ts $te;

        @include from(10) {
          width: calc(100% + #{$desktop-padding});
        }

        &[data-expanded='true'] {
          visibility: visible;
          opacity: 1;
          pointer-events: auto;
          border-radius: 0;
        }

        li {
          margin-bottom: 10px;
        }

        a {
          display: inline;
          @include rem(font-size, 13px);
          @include line-height(16, 13);
          color: primary-color(black);
          border-bottom: 1px solid transparent;

          @include hover {
            color: primary-color(purple);
            border-color: primary-color(purple);
          }
        }
      }
    }
  }

  // MIDDLE MAIN SECTION
  &-inner {
    @include grid-placement;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    padding: 14px 0;

    @include from(7) {
      padding: 25px 0;
    }

    @include between(10, 11) {
      position: relative;
      height: 145px;
      align-items: flex-start;
    }
  }

  // LOGO (Split into 2 pieces because text hides at some breakpoints when menu is open)
  .logo {
    display: flex;
    align-items: center;
    flex: none;
    font-size: 0;
    width: 205px;
    transition: none;
    margin-right: auto;

    @include from(7) {
      width: 260px;
      margin-right: 25px;
    }

    @include between(10, 11) {
      margin-top: 2px;
    }

    @include between(12, 13) {
      width: 115px;
    }

    @include from(14) {
      width: 279px;
      margin-right: 20px;
    }

    .aota-logo {
      flex: none;
      width: 90px;

      @include from(7) {
        width: 115px;
      }

      @include from(12) {
        width: 125px;
      }
    }

    .aota-logo-text {
      flex: none;
      width: 110px;
      margin-left: 4px;

      @include from(7) {
        width: 140px;
      }

      @include between(12, 13) {
        display: none;
      }

      @include from(14) {
        width: 150px;
      }
    }
  }

  // MOBILE NAV BUTTONS
  .mobile-nav-toggles {
    display: flex;
    flex: none;
    margin-left: 30px;

    @include from(10) {
      display: none;
    }

    .mobile-search-toggle,
    .mobile-menu-toggle {
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      flex: none;
      width: 44px;
      height: 44px;
      border-radius: 100%;

      @include icon(null) {
        flex: none;
        @include rem(font-size, 20px);
      }

      @include hover {
        background-color: primary-color(purple);
        color: primary-color(white);
      }
    }

    .mobile-search-toggle {
      display: none;
      @include icon(search, before, false);

      @include breakpoint(360px) {
        display: inline-flex;
      }
    }

    .mobile-menu-toggle {
      @include icon(menu, before, false);
      margin-left: 10px;
    }
  }

  // MAIN NAV WRAPPER
  &-nav-container {
    display: none;

    @include from(10) {
      display: flex;
      justify-content: flex-end;
      flex: 1 1 auto;
      transition: $ts $te;
    }

    @include from(12) {
      // position: relative;
      align-items: center;
      justify-content: space-between;
    }
  }

  // PURPLE MENU BUTTON
  &-dropdown-toggle {
    display: none;

    @include from(7) {
      display: inline-flex;
      align-items: center;
      flex: none;
      width: 120px;
      height: 48px;
      margin: 0 5px;
      @include rem(font-size, 11px);
      @include rem(letter-spacing, 1.1px);
    }

    @include from(14) {
      @include rem(font-size, 12px);
    }

    .menu-icon {
      position: relative;
      width: 18px;
      height: 15px;
      margin-left: 16px;
      transform: rotate(0deg);
      transition: .2s $te;

      &::before,
      &::after,
      span {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        height: 2px;
        width: 100%;
        background-color: primary-color(white);
        opacity: 1;
        transform: rotate(0deg);
        transition: .2s $te;
      }

      &::before {
        top: 0;
      }

      &::after {
        top: 12px;
      }

      span {
        position: absolute;
        left: 0;
        display: block;
        height: 2px;
        width: 100%;
        background-color: primary-color(white);
        opacity: 1;
        transform: rotate(0deg);
        transition: .2s $te;
        top: 6px;
      }
    }

    @include hover {

      .menu-icon {

        &::before,
        &::after,
        span {
          background-color: primary-color(black);
        }
      }
    }

    &[aria-expanded='true'] {

      .menu-icon {

        &::before,
        &::after {
          top: 6px;
          left: 50%;
          width: 0%;
        }

        span {

          &:nth-child(1) {
            transform: rotate(45deg);
          }

          &:nth-child(2) {
            transform: rotate(-45deg);
          }
        }
      }
    }
  }

  // MAIN NAV
  &-nav {
    display: none;

    @include from(10) {
      display: block;
      transition: $ts $te;
    }

    @include between(10, 11) {
      position: absolute;
      bottom: 0;
      left: -30px;
      width: calc(100% + 60px);
      height: 45px;
    }

    @include from(12) {
      flex: 1 1 auto;
      margin: 0 auto;
      padding: 0 10px;
      opacity: 1;
    }

    &-items {
      // position: relative;
      transition: border-color $ts $te;

      @include between(10, 11) {
        padding: 0 $desktop-padding;
        border-top: 1px solid primary-color(gray);
      }

      [data-dropdown-menu='open'] & {

        @include between(10, 11) {
          border-color: transparent;
        }
      }

      > ul {
        display: flex;
        justify-content: space-around;
      }
    }

    &-item {
      // position: relative;
      flex: none;

      &:hover {
        .menu-dropdown-item {
          display: block;
          visibility: visible;
          opacity: 1;
          // transition: $ts $te;
        }
      }


      &-link {
        display: block;
        padding: 13px 20px;
        font-family: $inter-sb;
        @include rem(font-size, 15px);
        color: primary-color(black);

        @include from(12) {
          @include rem(font-size, 14px);
          padding: 17px 5px;
        }

        span {
          padding-bottom: 2px;
          border-bottom: 1px solid transparent;
          transition: $ts $te;
        }

        &[data-active='true'] {
          color: primary-color(purple, tint1);
        }

        @include hover {
          color: primary-color(purple, tint1);

          span {
            border-color: primary-color(purple, tint1);
          }
        }
      }
    }
  }

  // INTERIOR PAGE PRIORITY NAV
  &-subnav {
    display: flex;
    align-items: center;

    @include from(12) {
      display: block;
      padding-top: 15px;
    }

    .eyebrow {
      flex: none;
      margin-right: 15px;
      margin-bottom: 0;
      @include rem(font-size, 11px);
      @include line-height(28, 11);
      @include rem(letter-spacing, 1px);
      text-transform: uppercase;
      color: primary-color(black);

      @include from(12) {
        position: absolute;
        z-index: 10;
        top: -6px;
        left: 12px;
        padding: 10px 0 0;
        line-height: 1;
      }

      a {
        color: primary-color(black);

        @include hover {
          color: primary-color(purple);
        }
      }
    }

    // needed so priority nav doesn't collapse
    .site-header-subnav-priority-nav-container {
      flex-grow: 1;

      @include from(12) {
        margin-right: 40px;
      }

      @include from(14) {
        width: 85%;
      }
    }

    .p-plus {

      @include from(10) {
        display: inline-flex;
      }
    }

    // subnav priority nav
    .site-header-subnav-items {
      display: flex;
      justify-content: space-around;
      opacity: 0;
      transition: opacity $ts $te;

      &.p-plus__primary {
        opacity: 1;
      }

      @include from(12) {
        justify-content: flex-start;
      }

      .site-header-nav-item {

        > .site-header-nav-item-link {
          padding: 9px 20px;
          font-family: $inter;
          @include rem(font-size, 15px);
          @include line-height(24, 15);

          @include from(12) {
            padding: 5px 12px;
          }

          &[data-active='true'] {
            color: primary-color(black);

            span {
              border-color: primary-color(red);
            }

            @include hover {

              span {
                border-color: primary-color(purple, tint1);
              }
            }
          }
        }
      }
    }

    .p-plus__toggle-btn {
      padding: 9px 20px;
      font-family: $inter-b;
      @include rem(font-size, 11px);
      @include line-height(24, 11);
      @include rem(letter-spacing, 1px);
      text-transform: uppercase;
      color: primary-color(black);
      transition: $ts $te;
      @include icon(caret-down, after) {
        margin-left: 8px;
        @include rem(font-size, 10px);
        transition: $ts $te;
      }

      @include from(12) {
        padding: 5px 12px;
      }

      @include hover {
        color: primary-color(purple, tint1);

        &::after {
          color: primary-color(purple, tint1);
        }
      }

      &[aria-expanded='true'] {
        @include icon(caret-up, after);
      }
    }

    .p-plus__overflow {
      top: calc(100% + 2px);
      min-width: 210px;
      max-width: 350px;
      max-height: 400px;
      overflow-y: auto;
      padding: 20px;
      background: primary-color(white);
      border: 1px solid primary-color(lightgray);
      box-shadow: 0 4px 4px rgba(0, 0, 0, .08);

      @include from(12) {
        top: calc(100% + 10px);
      }

      .site-header-nav-item-link {
        padding: 10px 0;
        font-family: $inter;
        @include rem(font-size, 13px);
        @include line-height(18, 13);

        span {
          padding: 0;
        }
      }
    }
  }

  // GRAY SEARCH TOGGLE (opens search and mega menu)
  .search-toggle {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    flex: none;
    width: 48px;
    height: 48px;
    margin: 0 5px;
    opacity: 1;
    border: 2px solid primary-color(lightgray);
    border-radius: 100%;
    background-color: primary-color(lightgray);
    transition: $ts $te;
    @include icon(search, after) {
      @include rem(font-size, 20px);
    }

    @include hover {
      background-color: primary-color(white);
      border-color: primary-color(gray);
    }
  }

  // SEARCH AREA
  &-search-container {
    display: none;
    padding: 40px 20px;
    position: relative;
    grid-column: full;
    height: 180px;
    transition: $ts $te;

    // @include from(12) {
    //   position: absolute;
    // }

    &[area-expanded='true'] {
      display: block;
      transition: $ts $te;
    }

    .container-max-width {
      max-width: $inner-max-width;
      position: relative;
      margin-left: auto;
      margin-right: auto;
    }

    &-content {
      max-width: 1000px;
      margin-left: auto;
      margin-right: auto;
      text-align: center;

      p {
        @include rem(font-size, 20px);

        span {
          font-family: $inter-sb;
        }
      }
    }

    .search-container-close {
      position: absolute;
      top: 0;
      right: 20px;

      &::after {
        display: inline-block;
        content: "\00d7";
        @include rem(font-size, 30px);
      }
    }
  }

  &-search {
    position: relative;
    height: 48px;

    .site-header-search-input {
      height: 48px;
      padding: 0 50px 0 22px;
      border-radius: 100px;
    }

    .site-header-search-submit {
      position: absolute;
      top: 1px;
      right: 1px;
      width: 50px;
      height: calc(100% - 2px);
      font-size: 0;
      background-color: transparent;
      border: 1px solid transparent;
      border-radius: 0 100px 100px 0;
      background-image: url('../images/search.svg');
      background-size: 21px 22px;
      background-repeat: no-repeat;
      background-position: center;

      &:focus {
        background-color: primary-color(lightgray);
      }

      &:hover {
        background-color: primary-color(purple);
        background-image: url('../images/search-white.svg');
      }
    }
  }

  // BECOME A MEMBER BUTTON AND DROPDOWN
  &-member-btn {
    display: none;

    @include from(10) {
      position: relative;
      display: inline-flex;
      flex: none;
      min-width: 180px;
      height: 48px;
      margin: 0 0 0 5px;
    }

    > .btn {
      width: 100%;
    }

    .btn {
      justify-content: center;
      @include rem(font-size, 11px);
      @include rem(letter-spacing, 1.1px);

      @include from(14) {
        @include rem(font-size, 12px);
      }
    }

    .become-member-dropdown {
      visibility: hidden;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      z-index: 300;
      top: 0;
      left: 0;
      width: 100%;
      background: primary-color(yellow);
      border-radius: 50px 50px 0 0;
      box-shadow: 0 4px 4px rgba(0, 0, 0, .08);
      transition: $ts $te;

      .label {
        width: 100%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        padding: 14px 20px 10px;
        font-family: $inter-b;
        @include rem(font-size, 11px);
        @include rem(letter-spacing, 1.1px);
        @include line-height(17, 14);
        text-align: left;
        text-transform: uppercase;
        border: 2px solid primary-color(yellow);
        border-radius: 20px 20px 0 0;

        @include from(14) {
          @include rem(font-size, 12px);
        }
      }

      ul {
        margin: 0 22px;

        li {
          margin: 10px 0 18px;
        }

        a {
          display: inline;
          @include rem(font-size, 14px);
          @include line-height(17, 14);
          color: primary-color(black);
          border-bottom: 1px solid transparent;

          @include hover {
            border-color: primary-color(black);
          }
        }
      }

      .join-btn {
        display: flex;
        margin: 15px 22px 25px;

        &.btn {

          &::before {
            background-color: #ffde8f;
          }
        }
      }

      .become-member-dropdown-close {
        width: 100%;
        margin: 0;
        padding: 10px;
        border-top: 1px solid primary-color(white);
        font-family: $inter-b;
        @include rem(font-size, 11px);
        @include line-height(13, 11);
        @include rem(letter-spacing, 1px);
        text-transform: uppercase;
        @include icon(caret) {
          margin-right: 8px;
          transform: rotate(-90deg);
        }

        @include hover {
          background-color: #ffde8f;
        }
      }

      &[data-expanded='true'] {
        visibility: visible;
        opacity: 1;
        pointer-events: auto;
        border-radius: 0;
      }
    }
  }
}

// MEGA MENU OPEN...
html {

  &[data-dropdown-menu='open'] {

    body {
      overflow: hidden;
      padding-right: 15px;
    }

    .aota-color-bars {
      border-right-style: solid;
      border-right-color: primary-color(green);
    }

    .site-header {

      @include between(10, 11) {
        box-shadow: none;
      }

      &::after {

        @include between(10, 11) {
          visibility: visible;
          opacity: 1;
          pointer-events: auto;
        }
      }
    }

    .logo {

      @include between(7, 9) {
        width: 115px;
      }
    }

    .aota-logo-text {

      @include between(7, 9) {
        display: none;
      }
    }

    .site-header-nav {

      @include from(12) {
        visibility: hidden;
        opacity: 0;
      }
    }

    .search-toggle {
      visibility: hidden;
      opacity: 0;
      pointer-events: none;

      @include to(11) {
        width: 0;
        padding: 0;

        &::after {
          display: none;
        }
      }
    }

    .site-header-dropdown {
      overflow-x: hidden;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      height: 100% !important;

      // potential fix for safari
      // ::-webkit-scrollbar {
      //   -webkit-appearance: none;
      //   width: 7px;
      // }
      //
      // ::-webkit-scrollbar-thumb {
      //   border-radius: 4px;
      //   background-color: rgba(0, 0, 0, .5);
      //   box-shadow: 0 0 1px rgba(255, 255, 255, .5);
      // }
    }

    .grid-container,
    .site-footer {

      @include from(7) {
        opacity: 0;
      }
    }
  }
}
