.alert-banner {
  display: none;
  position: relative;
  z-index: 1;
  max-width: 1920px;
  margin: 0 auto;
  padding: 15px 20px;
  background-color: primary-color(yellow);

  @include from(10) {
    padding: 15px 30px;
  }

  &[data-active='true'] {
    display: block;
  }

  @include from(12) {
    background-image:
    url('../images/bg-alert-banner-left.svg'),
    url('../images/bg-alert-banner-right.svg');
    background-position:
    left -160px center,
    right -60px center;
    background-repeat: no-repeat;
    background-size: auto 100%;
  }

  @include from(16) {
    background-position:
    left center,
    right center;
  }

  .inner {
    @include content-constraint;

    @include from(12) {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-right: 95px;
    }
  }

  &-message {
    margin-bottom: 20px;
    padding-right: 50px;

    @include from(7) {
      padding-right: 70px;
    }

    @include from(12) {
      margin: 0;
      padding-right: 60px;
    }
  }

  .wysiwyg-content {

    p {
      @include rem(font-size, 16px);
    }
  }

  .btn {

    @include from(12) {
      flex: none;
    }
  }

  &-close {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 38px;
    background-color: primary-color(white);
    border-bottom-left-radius: 13px;
    transition: $ts $te;
    @include icon(close) {
      @include rem(font-size, 18px);
    }

    @include hover {
      background-color: primary-color(purple);
      color: primary-color(white);
    }
  }
}
