// IcoMoon font declaration
@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon.woff2?be36rq') format('woff2');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

// Icomoon variables
$icons: (
  arrow-long-prev: '\e91a',
  arrow-long-next: '\e91b',
  arrow-back: '\e900',
  arrow: '\e901',
  caret-back: '\e902',
  caret: '\e903',
  caret-down: '\e904',
  caret-up: '\e905',
  cart: '\e906',
  check: '\e91e',
  close: '\e907',
  close-circle: '\e916',
  controller-play: '\e91d',
  external-link: '\e908',
  home: '\e915',
  email: '\e919',
  menu: '\e909',
  minus: '\e90a',
  open-circle: '\e917',
  pause: '\e91c',
  play: '\e90b',
  plus: '\e90c',
  print: '\e918',
  quote: '\e90d',
  search: '\e90e',
  user: '\e90f',
  facebook: '\e910',
  linkedin: '\e911',
  twitter: '\e912',
  youtube: '\e913',
  instagram: '\e914'
);

[class^='icon-'],
[class*=' icon-'] {

  &::before {
    @include icomoon;
  }
}

@each $icon, $content in $icons {
  .icon-#{$icon} {

    &::before {
      content: '#{$content}';
    }
  }
}

