// Article Promo
//
// Markup: article-promo.twig
//
// Styleguide Components.Article Promo
.article-promo {
  max-width: 500px;
  margin: 0 auto 40px;

  &[data-color='purple'] {

    header {
      background-color: primary-color(purple);
    }
  }

  &[data-color='yellow'] {

    header {
      background-color: primary-color(yellow);

      .h3 {
        color: primary-color(black);
      }
    }
  }

  header {
    padding: 20px is-clamp(30px, 6vw, 50px);
    background-color: primary-color(purple);

    .h3 {
      margin: 0;
      color: primary-color(white);
      @include line-height(38, 36);
    }
  }

  &-text {
    padding: 45px is-clamp(30px, 6vw, 50px);
    background-image: linear-gradient(180deg, primary-color(lightgray) 50%, rgba(primary-color(lightgray), 0) 100%);

    .wysiwyg-content {

      p,
      li {
        @include rem(font-size, 16px);
        @include line-height(24, 16);
      }
    }
  }

  .button-row {
    margin-top: 30px;
  }

  .btn-text {
    margin-top: 20px;
  }
}
