// Feature Text-List
//
// Markup: feature-text-list.twig
//
// Styleguide Components.Feature Text-List
.feature-text-list {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  padding: is-clamp(40px, 5vw, 80px) 0;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='true'] {
    padding: is-clamp(40px, 5vw, 115px) 0 160px;
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);

    @include from(9) {
      padding: is-clamp(100px, 11.11vw, 130px) 0 115px;
      background-image: url('../images/bg-fifty-fifty-square-left.svg');
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: top right;
    }
  }

  &[data-gradient='right'] {

    @include from(9) {
      background-image: url('../images/bg-fifty-fifty-square-right.svg');
    }
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);

    @include from(9) {
      background-image: url('../images/bg-fifty-fifty-square-left.svg');
    }
  }

  &::before,
  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 150px;
    height: 240px;
    transition: $ts $te;

    @include between(10, 12) {
      height: 180px;
      width: 90px;
    }
  }

  &::before {
    background-image: linear-gradient(0deg, rgba(primary-color(yellow), 1) 0%, rgba(primary-color(yellow), 0) 100%);
    clip-path: polygon(0 0, calc(0% + 30px) 0%, 100% 100%, 0% 100%);

    @include between(10, 12) {
      clip-path: polygon(0 0, calc(0% + 15px) 0%, 100% 100%, 0% 100%);
    }
  }

  &::after {
    width: 120px;
    background-color: primary-color(green);
    clip-path: polygon(0 0, 0% 100%, 100% 100%);

    @include between(10, 12) {
      width: 75px;
    }
  }

  .inner {
    position: relative;
    z-index: 1;
    @include grid-placement;
    @include wide-padding;

    @include from(9) {
      display: flex;
      justify-content: space-between;
    }
  }

  .feature {
    position: relative;
    margin: 0 0 is-clamp(40px, 8vw, 70px);

    @include from(9) {
      width: 52%;
      padding: 0 is-clamp(50px, 5.55vw, 100px) 0 0;
      margin: 0;
    }

    header {
      margin-bottom: 30px;

      .h2 {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          top: is-clamp(15px, 3vw, 20px);
          left: -70px;
          display: none;
          width: 52px;
          height: 3px;
          background-color: primary-color(red);

          @include from(14) {
            display: block;
            left: -65px;
          }

          @include from(16) {
            left: -77px;
          }
        }

        a {
          display: inline;
          border-bottom: 1px solid transparent;
          transition: $ts $te;

          @include hover {
            border-color: primary-color(purple);
          }
        }
      }

      .meta {
        margin-bottom: 20px;
        @include rem(font-size, 14px);
      }

      .meta-author {
        margin-right: 30px;
      }
    }

    .btn {
      margin-top: 30px;
    }

    .link-list {
      margin: 30px 0 0;

      li {

        & + li {
          margin-top: is-clamp(15px, 1.66vw, 25px);
        }
      }
    }
  }

  &-articles {
    padding-bottom: 150px;

    @include from(9) {
      width: 48%;
      margin: 0;
      padding-bottom: 0;
      text-align: left;
    }

    .item {
      position: relative;
      display: flex;
      padding-left: is-clamp(15px, 3vw, 38px);
      margin-bottom: 40px;

      @include to(8) {
        max-width: 650px;
      }

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 3px;
        height: calc(100% - 40px);
        background-color: primary-color(green);
      }

      &:not(:last-child) {
        padding-bottom: 40px;
        border-bottom: 1px solid rgba($hr-color, .4);
      }

      .item-image {
        overflow: hidden;
        width: 112px;
        flex: none;

        @include to(4) {
          display: none;
        }

        @include between(9, 10) {
          display: none;
        }

        img {
          transition: .3s $te;
        }
      }

      .item-text {
        flex: 1 1 auto;
        padding-right: 30px;

        @include between(7, 10) {
          padding: 0;
        }
      }

      .eyebrow {
        margin-bottom: 5px;
      }

      .h5 {
        margin-bottom: 10px;

        a {
          display: inline;
          border-bottom: 1px solid transparent;
          transition: $ts $te;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }

      .meta {
        margin-bottom: 20px;
        @include rem(font-size, 14px);
      }

      .meta-author {
        margin-right: 30px;
      }

      p {
        @include rem(font-size, 16px);
        @include line-height(24, 16);
      }

      &.js-link-event {

        @include hover {

          .item-image {

            img {
              transform: scale(1.05);
            }
          }

          .h5 {

            a {
              border-bottom: 1px solid primary-color(purple);
            }
          }
        }
      }
    }
  }

  &[data-feature-position='right'] {

    &[data-gradient='true'] {
      background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);

      @include from(9) {
        background-image: url('../images/bg-fifty-fifty-square-right.svg');
      }
    }

    .inner {

      @include from(9) {
        flex-direction: row-reverse;
      }
    }

    .feature {

      @include from(9) {
        padding-right: 0;
        padding-left: is-clamp(100px, 11.11vw, 180px);
      }

      header {

        .h2 {

          &::before {

            @include from(9) {
              display: block;
            }
          }
        }
      }
    }
  }
}
