// Vertical Tabs
//
// Markup: vertical-tabs.twig
//
// Styleguide Components.Vertical Tabs
.vertical-tabs {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  padding: is-clamp(40px, 5vw, 100px) 0;

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='right'] {
    background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
  }

  &[data-gradient='left'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    @include grid-placement;
    @include wide-padding;
  }

  header {
    max-width: 700px;
    margin: 0 0 is-clamp(40px, 8vw, 70px);

    @include from(8) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }

    p {
      max-width: 500px;

      @include from(8) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .button-row {
      margin-top: 30px;
    }
  }

  $sidetabs_breakpoint: 9;

  // overrides of base accordion-tabs settings
  .accordion-tabs {

    @include from($sidetabs_breakpoint) {
      display: flex;
    }

    .tabs {
      display: none;

      @include from($sidetabs_breakpoint) {
        display: block;
        width: calc(50% - 25px);
        margin-right: 25px;
      }

      @include from(12) {
        width: calc(50% - 50px);
        margin-right: 50px;
      }

      @include from(14) {
        flex: none;
        width: 650px;
        margin-right: 0;
      }
    }

    .accordion-tabs-item {

      .tab-trigger-mobile {
        display: flex;

        @include from($sidetabs_breakpoint) {
          display: none;
        }
      }
    }

    .content {

      @include from($sidetabs_breakpoint) {
        width: calc(50% - 25px);
        margin-left: 25px;
      }

      @include from(12) {
        width: calc(50% - 50px);
        margin-left: 50px;
      }

      @include from(14) {
        width: auto;
        flex: 1 1 auto;
        margin-left: 100px;
      }
    }

    .vertical-tabs-content {
      padding: 35px 0;

      @include from($sidetabs_breakpoint) {
        padding: 0;
      }

      .image {
        position: relative;
        margin-bottom: 35px;

        &::before {
          content: '';
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 20%;
          height: 85%;
          background-image: linear-gradient(180deg, rgba(primary-color(green), 1) 0%, rgba(primary-color(green), 0) 100%);
          clip-path: polygon(0% 0%, 100% 0%, 0% 100%);
          opacity: .9;
        }

        .video-link {
          position: absolute;
          left: 0;
          bottom: 0;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          width: 100%;
          height: 100%;
          cursor: pointer;

          > span {
            display: flex;
            align-items: center;
            padding: is-clamp(15px, 3vw, 30px);
            font-family: $inter-b;
            @include font-clamp(rem(18px), 3.6vw, rem(22px));
            @include line-height(38, 22);
            color: primary-color(white);
            transform-style: preserve-3d;
            pointer-events: none;

            @include icon(play) {
              margin-right: is-clamp(10px, 2vw, 20px);
              color: primary-color(white);
              @include font-clamp(rem(30px), 6vw, rem(48px));
              transition: $ts $te;
            }

            &::after {
              content: '';
              position: absolute;
              z-index: -1;
              bottom: -1px;
              left: 0;
              display: block;
              width: 100%;
              height: 100%;
              background-image: linear-gradient(180deg, rgba(primary-color(black), 0) 0%, primary-color(black) 75%);
            }
          }

          @include hover {

            > span {

              &::before {
                color: primary-color(yellow);
              }
            }
          }
        }
      }

      .wysiwyg-content {
        margin-bottom: 50px;
      }

      .link-list {
        margin: 0;

        li {

          & + li {
            margin-top: is-clamp(15px, 1.66vw, 25px);
          }
        }
      }
    }
  }
}
