.js-link-event {
  cursor: pointer;
}

// Links
//
// Wrapper:
// max-width: 700px;
//
// :hover    - Hover state
// :focus    - Focus state
// :active   - Active state
// :disabled - Disabled state
//
// Markup:
// <div class="wysiwyg-content">
//   <p>This is a <a class="{{modifier_class}}" href="#">standard link</a> inside of WYSIWYG text. Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
// </div>
//
// Weight: -99
//
// Styleguide Components.Links
a {
  display: inline-block;
  color: primary-color(purple);
  text-decoration: none;
  transition: $ts $te;

  @include hover {
    color: primary-color(purple);
  }

  /////////////////////////////////////////////////////////////
  // NOTE: Adding .wysiwyg-content below is to handle
  //       links in a WYSIWYG.
  //
  //       For Drupal projects, also add .mce-content-body to
  //       add styles in the backend WYSIWYG.
  /////////////////////////////////////////////////////////////
  .wysiwyg-content & {

    &:not(.btn):not(.btn-text) {
      display: inline;
      border-bottom: 1px solid primary-color(purple);

      @include hover {
        background-color: rgba(primary-color(purple), .11);
        color: primary-color(purple, tint1);
      }

      &:visited {
        color: #585757;
        border-color: #585757;

        @include hover {
          background-color: rgba(primary-color(purple), .11);
          color: primary-color(purple, tint1);
        }
      }
    }
  }

  &:disabled,
  &[disabled] {
    opacity: .5;
    pointer-events: none;
  }

  &.-caret {
    display: inline;
    @include rem(font-size, 12px);
    @include font-weight(bold);
    @include line-height(15, 12);
    text-transform: uppercase;
    color: primary-color(purple);
    border: 0;
    @include icon(caret) {
      position: relative;
      left: 0;
      margin-right: 8px;
      @include rem(font-size, 11px);
      transition: .1s $te;
    }

    span {
      border-bottom: 1px solid transparent;
      transition: $ts $te;
    }

    @include hover {

      span {
        border-bottom: 1px solid primary-color(purple);
      }

      &::before {
        left: 3px;
      }
    }
  }
}
