.site-header-dropdown {
  @extend %grid-columns;
  @include grid-placement(full-bleed);
  background-color: primary-color(white);
  display: none;
  position: absolute;
  z-index: 100;
  top: calc(100% + 4px);
  left: 0;
  width: 100%;
  min-height: calc(100vh - 156px);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .07);
  transition: $ts $te;

  @include to(6) {
    visibility: hidden !important;
    opacity: 0 !important;
    pointer-events: none !important;
  }

  @include between(10, 11) {
    left: -($desktop-padding);
    width: calc(100% + 60px);
    box-shadow: 0 4px 4px rgba(0, 0, 0, .07);
  }

  &[data-expanded='true'] {

    @include from(7) {
      display: grid;
      visibility: visible;
      opacity: 1;
      pointer-events: auto;
    }
  }

  &[data-active='true'] {

    .site-header-dropdown-inner {
      opacity: 1;
      transition: $ts $te;
    }
  }

  @include between(10, 11) {
    top: calc(100% - 45px);
  }

  &-inner {
    position: relative;
    @include grid-placement(right-bleed);
    background-color: #f8f8f8;
    opacity: 0;
    transition: $ts $te;

    @include between(10, 11) {
      top: 5px;
      height: calc(100% - 5px);
    }
  }

  &-nav {
    display: flex;
    max-width: $inner-max-width;
    height: 100%;

    .left {
      position: relative;
      left: -1px; // sometimes elements (like icons) peak out behind dropdown
      flex: none;
      width: 252px;
      padding: 30px 30px 30px 10px;
      background-color: primary-color(white);
      transition: $ts $te;
      box-shadow: 8px 0 4px -4px rgba(0, 0, 0, .05);

      @include from(10) {
        width: 350px;
        padding-left: 0;
      }

      // so bottom dropshadow does not get overlapped by offset of this element
      @include between(10, 11) {
        background-color: transparent;
        background-image: linear-gradient(180deg, rgba(primary-color(white), 1) 0%, rgba(primary-color(white), 1) calc(100% - 5px), rgba(primary-color(white), 0) calc(100% - 5px), rgba(primary-color(white), 0) 100%);
      }

      @include from(12) {
        width: 525px;
        padding: 30px 55px 55px 30px;
      }

      @include from(14) {
        width: 585px;
      }

      @include from(16) {
        width: 610px;
      }

      .nav-section-title-container {
        margin-bottom: 25px;

        @include from(12) {
          display: flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid primary-color(gray);
        }

        .nav-section-title {
          margin-bottom: 15px;

          @include from(12) {
            margin: 0;
            border: 0;
          }
        }

        a {

          &.-caret {

            @include from(12) {
              margin: 0 0 0 25px;
            }
          }
        }
      }

      .nav-sections {
        margin-bottom: 40px;

        @include from(12) {
          margin-bottom: 50px;
        }

        @include from(14) {
          column-count: 2;
          column-gap: 70px;
        }

        > li {
          margin-bottom: 30px;

          @include from(14) {
            break-inside: avoid;
          }
        }
      }

      .nav-section {

        @include from(12) {
          margin-bottom: 45px;
        }

        @include from(14) {
          margin-bottom: 30px;
        }

        li {

          & + li {
            margin-top: 10px;
          }

          &.landing-link {

            &:not(:first-child) {
              margin-top: 12px;
            }
          }

          a {
            @include line-height(17, 14);
          }
        }
      }

      .promo {

        .top {
          position: relative;
          padding: 30px;
          @include rem(font-size, 16px);
          @include line-height(24, 16);
          color: primary-color(white);
          background-color: primary-color(purple);
          transition: $ts $te;

          @include from(10) {
            @include rem(font-size, 20px);
            @include line-height(28, 20);
          }

          @include from(12) {
            display: flex;
            padding-bottom: 0;
            @include rem(font-size, 21px);
          }

          @include from(14) {
            @include rem(font-size, 24px);
            @include line-height(32, 24);
          }

          a {
            display: inline;
            color: primary-color(white);
            border-bottom: 1px solid transparent;

            @include from(12) {
              display: block;
              padding-bottom: 30px;
            }
          }

          .journal {
            display: none;

            @include from(12) {
              position: relative;
              top: 0;
              z-index: 2;
              display: block;
              flex: none;
              width: 146px;
              margin-bottom: -55px;
              margin-right: 35px;
              transition: $ts $te;
            }
          }
        }

        .bottom {
          position: relative;
          z-index: 1;
          padding: 30px;
          background: linear-gradient(180deg, rgba(237, 237, 237, 0) 0%, primary-color(lightgray) 100%);

          @include from(12) {
            padding-left: 211px;
            background: linear-gradient(180deg, #f6f6f6 0%, primary-color(lightgray) 100%);
          }

          p {
            @include rem(font-size, 14px);
            @include line-height(20, 14);

            @include from(14) {
              @include rem(font-size, 16px);
              @include line-height(24, 16);
            }
          }
        }

        @include hover {

          .top {

            @include to(11) {

              a {
                border-bottom: 1px solid primary-color(white);
              }
            }

            .journal {

              @include from(12) {
                top: -8px;
              }
            }
          }
        }
      }
    }

    .right {
      flex: 1 1 auto;
      padding: 30px 15px 10px;

      .nav-sections {
        display: flex;
        flex-wrap: wrap;

        > li {
          flex: none;
          width: calc(50% - 50px);
          margin: 0 25px 35px;

          @include from(14) {
            width: calc(33.33% - 50px);
          }
        }
      }
    }

    .nav-section {

      li {
        @include rem(font-size, 14px);

        & + li {
          margin-top: 13px;
        }

        a {

          &:not(.-caret) {
            display: inline;
            color: primary-color(black);
            @include line-height(20, 14);
            border-bottom: 1px solid transparent;
            transition: $ts $te;

            @include hover {
              border-bottom: 1px solid primary-color(black);
            }

            &[data-active='true'] {
              border-bottom: 1px solid primary-color(black);
            }
          }
        }
      }

      .nav-section-subnav {
        list-style: disc;
        margin: 15px 0 0 25px;

        > li {

          & + li {
            margin-top: 10px;
          }
        }
      }
    }

    .nav-section-title {
      display: flex;
      margin-bottom: 20px;
      padding: 12px 0;
      @include rem(font-size, 14px);
      @include font-weight(bold);
      text-transform: uppercase;
      @include rem(letter-spacing, 2px);
      color: primary-color(black);
      border-bottom: 1px solid primary-color(gray);

      &::before {
        content: '';
        flex: none;
        display: block;
        width: 25px;
        height: 3px;
        margin: 6px 15px 0 0;
        background-color: primary-color(red);
      }

      @include hover {
        color: primary-color(red, shade1);
      }
    }

    .nav-section-subtitle {
      margin-bottom: 12px;
      @include rem(font-size, 12px);
      @include font-weight(bold);
      text-transform: uppercase;
      color: primary-color(black);
    }
  }

  .mobile-hidden {
    display: none;

    @include from(12) {
      display: block;
    }
  }
}
