// Featured Resource
//
// Markup: featured-resource.twig
//
// Styleguide Components.Featured Resource
.featured-resource {
  @extend %grid-columns;
  position: relative;
  @include grid-placement(full-bleed);
  @include component-padding;
  overflow: hidden;

  @include from(10) {
    padding-top: is-clamp(80px, 7.8vw, 100px);
    padding-bottom: 80px;
  }

  &[data-margin='true'] {
    @include component-margin;
  }

  &[data-gradient='true'] {
    background-image: linear-gradient(110deg, #f2f2f2 5.56%, primary-color(white) 40%);
  }

  .inner {
    position: relative;
    @include grid-placement;
    @include wide-padding;
  }

  .title {
    display: flex;
    align-items: center;

    &.h3 {
      margin-bottom: 20px;
    }

    &::before {
      content: '';
      display: block;
      width: is-clamp(30px, 6vw, 50px);
      height: 3px;
      margin-right: is-clamp(15px, 3vw, 20px);
      background-color: primary-color(red);
    }
  }

  .articles-container {

    @include from(7) {
      display: flex;
      align-items: flex-start;
    }

    @include from(12) {
      width: 60%;
    }

    @include from(16) {
      width: 65%;
    }
  }

  // main article
  .feature {
    margin-bottom: 50px;

    @include from(7) {
      width: 50%;
      margin: 0;
      padding-right: 20px;
    }

    @include from(9) {
      width: 450px;
      flex: none;
      padding-right: 30px;
    }

    @include between(12, 14) {
      width: 325px;
    }

    .feature-image {
      position: relative;
      margin-bottom: 30px;
      overflow: hidden;

      img {
        transform: scale(1);
        transition: .3s $te;
      }
    }

    .h3 {
      @include line-height(30, 26);

      a {
        display: inline;
        border: 1px solid transparent;
        transition: $ts $te;
      }
    }

    p {
      margin-top: 20px;
    }

    @include hover {

      .h3 {

        a {
          border-bottom: 1px solid primary-color(purple);
        }
      }

      .feature-image {

        img {
          transform: scale(1.03);
        }
      }
    }
  }

  // right column
  .secondary {

    @include from(7) {
      width: 50%;
      padding-left: 20px;
    }

    @include from(9) {
      width: auto;
      flex: 1 1 auto;
      padding-left: 30px;
    }
  }

  // secondary teasers
  .secondary-article {
    margin-bottom: 55px;
    // max-width: 700px;

    @include from(7) {
      margin-bottom: 40px;
      padding-bottom: 40px;
      border-bottom: 1px solid rgba(#979797, .4);
    }

    &:last-child {
      border-bottom: 0;
      padding-bottom: 0;
    }

    .meta {

      span {

        @include between(7, 9) {
          display: block;
        }
      }
    }

    .meta-author {

      @include between(7, 9) {
        margin: 0 0 5px;
      }
    }

    p {
      margin-top: 20px;
      @include rem(font-size, 16px);
      @include line-height(24, 16);
    }

    @include hover {

      .h5 {

        a {
          border-bottom: 1px solid primary-color(purple);
        }
      }
    }
  }

  .eyebrow {
    margin-bottom: 5px;
  }

  .h3,
  .h5 {
    margin-bottom: 0;

    a {
      display: inline;
      border: 1px solid transparent;
      transition: $ts $te;
    }
  }

  .meta {
    margin-top: 10px;
    margin-bottom: 0;
    @include rem(font-size, 14px);
  }

  .meta-author {
    margin-right: 30px;
  }

  .photo {
    display: none;

    @include from(12) {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 40%;
      height: 100%;
    }

    @include from(16) {
      width: 35%;
    }

    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      top: 0;
      right: 0;
      width: 20.5%;
      height: 25%;
      background-image: linear-gradient(180deg, rgba(primary-color(red), 1) 35%, rgba(primary-color(red), 0) 100%);
      clip-path: polygon(0% 0%, 100% 0%, 100% 100%);
      opacity: 0;
      transition: 1s $te;
    }

    picture {
      @include absolute(0, 0, null, 0);
      height: 100%;

      &::before {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        top: 1px;
        left: -1px;
        width: 100%;
        height: calc(100% + 2px);
        background-color: primary-color(white);
        clip-path: polygon(0% 0%, 75% 100%, 0% 100%);
      }

      &::after {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        top: 1px;
        left: 0;
        width: 100%;
        height: calc(100% + 2px);
        background-image: linear-gradient(330deg, rgba(primary-color(yellow), 1) 35%, rgba(primary-color(yellow), 0) 60%);
        clip-path: polygon(-90px 0, 0% 0, 75% 100%, calc(75% - 90px) 100%);
        opacity: 0;
        transition: 1s $te;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    &.-animated {

      &::after {
        opacity: 1;
      }

      picture {

        &::after {
          opacity: 1;
        }
      }
    }
  }

  &[data-image-position='left'] {

    @include from(12) {
      padding-left: 40%;
    }

    @include from(16) {
      padding-left: 35%;
    }

    &[data-gradient='true'] {
      background-image: linear-gradient(250deg, #f2f2f2 5.56%, primary-color(white) 50%);
    }

    .inner {

      @include from(14) {
        padding-left: 0;
      }
    }

    .articles-container {

      @include from(12) {
        width: auto;
      }
    }

    .photo {

      @include from(12) {
        right: auto;
        left: 0;
      }

      &::after {
        right: auto;
        left: 0;
        clip-path: polygon(0% 0%, 100% 0%, 0% 100%);
      }

      picture {

        &::before {
          left: auto;
          right: -1px;
          clip-path: polygon(100% 0%, 100% 100%, 25% 100%);
        }

        &::after {
          left: auto;
          right: 0;
          background-image: linear-gradient(45deg, rgba(primary-color(yellow), 1) 35%, rgba(primary-color(yellow), 0) 60%);
          clip-path: polygon(100% 0%, calc(100% + 90px) 0, calc(25% + 90px) 100%, 25% 100%);
        }
      }
    }
  }
}
