.hero-home {
  @extend %grid-columns;
  position: relative;
  z-index: 1;
  @include grid-placement(full-bleed);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .08);

  &[data-margin='true'] {
    @include component-margin;
  }

  .kss-body & {
    margin: 0;
  }

  .inner {
    @include grid-placement(full-bleed);
    display: flex;
    flex-direction: column;

    @include from(10) {
      @include grid-placement(right-bleed);
      flex-direction: row-reverse;
      align-items: center;
    }
  }

  // text area
  .text {
    padding: 20px $mobile-padding 60px;

    @include from(5) {
      max-width: 600px;
      margin: 0 auto;
      padding-left: 50px;
      padding-right: 50px;
    }

    @include from(10) {
      width: 40%;
      max-width: 760px;
      padding: is-clamp(40px, 3.9vw, 80px) is-clamp($desktop-padding, 3vw, 75px) is-clamp(40px, 3.9vw, 80px) 0;
    }

    @include from(14) {
      padding-left: $wide-padding;
    }

    @include from(16) {
      width: 55%;
    }
  }

  .h1 {
    @include font-clamp(rem(36px), 7.2vw, rem(54px));

    @include from(10) {
      margin-bottom: 35px;
      @include font-clamp(rem(40px), 4vw, rem(75px));
      line-height: 1.1;
    }

    > span {
      position: relative;
      z-index: 1;
      padding-bottom: is-clamp(6px, 1.2vw, 8px);

      &::after {
        content: '';
        position: absolute;
        z-index: -1;
        left: -8px;
        bottom: 1px;
        width: calc(100% + 16px);
        height: 15px;
        background-image: url('../images/underline-red.svg');
        background-repeat: no-repeat;
        background-size: 100% 15px;
        background-position: center bottom;
      }
    }
  }

  p {
    max-width: 500px;
    color: #4d4d4f;
  }

  .button-row {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 35px;

    .btn {

      + .btn-text {
        margin: 35px 0 0;
      }
    }

    .btn-text {
      margin: 14px 0 0;
    }
  }

  .photo {
    position: relative;
    overflow: hidden;
    min-height: is-clamp(295px, 73.75vw, 550px);

    @include from(10) {
      flex: 1 1 auto;
      height: 100%;
      min-height: is-clamp(600px, 58.59vw, 795px);
    }

    img {
      transform: scale(1.1, 1.1);
      transition: 1s $te;
    }

    picture {
      @include absolute(0, 0, null, 0);
      height: 100%;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    // green gradient
    &::after {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      right: -100%;
      width: 50%;
      height: 135px;
      background: linear-gradient(285deg, primary-color(green) 0%, rgba(primary-color(green), 0) 65%);
      clip-path: polygon(0% 0%, 100% calc(100% - 60px), 100% 100%, 0% 100%);
      transition: 1s $te;
      transition-delay: .25s;

      @include from(10) {
        right: auto;
        left: -100%;
        width: 240px;
        height: 400px;
        background: linear-gradient(40deg, primary-color(green) 0%, rgba(primary-color(green), 0) 65%);
        // clip-path: polygon(0% 0%, calc(100% - 10px) 0, 120px 100%, 0% 100%);
        clip-path: polygon(0% 0%, 100% 0%, 50% 100%, 0% 100%);
      }

      @include from(15) {
        clip-path: polygon(0% 0%, 100% 0, 100px 100%, 0% 100%);
      }

      .stop-transition & {
        transition: none;
      }
    }
  }

  // white clipped shape with yellow gradient
  .photo-flair-left {
    position: absolute;
    z-index: 1;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 106px;
    background-color: primary-color(white);
    clip-path: polygon(0% calc(100% - 60px), 25% 0%, 100% 100%, 0% 100%);
    pointer-events: none;

    @include from(10) {
      width: 215px;
      height: 100%;
      top: 0;
      left: -1px; // fix for chrome
      bottom: auto;
      clip-path: polygon(0% 0%, 100px 0%, 75% 25%, 0% 100%);
    }

    @include from(15) {
      width: 276px;
    }

    // yellow gradient
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 100%;
      width: 100%;
      height: 100%;
      background: linear-gradient(130deg, primary-color(yellow) 22%, rgba(primary-color(yellow), 0) 66%);
      clip-path: polygon(0% 0%, 25% 0%, 100% 100%, 75% 100%);
      transition: 1s $te;
      transition-delay: 0s;

      @include from(10) {
        background: linear-gradient(225deg, primary-color(yellow) 22%, rgba(primary-color(yellow), 0) 60%);
        clip-path: polygon(75% 0, 75% 25%, 25% 75%, 0 75%);
      }

      .stop-transition & {
        transition: none;
      }
    }
  }

  // red and purple triangles
  .photo-flair-right {
    position: absolute;
    top: 0;
    right: 0;
    width: 58%;
    height: 82px;
    pointer-events: none;

    @include from(10) {
      top: auto;
      bottom: 0;
      width: 120px;
      height: 430px;
    }

    @include from(15) {
      width: 164px;
      height: 450px;
    }

    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
    }

    // red triangle
    &::before {
      z-index: 2;
      right: -100%;
      width: 66%;
      background-color: primary-color(red);
      clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 30px));
      transition: 1s $te;
      transition-delay: .75s;

      @include from(10) {
        top: auto;
        bottom: 0;
        width: 110px;
        height: 284px;
        clip-path: polygon(50px 100%, 100% 50px, 100% 100%);
      }

      @include from(15) {
        clip-path: polygon(28px 100%, 100% 50px, 100% 100%);
      }

      .stop-transition & {
        transition: none;
      }
    }

    // purple gradient
    &::after {
      z-index: 1;
      right: -100%;
      width: 100%;
      background-image: linear-gradient(90deg, primary-color(purple) 0%, rgba(primary-color(purple), 0) 66%);
      clip-path: polygon(0% 0%, 100% 0%, 100% 100%);
      transition: 1s $te;
      transition-delay: .5s;

      @include from(10) {
        top: auto;
        bottom: 0;
        background-image: linear-gradient(225deg, primary-color(purple) 10%, rgba(primary-color(purple), 0) 66%);
        clip-path: polygon(100% 0%, 100% 100%, 0% 100%);
      }

      .stop-transition & {
        transition: none;
      }
    }
  }

  &.-animated {

    .photo {

      img {
        transform: scale(1, 1);
      }

      // green
      &::after {
        right: 0;

        @include from(10) {
          left: 0;
        }
      }
    }

    .photo-flair-left {

      // yellow
      &::before {
        left: 0;
      }
    }

    .photo-flair-right {

      // red
      &::before {
        right: 0;
      }

      // purple
      &::after {
        right: 0;
      }
    }
  }
}
