.promo-300 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
  padding: 35px 0 65px;
  background: linear-gradient(0deg, rgba(primary-color(white), 1) 0%, rgba(primary-color(lightgray), 1) 100%);

  .label {
    margin-bottom: 30px;
    font-family: $inter-sb;
    @include rem(font-size, 12px);
    line-height: 1;
    color: primary-color(darkgray);
    text-transform: uppercase;
  }
}
